import { TERMS_SHOW, TERMS_AGREE, TERMS_DISAGREE, TERMS_TOGGLE } from "../actions/terms";

const defaultState = {
    steps: [],
    currentStep: null,
    previousRoute: null,
    onAgreement: false,
};

const termsReducer = (state, action) => {
    const terms = state.terms || defaultState;

    if (action.type.startsWith("INITIATE_")) {
        return Object.assign({}, { onAgreement: false });
    }

    switch (action.type) {
    case TERMS_SHOW:
        return {
            previousRoute: action.previousRoute,
            steps: action.steps,
            currentStep: action.currentStep,
            onAgreement: false,
        };

    case TERMS_AGREE:
        return Object.assign({}, { onAgreement: true });

    case TERMS_DISAGREE:
        return Object.assign({}, { onAgreement: false });

    case TERMS_TOGGLE:
        return Object.assign({}, { onAgreement: !terms.onAgreement });

    default:
        return terms;
    }
};

export default termsReducer;



