import { Logger, RestHost } from "common-functions";

const CCGRest = {};

CCGRest.active = false;
const retainCaseFields = ["registrationFields", "billPaymentFields"];

CCGRest.isActive = function () {
    return CCGRest.active === true;
};

CCGRest.start = function () {
    CCGRest.active = true;
};

CCGRest.end = function () {
    CCGRest.active = false;
};

CCGRest.init = function (callback, processOnBackground) {
    const getWebServiceUrlCallback = function (url) {
        CCGRest.host = new RestHost(url, null);

        if (callback) {
            callback();
        }
    };
    CCGRest.getLocalSetting("kioskWebServiceUrl", getWebServiceUrlCallback, processOnBackground);
};

const sendPostRequest = function (restUrl, data, successCallback, errorCallback, isXml) {
    const requestTime = new Date();
    CCGRest.start();

    CCGRest.host.post(restUrl, CCGRest.objectToUpperCase(data),
        (response) => {
            CCGRest.end();
            const responseTime = new Date();
            Logger.log(`REST SUCCESS in ${responseTime.getTime() - requestTime.getTime()} ms - POST / (${restUrl})`);
            Logger.performance(restUrl, requestTime.getTime(), responseTime.getTime());
            if (typeof successCallback === "function") {
                successCallback(response);
            }
        },
        (error) => {
            CCGRest.end();
            const responseTime = new Date();
            Logger.log(`REST FAILED in ${responseTime.getTime() - requestTime.getTime()} ms - POST / (${restUrl})`);
            Logger.performance(restUrl, requestTime.getTime(), responseTime.getTime(), error);
            if (typeof errorCallback === "function") {
                errorCallback(error);
            }
        }, isXml,
    );
};

CCGRest.post = function (restUrl, data, successCallback, errorCallback, isXml) {
    if (CCGRest.host == null) {
        CCGRest.init(() => {
            sendPostRequest(restUrl, data, successCallback, errorCallback, isXml);
        });
    } else {
        sendPostRequest(restUrl, data, successCallback, errorCallback, isXml);
    }
};

const sendRequest = function (sendType, restUrl, data, urlParams, successCallback,
  errorCallback, responseType, processOnBackground) {
    const requestTime = new Date();
    const strParams = (urlParams != null) ? urlParams.toString() : "";

    CCGRest.start();
    CCGRest.host.send(sendType, restUrl + CCGRest.addCacheParam(strParams), CCGRest.objectToUpperCase(data),
        (response) => {
            CCGRest.end();

            const responseTime = new Date();
            Logger.log(`REST SUCCESS in ${responseTime.getTime() - requestTime.getTime()} ms - ${sendType} / (${restUrl + strParams})`);
            Logger.performance(restUrl, requestTime.getTime(), responseTime.getTime());

            if (typeof successCallback === "function") {
                successCallback(response);
            }
        },
        (error) => {
            CCGRest.end();

            const responseTime = new Date();
            Logger.log(`REST FAILED in ${responseTime.getTime() - requestTime.getTime()} ms - ${sendType} / (${restUrl + strParams})`);
            Logger.performance(restUrl, requestTime.getTime(), responseTime.getTime(), error);
            if (typeof errorCallback === "function") {
                errorCallback(error);
            }
        },
        responseType,
        processOnBackground);
};

CCGRest.send = (sendType, restUrl, data, urlParams, successCallback,
    errorCallback, responseType, processOnBackground) => {
    const sendRequestCallback = () => {
        sendRequest(sendType, restUrl, data, urlParams, successCallback,
      errorCallback, responseType, processOnBackground);
    };

    if (CCGRest.host == null) {
        CCGRest.init(sendRequestCallback, processOnBackground);
    } else {
        sendRequestCallback();
    }
};

CCGRest.login = (rrn, password, successCallback, errorCallback) => {
    const requestData = {
        getCustomerIdRequest: {
            customerCredentials: {
                registrationRetrievalNumber: rrn,
                password,
            },
        },
    };

    CCGRest.post("customers/getCustomerId", requestData, (response) => {
        let customerId = null;
        if (response && response.getCustomerIdResponse
        && response.getCustomerIdResponse.customerIdStr) {
            customerId = response.getCustomerIdResponse.customerIdStr.replace("L", "");
        }
        successCallback(customerId);
    }, errorCallback);
};


CCGRest.objectToUpperCase = function (obj) {
    const objectCopy = Object.assign({}, obj);
    Object.keys(objectCopy).forEach((key) => {
        if (!retainCaseFields.includes(key)) {
            const value = objectCopy[key];
            if (typeof value === "string") {
                objectCopy[key] = value.toUpperCase();
            } else if (typeof value === "object") {
                objectCopy[key] = CCGRest.objectToUpperCase(value);
            }
        }
    });
    return objectCopy;
};

CCGRest.getApplicationId = function () {
    return CCGRest.localSettings.applicationId;
};

CCGRest.getLocalSetting = function (key, callback, processOnBackground) {
    if (CCGRest.localSettings == null) {
        const restHost = new RestHost("resources/config/", null, null);

        restHost.get("settings.json", (response) => {
            if (callback) {
                callback(response[key]);
            }
            CCGRest.localSettings = response;
        },
    (error) => console.error(error),
    RestHost.JSON,
    processOnBackground);
    } else {
        callback(CCGRest.localSettings[key]);
    }
};

CCGRest.addCacheParam = (url) => {
    let cacheParam = window.ccg ? window.ccg.cacheParam : "";
    cacheParam = window.encodeURIComponent(cacheParam);
    if (url.indexOf("?") >= 0) {
        return `${url}&cp=${cacheParam}`;
    }
    return `${url}?cp=${cacheParam}`;
};

CCGRest.init();

module.exports = CCGRest;



