import React from "react";
import { connect } from "react-redux";
import { InputV2 as Input, SelectV2, Dropdown, PasswordInput, Div, P, Span, Button } from "common-components";
import { Helpers } from "common-functions";
import { VALIDATE, VALIDATION_FAILED, CLEAR_VALIDATION_ERRORS, RESET_INPUTS_FORMAT_VALIDATION,
    CLEAR_SPECIFIC_VALIDATION_ERROR, validate } from "../../actions/validation";

import { INITIATE_RESET_PASSWORD } from "../../actions";

import Steps from "../../components/Steps";
import Navigation from "../../components/Navigation";

import { onContinue as resetContinue, onPrevious as resetPrevious,
    FILL_UP_RESET_PASSWORD_REQUEST_DATA, FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA,
    FILL_UP_FORCE_CHANGE_PASSWORD } from "../../actions/resetPassword";

const monthsSelection = [
    { value: "0", name: "months.jan" },
    { value: "1", name: "months.feb" },
    { value: "2", name: "months.mar" },
    { value: "3", name: "months.apr" },
    { value: "4", name: "months.may" },
    { value: "5", name: "months.jun" },
    { value: "6", name: "months.jul" },
    { value: "7", name: "months.aug" },
    { value: "8", name: "months.sep" },
    { value: "9", name: "months.oct" },
    { value: "10", name: "months.nov" },
    { value: "11", name: "months.dec" },
];

const RegistrationRetrievalNumberPage = ({ requestData, onChange, steps, currentStep, currentIndex, onContinue }) => (
    <Div>
        <Steps items={steps} currentStep={currentStep} />
        <div className="overview-page">
            <div className="title">
                <P>reset.password.page.title1</P>
                <P>reset.password.page.title2</P>
            </div>
        </div>
        <Div className="register-customer has-badge-panel indented-body">
            <Span className="badge">{currentIndex + 1}</Span>
            <P className="title">reset.password.page.rrn.label</P>
            <Div className="customer-form">
                <Div>
                    <Input name="registrationRetrievalNumber"
                      value={requestData.registrationRetrievalNumber}
                      className="col-12"
                      onChange={onChange}
                      placeholder="reset.password.page.rrn.placeholder"
                      shouldAutoFocus />
                </Div>
            </Div>
        </Div>
        <Navigation onNext={onContinue} disablePrevious nextOnEnter />
    </Div>
);

const SecurityQuestionPage = ({ requestData, securityQuestions, hotline, steps, currentStep, currentIndex,
        onDayChange, onSecurityQuestionChange, onSecurityAnswerChange, onContinue, onPrevious, showPopUp, onMainMenu }) => {
    const { questionType, answer, answerDay, answerMonth, answerYear } = requestData.customerQA;
    const isQuestionTypeDOB = questionType === "DOB";

    let day = answerDay;
    let month = answerMonth;
    let year = answerYear;

    const invalidMonth = !month && month !== 0;
    if (!year && !day && invalidMonth && answer) {
        const answerDate = Helpers.parseDate(answer);
        if (answerDate) {
            day = answerDate.getDate();
            month = answerDate.getMonth();
            year = answerDate.getFullYear();
        }
    }

    const textInput = (<Div>
        <Input name="answer"
          value={answer}
          className="col-12"
          onChange={onSecurityAnswerChange}
          placeholder="register.customer.customer.qa.answer.placeholder"
          shouldAutoFocus />
    </Div>);

    const dateInput = (<Div>
        <Dropdown name="answerMonth"
          value={month}
          items={monthsSelection}
          onChange={onSecurityAnswerChange}
          placeholder="register.customer.month.placeholder"
          className="col-6"
          focusOnCompleteFillUp="answerDay"
          shouldAutoFocus />
        <Input name="answerDay"
          value={day}
          className="col-3"
          onChange={(event) => onDayChange(event, onSecurityAnswerChange)}
          focusOnCompleteFillUp="answerYear"
          placeholder="register.customer.day.placeholder" />
        <Input name="answerYear"
          value={year}
          className="col-3"
          onChange={onSecurityAnswerChange}
          placeholder="register.customer.year.placeholder" />
    </Div>);

    return (<Div>
        <Steps items={steps} currentStep={currentStep} />
        <Div className="register-customer has-badge-panel indented-body">
            <Span className="badge">{currentIndex + 1}</Span>
            <P className="title">register.customer.customer.qa.question.label</P>
            <Div className="customer-form">
                <Div>
                    <SelectV2 name="questionType"
                      value={questionType}
                      items={securityQuestions}
                      className="col-12"
                      placeholder="register.customer.customer.qa.question.placeholder"
                      onChange={onSecurityQuestionChange} />
                </Div>
            </Div>

            <P className="title secondary">register.customer.customer.qa.answer.label</P>
            <Div className="customer-form">
                {isQuestionTypeDOB ? dateInput : textInput}
            </Div>
        </Div>
        <Navigation onNext={onContinue} onPrevious={onPrevious} nextOnEnter />

        <Div className="mo-popup" style={{ display: showPopUp ? "" : "none" }}>
            <Div className="reset-password-message">
                <Div className="reset-password-pop-up-header" args={hotline}>reset.password.page.default.error.message</Div>
                <Div className="actions reset-password-actions">
                    <Button className="reset-password-pop-up" onClick={onMainMenu} data-analytics="Try again">reset.password.page.try.again</Button>
                </Div>
            </Div>
        </Div>
    </Div>);
};

const ResetSuccessPage = ({ hotline, onMainMenu }) => (
    <Div>
        <img className="page-background"
          src="resources/images/background-success.png"
          alt="Reset password success page background" />

        <Div className="reset-password-success-page">
            <Div>
                <P className="title">reset.password.page.success.message1</P>
                <P className="title" args={hotline}>reset.password.page.success.message2</P>
                <Div className="actions">
                    <Button className="small" to="/" onClick={onMainMenu} data-analytics="Continue">continue.btn</Button>
                </Div>
            </Div>
        </Div>
    </Div>
);

const PasswordPage = ({ forceChangePasswordRequestData, onPasswordChange, currentIndex, onContinue }) => {
    const passwordDoNotMatch = forceChangePasswordRequestData.password !== forceChangePasswordRequestData.passwordConfirmation;
    const passwordIsInvalid = passwordDoNotMatch ? passwordDoNotMatch : undefined;

    return (
        <Div>
            <Div className="register-customer has-badge-panel indented-body">
                <Span className="badge">{currentIndex + 1}</Span>
                <P className="title">force.to.change.password.label</P>
                <Div className="customer-form">
                    <Div>
                        <PasswordInput name="password"
                          value={forceChangePasswordRequestData.password}
                          className="col-12"
                          onChange={onPasswordChange}
                          placeholder="register.customer.password.placeholder"
                          shouldAutoFocus />
                    </Div>
                </Div>

                <P className="title secondary">register.customer.password.confirmation.label</P>
                <Div className="customer-form">
                    <Div>
                        <PasswordInput name="passwordConfirmation"
                          value={forceChangePasswordRequestData.passwordConfirmation}
                          className="col-12"
                          onChange={onPasswordChange}
                          placeholder="register.customer.password.confirmation.placeholder" />
                    </Div>
                </Div>
            </Div>
            <Navigation onNext={onContinue} disablePrevious nextOnEnter hasFormatErrors={passwordIsInvalid} />
        </Div>
    );
}

const mapStateToProps = (state) => {
    const { pages, steps, currentIndex, requestData, forceChangePasswordRequestData, showPopUp } = state.resetPassword;
    const { securityQuestions } = state.config;

    const currStepIndex = currentIndex;
    const page = pages[currentIndex];
    const currentStep = page.step;

    return {
        pages,
        steps,
        requestData,
        forceChangePasswordRequestData,
        currentStep,
        securityQuestions,
        showPopUp,
        currentIndex: currStepIndex,
        hotline: state.config.defaultValues.helpHotline,
    };
};

const processEvent = (event) => {
    const fieldName = event.target.name;
    const fieldValue = event.target.value;
    const data = {};
    data[fieldName] = fieldValue;
    return data;
};

const onDayChange = (event, onChange) => (dispatch) => {
    const { name, value } = event.target;
    onChange(event);

    if (value) {
        if (!Helpers.isDayValid(value)) {
            dispatch({ type: VALIDATION_FAILED, error: { fieldName: name, message: "invalid.day" } });
        } else {
            dispatch({ type: CLEAR_SPECIFIC_VALIDATION_ERROR, filedName: name });
        }
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
    const onContinue = () => dispatch(validate(ownProps.onContinue || resetContinue));
    const onPrevious = () => dispatch(ownProps.onPrevious || resetPrevious);

    return {
        onContinue,
        onPrevious,
        onDayChange: (event, onChange) => dispatch(onDayChange(event, onChange)),
        onChange: (event) => dispatch({ type: FILL_UP_RESET_PASSWORD_REQUEST_DATA, data: processEvent(event) }),
        onPasswordChange: (event) => dispatch({ type: FILL_UP_FORCE_CHANGE_PASSWORD, data: processEvent(event) }),
        onSecurityAnswerChange: (event) => dispatch({ type: FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA, data: processEvent(event) }),
        onSecurityQuestionChange: (event) => {
            const data = processEvent(event);
            data.answer = null;
            data.answerMonth = null;
            data.answerDay = null;
            data.answerYear = null;
            dispatch({ type: FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA, data });
            dispatch({ type: CLEAR_VALIDATION_ERRORS });
            dispatch({ type: RESET_INPUTS_FORMAT_VALIDATION });
        },
        onPopUpCancel: () => dispatch({ type: "SHOW_POP_UP", showPopUp: false }),
        onMainMenu: () => dispatch({ type: INITIATE_RESET_PASSWORD }),
    };
};

const SecurityQuestion = connect(mapStateToProps, mapDispatchToProps)(SecurityQuestionPage);
const RegistrationRetrievalNumber = connect(mapStateToProps, mapDispatchToProps)(RegistrationRetrievalNumberPage);
const ResetSuccess = connect(mapStateToProps, mapDispatchToProps)(ResetSuccessPage);
const ForceChangePassword = connect(mapStateToProps, mapDispatchToProps)(PasswordPage);

export { SecurityQuestion, RegistrationRetrievalNumber, ForceChangePassword, ResetSuccess };



