/* eslint jsx-a11y/heading-has-content: 0 */

import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Intl, MessageArgs } from "common-functions";

const process = (props) => {
    const propsCopy = Object.assign({}, props);

    delete propsCopy.translation;
    delete propsCopy.dispatch;
    delete propsCopy.args;
    delete propsCopy.defaultValue;
    delete propsCopy.messageArgs;
    delete propsCopy.language;
    delete propsCopy.parentClassName;
    delete propsCopy.defaultErrorMessage;
    delete propsCopy.dontBreakOnLastLine;

    if (typeof propsCopy.children === "string") {
        propsCopy.children = Intl.translate(props.translation, propsCopy.children, props.defaultValue, props.args);
        propsCopy.children = MessageArgs.replaceVars(propsCopy.children, props.messageArgs, props.language);

        const renderBreakLine = (index, lineLength) => props.dontBreakOnLastLine && index === lineLength - 1 ? null : <br />;
        const addBreakLine = (line, index, lines) => <span key={`${line}${index}`}>{line}{renderBreakLine(index, lines.length)}</span>;
        const lines = propsCopy.children.split(/\\n|[\n\r]/);
        propsCopy.children = lines.length > 1 ? lines.map(addBreakLine) : propsCopy.children;
    }
    return propsCopy;
};

const HTMLDiv = (props) => {
    const propsCopy = process(props);
    return (<div {...propsCopy} />);
};

const HTMLSpan = (props) => {
    const propsCopy = process(props);
    return (<span {...propsCopy} />);
};

const HTMLP = (props) => {
    const propsCopy = process(props);
    return (<p {...propsCopy} />);
};

const HTMLLabel = (props) => {
    const propsCopy = process(props);
    return (<label htmlFor {...propsCopy} />);
};

const HTMLButton = (props) => {
    const propsCopy = process(props);
    let component = <button {...propsCopy} />;

    if (typeof props.to !== "undefined" && props.to !== null) {
        component = <Link to={props.to}>{component}</Link>;
    }
    return component;
};

const HTMLH1 = (props) => {
    const propsCopy = process(props);
    return (<h1 {...propsCopy} />);
};

const HTMLH2 = (props) => {
    const propsCopy = process(props);
    return (<h2 {...propsCopy} />);
};

const HTMLH3 = (props) => {
    const propsCopy = process(props);
    return (<h3 {...propsCopy} />);
};

const HTMLH4 = (props) => {
    const propsCopy = process(props);
    return (<h4 {...propsCopy} />);
};

const HTMLH5 = (props) => {
    const propsCopy = process(props);
    return (<h5 {...propsCopy} />);
};

const HTMLH6 = (props) => {
    const propsCopy = process(props);
    return (<h6 {...propsCopy} />);
};

const mapStateToProps = (state) => {
    const language = state.internationalization.language;
    const translation = state.internationalization.translation || {};
    const messageArgs = state.config.messageArgs || {};
    return { translation, messageArgs, language };
};

const Div = connect(mapStateToProps)(HTMLDiv);
const Span = connect(mapStateToProps)(HTMLSpan);
const P = connect(mapStateToProps)(HTMLP);
const Label = connect(mapStateToProps)(HTMLLabel);
const Button = connect(mapStateToProps)(HTMLButton);

const H1 = connect(mapStateToProps)(HTMLH1);
const H2 = connect(mapStateToProps)(HTMLH2);
const H3 = connect(mapStateToProps)(HTMLH3);
const H4 = connect(mapStateToProps)(HTMLH4);
const H5 = connect(mapStateToProps)(HTMLH5);
const H6 = connect(mapStateToProps)(HTMLH6);

export { Div, Span, P, Label, Button, H1, H2, H3, H4, H5, H6 };



