import React from "react";
import { connect } from "react-redux";
import { INITIATE_CHECK_CASHING } from "../../actions/constants";
import { INITIATE_LOGIN } from "../../reducers/login";
import { INITIATE_HALF_REGISTER_CUSTOMER, INITIATE_RESET_PASSWORD } from "../../actions";
import { Div, P, Button } from "../../components/HTML";

const Intervention = ({ onClick, display, title, description, resetAll, url, redirect, button, additionalClass, lang }) => {

    return (<Div style={{ display }} className={`restricted-access page-background-3 ${additionalClass} ${lang}-intervention`}>

        <Div className="restricted-access-page">
            <P className="title" title={title.args}>{title.text || title}</P>
            <P className="subtitle" args={description.args}>{description.text || description}</P>

            <Div className="buttons">
                <Button className="close-btn" to={redirect ? url : null} onClick={onClick || resetAll}>{button}</Button>
            </Div>
        </Div>
    </Div>);
};

const mapStateToProps = (state) => {
    return {
        display: state.checkCashing.showIntervention ? "" : "none",
        title: state.checkCashing.interventionTitle || "",
        description: state.checkCashing.interventionDesc || "",
        onClick: state.checkCashing.interventionOnClick,
        url: state.checkCashing.interventionURL || "/",
        redirect: !state.checkCashing.noLink,
        button: state.checkCashing.button || "close.btn",
        additionalClass: state.checkCashing.additionalClass,
        lang: state.internationalization.language,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        resetAll: () => {
            dispatch({ type: INITIATE_LOGIN });
            dispatch({ type: INITIATE_HALF_REGISTER_CUSTOMER });
            dispatch({ type: INITIATE_RESET_PASSWORD });
            dispatch({ type: INITIATE_CHECK_CASHING });
        },
    };
};

const CheckCashingIntervention = connect(mapStateToProps, mapDispatchToProps)(Intervention);
export { CheckCashingIntervention };



