import React from "react";
import cloneDeep from "lodash.clonedeep";

import { RegistrationRetrievalNumber, SecurityQuestion, ResetSuccess } from "../containers/customer/ResetPasswordPages";

import { INITIATE_RESET_PASSWORD } from "../actions";
import { RESET_PASSWORD_NEXT_PAGE, RESET_PASSWORD_PREV_PAGE,
  FILL_UP_RESET_PASSWORD_REQUEST_DATA, FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA,
  FILL_UP_FORCE_CHANGE_PASSWORD, GET_CUSTOMER_QA_SUCCESS, GET_CUSTOMER_QA_FAILURE, resetPassword, getCustomerQA } from "../actions/resetPassword";

const resetPasswordSteps = ["reset.password.page.rrn.step", "steps.security"];
const resetPasswordPages = [
    { step: "reset.password.page.rrn.step", title: "Username", component: <RegistrationRetrievalNumber />, onContinue: getCustomerQA },
    { step: "steps.security", title: "SecurityQuestion", component: <SecurityQuestion />, onContinue: resetPassword },
    { title: "Sucess", component: <ResetSuccess /> },
];

const defaultState = {
    requestData: {
        registrationRetrievalNumber: null,
        customerQA: {
            questionType: "MMN",
            answer: null,
        },
    },
    currentIndex: 0,
    pages: resetPasswordPages,
    steps: resetPasswordSteps,
    forceChangePasswordRequestData: {},
    showPopUp: false,
};

const resetPasswordReducer = (state, action) => {
    const resetPasswordState = state.resetPassword || defaultState;
    const nextResetPassword = cloneDeep(resetPasswordState);

    switch (action.type) {
        case FILL_UP_RESET_PASSWORD_REQUEST_DATA: {
            Object.assign(nextResetPassword.requestData, action.data);
            return nextResetPassword;
        }
        case FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA: {
            Object.assign(nextResetPassword.requestData.customerQA, action.data);
            return nextResetPassword;
        }
        case GET_CUSTOMER_QA_SUCCESS: {
            const customerQA =  {
                questionType: action.response.getCustomerQAResponse.questionType,
                answer: null,
            };
            Object.assign(nextResetPassword.requestData.customerQA, customerQA);
            return nextResetPassword;
        }
        case GET_CUSTOMER_QA_FAILURE: {
            const customerQA =  {
                questionType: "MMN",
                answer: null,
            };
            Object.assign(nextResetPassword.requestData.customerQA, customerQA);
            return nextResetPassword;
        }
        case RESET_PASSWORD_NEXT_PAGE: {
            nextResetPassword.currentIndex += 1;
            return nextResetPassword;
        }
        case RESET_PASSWORD_PREV_PAGE: {
            nextResetPassword.currentIndex -= 1;
            return nextResetPassword;
        }
        case FILL_UP_FORCE_CHANGE_PASSWORD: {
            Object.assign(nextResetPassword.forceChangePasswordRequestData, action.data);
            return nextResetPassword;
        }
        case INITIATE_RESET_PASSWORD:
            return defaultState;
        case "SHOW_POP_UP": {
            nextResetPassword.showPopUp = action.showPopUp;
            return nextResetPassword;
        }
        default:
            return nextResetPassword;
    }
};

export default resetPasswordReducer;



