import React from "react";
import { connect } from "react-redux";
import { MessageArgs } from "common-functions";
import { Panel } from "common-components";

import { faqsInfo, faqsInfoSelect, HELP_FAQS_CATEGORY_SELECT } from "../../actions/help";

import ViewComponent from "../../components/ViewComponent";
import Navigation from "../../components/Navigation";
import Card from "../../components/Card";

class Faqs extends ViewComponent {

    componentDidMount() {
        this.props.onLoadData(this.props.language);
    }

    componentDidUpdate() {
        this.panel.update();
    }

    onHelpItemClick(index) {
        if (this.props.data) {
            const data = Object.assign([], this.props.data);
            switch (this.props.selectedFaq) {
                case "transaction":
                    data.transaction[index].shown = !data.transaction[index].shown;
                    this.props.onHelpItemClick(data);
                    break;

                case "voucher":
                    data.voucher[index].shown = !data.voucher[index].shown;
                    this.props.onHelpItemClick(data);
                    break;

                case "limits":
                    data.limits[index].shown = !data.limits[index].shown;
                    this.props.onHelpItemClick(data);
                    break;

                default:
                    data.account[index].shown = !data.account[index].shown;
                    this.props.onHelpItemClick(data);
                    break;
            }
        }
    }

    getHelpItem(item, index) {
        const { messageArgs, language } = this.props;
        const shown = item.shown;
        const imagePath = `resources/images/help-${shown ? "hide" : "show"}.png`;
        let answer = Array.isArray(item.answer) ? item.answer.join(" ") : item.answer;
        answer = MessageArgs.replaceVars(answer, messageArgs, language);
        const answerComponent = (shown ? <div className="help-answer with-sound" dangerouslySetInnerHTML={{ __html: answer }} /> : null);

        return (
            <div key={index}
              className="help-item"
              role="button"
              tabIndex="-1"
              onClick={() => this.onHelpItemClick(index)}>
                <p className="help-question">{item.question}</p>
                <img alt="icon" src={imagePath} className="collapsible" />
                <div className="clearfix" />
                {answerComponent}
            </div>
        );
    }

    render() {
        const hotline = this.props.hotline || "";
        let questionAnswer = null;
        let items = [];
        let selected = "account";

        if (this.props.data) {
            switch (this.props.selectedFaq) {
                case "transaction":
                    selected = "transaction";
                    questionAnswer = this.props.data.transaction;
                    break;

                case "voucher":
                    selected = "voucher";
                    questionAnswer = this.props.data.voucher;
                    break;

                case "limits":
                    selected = "limits";
                    questionAnswer = this.props.data.limits;
                    break;

                default:
                    questionAnswer = this.props.data.account;
                    break;
            }
            if (questionAnswer && questionAnswer.length > 0) {
                items = questionAnswer.map((item, index) => this.getHelpItem(item, index));
            }
        }

        return (<div className="help">
            <div className="faq-buttons">
                <Card messageKey="account"
                  className={`faq-button ${selected === "account" ? "selected" : ""}`}
                  onClick={() => this.props.selectedCategory(this.props.data, "account")} data-analytics="Account">help.page.faqs.subtitle.account</Card>
                <Card messageKey="transaction"
                  className={`faq-button ${selected === "transaction" ? "selected" : ""}`}
                  onClick={() => this.props.selectedCategory(this.props.data, "transaction")} data-analytics="Transaction">help.page.faqs.subtitle.transaction</Card>
                <Card messageKey="voucher"
                  className={`faq-button ${selected === "voucher" ? "selected" : ""}`}
                  onClick={() => this.props.selectedCategory(this.props.data, "voucher")} data-analytics="Voucher">help.page.faqs.subtitle.voucher</Card>
                <Card messageKey="limits"
                  className={`faq-button ${selected === "limits" ? "selected" : ""}`}
                  onClick={() => this.props.selectedCategory(this.props.data, "limits")} data-analytics="Limits">help.page.faqs.subtitle.limits</Card>
            </div>

            <div className="relative">
                <Panel ref={(panel) => (this.panel = panel)} className="help-body faq-body">
                    <Card messageKey="help.hotline" className="help-hotline" args={hotline}>help.page.faqs.hotline</Card>
                    <div className="items">
                        {items} <br />
                    </div>
                </Panel>
                <img alt="gradient-overlay" className="overlay" src="resources/images/gradient-overlay.png" />
            </div>

            <Navigation prevUrl="/" disableNext />
        </div>);
    }

}

const mapStateToProps = (state) => {
    return {
        selectedFaq: state.help.selectedFaq,
        data: state.help.faqsData,
        hotline: state.config.defaultValues.helpHotline,
        language: state.internationalization.language,
        messageArgs: state.config.messageArgs,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onHelpItemClick: (data) => {
            faqsInfoSelect(data, dispatch);
        },
        onLoadData: (language) => {
            faqsInfo(language, null, dispatch);
        },
        selectedCategory: (data, selectedFaq) => {
            dispatch({ type: HELP_FAQS_CATEGORY_SELECT, data, selectedFaq });
        },
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Faqs);



