import { RestHost } from "common-functions";
import { moneygramMessagesLoader } from "./moneygramMessages";

export const INTERNATIONALIZATION_REQUEST = "INTERNATIONALIZATION_REQUEST";
export const INTERNATIONALIZATION_SUCCESS = "INTERNATIONALIZATION_SUCCESS";
export const INTERNATIONALIZATION_FAILURE = "INTERNATIONALIZATION_FAILURE";

export const languageTranslate = (language, dispatch, processOnBackground) => {
    dispatch({ type: INTERNATIONALIZATION_REQUEST, processOnBackground });

    const restHost = new RestHost("resources/internationalization/", null, null);
    const selectedLanguage = language || "en";
    window.localStorage.setItem("lang", selectedLanguage);

    restHost.get(`${selectedLanguage}.properties`,
        (response) => dispatch({ type: INTERNATIONALIZATION_SUCCESS, translation: response, selectedLanguage, processOnBackground }),
        (error) => dispatch({ type: INTERNATIONALIZATION_FAILURE, error, processOnBackground }),
        RestHost.PROPERTIES,
        true,
    );

    moneygramMessagesLoader(selectedLanguage, dispatch, processOnBackground);
};



