import React from "react";
import { connect } from "react-redux";
import { Helpers } from "common-functions";
import { Dropdown, Input, Div, P, Label } from "common-components";

import { FILL_UP_AMOUNT, NEXT_PAGE, PREV_PAGE, FILL_UP_DATE, VALIDATE_DATE } from "../../actions/constants";
import { VALIDATE, VALIDATION_FAILED, CLEAR_SPECIFIC_VALIDATION_ERROR } from "../../actions/validation";

import { getMaxAmount, getPaymentFee } from "../../reducers/checkCashing";

import Navigation from "../../components/Navigation";

const monthsSelection = [
    { value: "01", name: "months.jan" },
    { value: "02", name: "months.feb" },
    { value: "03", name: "months.mar" },
    { value: "04", name: "months.apr" },
    { value: "05", name: "months.may" },
    { value: "06", name: "months.jun" },
    { value: "07", name: "months.jul" },
    { value: "08", name: "months.aug" },
    { value: "09", name: "months.sep" },
    { value: "10", name: "months.oct" },
    { value: "11", name: "months.nov" },
    { value: "12", name: "months.dec" },
];

const AmountInputPage = ({ onAmountChange, voucher, showAmountError, onNext, onPrevious, hasValidInput, onNoInput, language }) => {
    const MAX_AMOUNT = Helpers.formatMoney(getMaxAmount());
    const FEE_AMOUNT = Helpers.formatMoney(getPaymentFee());
    const NEXT = language === "en" ? "Next" : "siguente";
    const amountBelow = showAmountError === "check.cashing.amount.error.message";
    const amountErrorClass = showAmountError !== "check.cashing.amount.error.message" ? "amount-error-welcome2" : "amount-error-welcome";
    return (<Div>
        <Div className="check-amount-page">
            <P className="mo-review-subtitle">details.of.check.label</P>
            <Div className="pull-left">
                <Label>check.cashing.amount</Label>

                <Input prefix="$"
                  name="checkAmount"
                  value={voucher.checkAmount}
                  className="input-review-edit col-6"
                  onChange={onAmountChange}
                  placeholder="check.cashing.amount.placeholder"
                  shouldAutoFocus />

                <P className={amountErrorClass} args={[MAX_AMOUNT, FEE_AMOUNT, NEXT ]} style={{ display: showAmountError ? "" : "none" }}>{showAmountError}</P>
            </Div>
        </Div>

        <Navigation onNext={hasValidInput || amountBelow ? onNext : onNoInput}
          onPrevious={onPrevious}
          nextOnEnter
          hasFormatErrors={!hasValidInput}
          hasInput={amountBelow ? !amountBelow : !hasValidInput} />
    </Div>);
};

const DateInputPage = ({ onDateChange, onDayChange, checkDate, showAmountError, onPrevious, hasValidDate, onDateInput }) => {
    const MAX_AMOUNT = Number(getMaxAmount()).toLocaleString();
    return (<Div>
        <Div className="check-date-page">
            <P className="mo-review-subtitle">details.of.check.label</P>
            <Div className="pull-left">
                <Label>check.cashing.date</Label>

                <Dropdown name="checkMM"
                  value={checkDate.checkMM}
                  items={monthsSelection}
                  onChange={onDateChange}
                  placeholder="register.customer.month.placeholder"
                  className="col-4"
                  focusOnCompleteFillUp="checkDD"
                  maxDisplayedItem={3}
                  shouldAutoFocus />

                <Input name="checkDD"
                  value={checkDate.checkDD}
                  className="input-review-edit date-dd"
                  onChange={(event) => onDayChange(event, onDateChange)}
                  focusOnCompleteFillUp="checkYY"
                  placeholder="DD" />

                <Input prefix="20"
                  name="checkYY"
                  value={checkDate.checkYY}
                  className="input-review-edit date-yy"
                  onChange={onDateChange}
                  placeholder="YY" />

                <P className="amount-error-welcome" args={MAX_AMOUNT} style={{ display: showAmountError ? "" : "none" }}>{showAmountError}</P>
            </Div>
        </Div>

        <Navigation onNext={onDateInput}
          onPrevious={onPrevious}
          hasFormatErrors={!hasValidDate}
          hasInput={!hasValidDate}
          nextOnEnter />
    </Div>);
};

const onChange = (event, dispatch, action) => {
    dispatch({ type: action, target: event.target });
};

const onDayChange = (event, callback) => (dispatch) => {
    const { name, value } = event.target;
    callback(event);

    if (value) {
        if (!Helpers.isDayValid(value)) {
            dispatch({ type: VALIDATION_FAILED, error: { fieldName: name, message: "invalid.day" } });
        } else {
            dispatch({ type: CLEAR_SPECIFIC_VALIDATION_ERROR, filedName: name });
        }
    }
};

const mapDispatchToProps = (dispatch, state) => {
    const showAmountError = state.checkCashing && state.checkCashing.showAmountError;
    return {
        onAmountChange: (event) => {
            if(showAmountError === ""){
                onChange(event, dispatch, FILL_UP_AMOUNT);
            } else {
                onChange(event, dispatch, FILL_UP_AMOUNT)
                dispatch({ type: FILL_UP_AMOUNT, showError: false });
            }
        },
        onDayChange: (event, callback) => dispatch(onDayChange(event, callback)),
        onDateChange: (event) => {
            onChange(event, dispatch, FILL_UP_DATE);
            return true;
        },
        onDateInput: (event) => {
            onChange(event, dispatch, VALIDATE_DATE);
            return true;
        },
        onNoInput: () => {
            dispatch({ type: FILL_UP_AMOUNT, showError: true });
        },
        onNext: () => {
            dispatch({ type: NEXT_PAGE });
        },
        onPrevious: () => {
            dispatch({ type: PREV_PAGE });
        },
    };
};

const mapStateToProps = (state) => {
    return {
        showAmountError: state.checkCashing.showAmountError,
        hasValidInput: state.checkCashing.hasValidInput,
        hasValidDate: state.checkCashing.hasValidDate,
        voucher: state.checkCashing.voucher,
        checkDate: state.checkCashing.checkDate,
        language: state.internationalization.language,
    };
};


const AmountInput = connect(mapStateToProps, mapDispatchToProps)(AmountInputPage);
const DateInput = connect(mapStateToProps, mapDispatchToProps)(DateInputPage);

export { AmountInput, DateInput };



