import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Intl, Helpers, Validation } from "common-functions";
import styled from "styled-components";
import Draggable from "react-draggable";

import { Label, P } from "./HTML";
import { FIELD_FOCUS } from "../actions/keyboardAction";

const InputContainer = styled.div`
    overflow: hidden;
    display: inline-block;
    position: absolute;
`;

const InputValue = styled.div`
    white-space: nowrap;
`;

class InputV2 extends React.Component {
    constructor(props) {
        super(props);
        this.refocusOnValidationLoad = true;
        this.handleInput = this.handleInput.bind(this);
        this.getInputTextLeftBound = this.getInputTextLeftBound.bind(this);
        this.handleDrag = this.handleDrag.bind(this);
        this.viewRightmostOfInputText = this.viewRightmostOfInputText.bind(this);

        this.state = {
            // this property keeps track of the text's scrolling position.
            textScrollX: 0,
        };
    }

    componentDidMount() {
        this.bindKeyboardEvent();
        const { mobile, landscape } = Helpers.navigator;
        const disableAutoFocus = mobile && landscape;
        if (this.props.shouldAutoFocus && this.props.validationLoaded && !disableAutoFocus) {
            this.input.focus();
            this.refocusOnValidationLoad = false;
        }
    }

    componentWillReceiveProps(nextProps) {
        this.focusNext = false;
        const { focusOnCompleteFillUp, errorMessage } = nextProps;
        if (focusOnCompleteFillUp && this.props.value !== nextProps.value && !errorMessage) {
            const value = String(nextProps.value);
            if (value.length === this.input.maxLength) {
                this.focusNext = true;
            }
        }
    }

    componentDidUpdate() {
        this.bindKeyboardEvent();
        const { focusOnCompleteFillUp, shouldAutoFocus, onFocus, validationLoaded } = this.props;
        const { mobile, landscape } = Helpers.navigator;
        const disableAutoFocus = mobile && landscape;

        if (shouldAutoFocus && validationLoaded && this.refocusOnValidationLoad && !disableAutoFocus) {
            onFocus(this.input);
        }

        if (this.focusNext && focusOnCompleteFillUp) {
            const focusOnCompleteFillUpElement = document.getElementById(focusOnCompleteFillUp);
            if (focusOnCompleteFillUpElement) {
                focusOnCompleteFillUpElement.focus();
            }
        }
    }

    getInputProps() {
        const props = this.props;
        const id = props.id || props.name;

        const validationAttributes = Validation.getAsAttributes(props.validation);
        const inputProps = Object.assign({}, validationAttributes, props, { id });

        inputProps.value = inputProps.value || inputProps.value === 0 ? inputProps.value : "";

        const currentClassName = inputProps.className ? `${inputProps.className} ` : "";
        const additionalClassName = inputProps.selected ? "selected" : "";
        inputProps.className = `${currentClassName}${additionalClassName}`;

        inputProps.onClick = (event) => {
            Helpers.setCaretToEnd(id);
            if (props.onClick) {
                props.onClick(event);
            }
        };

        inputProps.onKeyPress = (event) => {
            if (props.onEnter) {
                const keyCode = event.which || event.keyCode;
                if (keyCode === 13) { // ENTER
                    props.onEnter(event);
                }
            }
            if (props.onKeyPress) {
                props.onKeyPress(event);
            }
        };

        delete inputProps.onEnter;
        delete inputProps.translation;
        delete inputProps.label;
        delete inputProps.validation;
        delete inputProps.errorMessage;
        delete inputProps.dispatch;
        delete inputProps.shouldAutoFocus;
        delete inputProps.hideErrorMessage;
        delete inputProps.hideErrorLabel;
        delete inputProps.labelComponent;
		delete inputProps.labelClassName;
        delete inputProps.parentClassName;
        delete inputProps.onChange;
        delete inputProps.focusOnCompleteFillUp;
        delete inputProps.defaultPlaceholder;
        delete inputProps.validationLoaded;
        delete inputProps.allowedLetters;
        delete inputProps.customErrorMessage;
        delete inputProps.sessionReplaying;
        delete inputProps.inputRef;
        delete inputProps.notes;
		delete inputProps.isWebsite;
        delete inputProps.defaultErrorMessage;
        return inputProps;
    }

    getAdjustedPaddingIfHasPrefix() {
        const { prefix } = this.props;
        const isWebsite = typeof this.props.isWebsite !== "undefined";

        const charWithWideStroke = "M";
        const charWithShortStroke = "I";
        const approxPixelsPerLetter = 26;

        let padding = 0;
        if (prefix.length > 3) return "";

        if (isWebsite) {
            padding -= prefix.indexOf(charWithShortStroke) !== -1 ? 8 : 0;
            padding += prefix.length * 10;
            padding -= !isNaN(prefix) ? 5 : 0;
            return padding + 18;
        }

        padding += (prefix.indexOf(charWithWideStroke) !== -1 && prefix.length !== 3) ? 6 : 0;
        padding -= prefix.indexOf(charWithShortStroke) !== -1 ? 6 : 0;
        padding -= !isNaN(prefix) ? 8 : 0;

        return (prefix.length * approxPixelsPerLetter) + padding;
    }

    getInputWidth() {
        if (!this.input) {
            return 0;
        }
        return this.input.offsetWidth;
    }

    // gets the furthest the draggable text can go before refusing to go any further.
    // this is required so that the text can't be scrolled so far that it leaves the box.
    getInputTextLeftBound() {
        if (!this.inputTextRef) return 0;

        const { offsetWidth } = this.inputTextRef;

        const inputWidth = this.getInputWidth();

        return offsetWidth > inputWidth ? (
            -(offsetWidth - inputWidth)
        ) : 0;
    }

    handleInput(event) {
        if (this.props.onChange) {
            this.props.onChange(event);
        }
        // wait for update to get the new text width
        this.forceUpdate(this.viewRightmostOfInputText);
    }

    bindKeyboardEvent() {
        if (this.input) {
            this.input.removeEventListener("onkeyclick", this.handleInput);
            this.input.addEventListener("onkeyclick", this.handleInput, false);
        }
    }

    // this is used to update the x coordinate when the user swipes the text
    handleDrag(e, { x }) {
        this.setState({
            textScrollX: x,
        });
    }

    // jumps the input value to the very right, so you can see the end of the text.
    viewRightmostOfInputText() {
        this.setState({
            textScrollX: this.getInputTextLeftBound(),
        });
    }

    render() {
        const props = this.props;
        const id = props.id || props.name;

        const label = props.label || props.placeholder;
        const translatedLabel = Intl.translate(props.translation, label);
        const labelComponent = props.label ? <Label htmlFor={id}>{props.label}</Label> : props.labelComponent;

        let inputContainerClassName = props.prefix ? "prefixed relative" : "relative";
        inputContainerClassName = props.disabled ? `${inputContainerClassName} disabled` : inputContainerClassName;


        const hasPrefix = props.prefix;
        const prefixComponent = hasPrefix ? <p className="prefix">{props.prefix}</p> : null;
        const placeHolderComputedPadding = hasPrefix ? this.getAdjustedPaddingIfHasPrefix() : "";
        const paddingAdjustmentStyle = { paddingLeft: placeHolderComputedPadding };

        const inputProps = this.getInputProps();

        inputContainerClassName = hasPrefix ? `${inputContainerClassName} prefixed` : inputContainerClassName;

        let placeholderComponent = null;
        const placeholderIntl = Intl.translate(props.translation, props.placeholder, props.defaultPlaceholder);

        if (Helpers.hasPlaceholderIssue() && !inputProps.value && inputProps.value !== 0) {
            const inputClassName = inputProps.className ? ` ${inputProps.className}` : "";
            placeholderComponent = <P id={`${id}Placeholder`} className={`placeholder ${inputClassName}`} style={paddingAdjustmentStyle} onClick={() => this.input.focus()}>{placeholderIntl}</P>;
            delete inputProps.placeholder;
        } else {
            inputProps.placeholder = placeholderIntl;
        }

        const decorComponent = (<div className="absolute">
            {prefixComponent}
            {placeholderComponent}
        </div>);

        const dataLabel = !/^[\s]+$/.test(translatedLabel) ? translatedLabel : placeholderIntl;
        const propsParentClassName = props.parentClassName ? `${props.parentClassName} ` : "";
        const parentClassName = `${props.errorMessage ? "has-error " : ""}${propsParentClassName}pull-left`;
        const showNotes = props.notes && !(props.errorMessage || props.customErrorMessage);

        const inputRef = (input) => {
            this.input = input;
            if (this.props.inputRef) {
                this.props.inputRef(input);
            }
        };

        const inputTextRef = (ref) => {
            this.inputTextRef = ref;
        };

        const messageClass = `${inputProps.className} message error`;
        const { hideErrorMessage, hideErrorLabel, errorMessage, customErrorMessage, defaultErrorMessage } = props;
        return (<div className={parentClassName} style={{ display: props.hidden ? "none" : "" }}>
            {labelComponent}

            <div className={inputContainerClassName}
              style={{
                  position: "relative",
              }}>
                {// eslint-disable-next-line no-unused-expressions
                }<InputContainer onMouseDown={() => { !this.props.selected && this.input && this.input.focus(); }}>
                    {/* The draggable component is used to be able to scroll long input text with the mouse */}
                    {/* It also can be positioned programatically. */}
                    <Draggable axis="x"
                      position={{ x: this.state.textScrollX, y: 0 }}
                      // the input text has bounds. this is to ensure we can't scroll it out of view.
                      bounds={{
                          right: 0,
                          // the left bound depends on how long the input text is. the longer the text, the bigger the bounds.
                          ...(this.inputTextRef ? { left: this.getInputTextLeftBound() } : 0),
                      }}
                      onDrag={this.handleDrag}>
                        <InputValue>
                            <div className={inputProps.className} style={{ width: this.getInputWidth(), ...paddingAdjustmentStyle }}>
                                <span ref={inputTextRef}
                                  className={`input-bad-scroll-patch ${this.props.disabled ? "scroll-patch-disabled" : ""}`}>
                                    {this.props.value}
                                </span>
                            </div>
                        </InputValue>
                    </Draggable>
                </InputContainer>

                {hasPrefix ? decorComponent : placeholderComponent}
                <input {...inputProps}
                  style={{
                      ...paddingAdjustmentStyle,
                      ...(this.input && this.input.value ? { color: "transparent" } : {}),
                  }}
                  data-label={dataLabel}
                  onChange={this.handleInput}
                  onFocus={(e) => {
                      props.onFocus(this.input || e.target);
                      this.viewRightmostOfInputText();
                  }}
                  ref={inputRef} />
            </div>

            <div className="clearfix" />
            {showNotes ? <P className="message notes" id={`${id}-notes`}>{props.notes}</P> : null}
            {!hideErrorMessage && !hideErrorLabel ? <P className={messageClass} id={`${id}-error`} defaultValue={defaultErrorMessage}>
                {errorMessage || customErrorMessage}
            </P> : null}
        </div>);
    }
}

InputV2.propTypes = {
    onChange: PropTypes.func,
    shouldAutoFocus: PropTypes.bool,
};

InputV2.defaultProps = {
    translation: {},
    type: "text",
    disabled: false,
    shouldAutoFocus: false,
    onChange: null,
};

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.id || ownProps.name;
    const i18n = state.internationalization;
    const validations = state.validation || state.validations;
    const validation = validations ? validations.find((item) => item.fieldName === id) : null;
    const validationLoaded = validations && validations.length > 0;

    let error = null;
    if (state.ui && !ownProps.hideErrorMessage) {
        error = state.ui.errors.find((item) => item.fieldName === id);
    }

    const { keyboard } = state;
    const selected = keyboard && keyboard.selectedField && keyboard.selectedField.id === id;

    return {
        validation,
        validationLoaded,
        selected,
        errorMessage: error ? error.message : null,
        defaultErrorMessage: error ? error.defaultMessage: null,
        translation: i18n ? i18n.translation : null,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onFocus: (selectedField) => dispatch({ type: FIELD_FOCUS, selectedField }),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(InputV2);



