import React from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import { KEYPRESS, BACKSPACE, CLEAR, ENTER, HIDE_KEYBOARD, onKeyclick } from "../actions/keyboardAction";

import KeyboardComponent from "../components/KeyboardComponent";

class Keyboard extends React.Component {

    componentDidMount() {
        const { history, hideKeyboard } = this.props;
        this.unlisten = history.listen((location) => {
            const currentPath = location.pathname;
            if (this.previousPath !== currentPath) {
                hideKeyboard();
            }
            this.previousPath = currentPath;
        });
    }

    componentWillUnmount() {
        this.unlisten();
    }

    render() {
        return <KeyboardComponent {...this.props} />;
    }

}

const onKeypress = (key) => (dispatch, getState) => {
    dispatch({ type: KEYPRESS, key });
    onKeyclick(getState().keyboard.selectedField);
};

const onBackspace = () => (dispatch, getState) => {
    dispatch({ type: BACKSPACE });
    onKeyclick(getState().keyboard.selectedField);
};

const onClear = () => (dispatch, getState) => {
    dispatch({ type: CLEAR });
    onKeyclick(getState().keyboard.selectedField);
};

const mapStateToProps = (state) => {
    const { show, allowedSpecialCharacters, allowedLetters, allowedNumbers, allowedTopLevelDomain, australiaOnly, unMapped } = state.keyboard;
    return { show, allowedSpecialCharacters, allowedLetters, allowedNumbers, allowedTopLevelDomain, australiaOnly, unMapped };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onKeypress: (key) => dispatch(onKeypress(key)),
        onBackspace: () => dispatch(onBackspace()),
        onClear: () => dispatch(onClear()),
        onEnter: () => dispatch({ type: ENTER }),
        hideKeyboard: () => dispatch({ type: HIDE_KEYBOARD }),
    };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Keyboard));



