import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Intl } from "common-functions";

const renderCardAsItem = ({ className, properties, children }) => (
    <div className={className} {...properties}>
        <div className="card-wrapper">
            <span className="helper" />
            <div className="card-content">
                {children}
            </div>
        </div>
    </div>
);

const renderCardAsButton = ({ className, properties, children }) => (
    <button className={className} {...properties}>
        {children}
    </button>
);

const Card = (props) => {
    let children = props.children;
    const className = `card section-item ${props.className || ""}`;
    const properties = Intl.removeInternationalizationProps(props);
    delete properties.dispatch;
    delete properties.className;

    if (typeof children === "string") {
        children = Intl.translate(props.translation, children, props.defaultValue, props.args);
    }

    const cardParams = { className, properties, children };

    let component =  props.renderAsItem ? renderCardAsItem(cardParams) : renderCardAsButton(cardParams);

    if (typeof props.to !== "undefined") {
        delete component.onClick;
        component = <Link to={props.to}>{component}</Link>;
    }
    return component;
};

const mapStateToProps = (state) => {
    return {
        translation: state.internationalization.translation,
    };
};

export default connect(mapStateToProps)(Card);



