import React from "react";
import { connect } from "react-redux";

const LoadingBox = (props) => {
    const loadingBox = (<div id="loadingBox" className={`loading-box show-${props.show}`} data-show={String(props.show)}>
      <div className="loading-box-image">
        <img src="resources/images/spinner.gif" alt="Loading spinner" />
      </div>
    </div>);
    return (props.show ? loadingBox : null);
};

const mapStateToProps = (state) => {
    return { show: state.ui.onGoingRequestCount >= 1 };
};

export default connect(mapStateToProps)(LoadingBox);



