import React from "react";
import { connect } from "react-redux";
import { Div } from "../../components/HTML";
import Overview from "../../components/Overview";

const overviewHeader = "steps.to.check.cashing.header";

const overviewItemsForNewCustomer = [
    "steps.to.check.cashing.new.customer.1",
    "steps.to.check.cashing.new.customer.2",
    "steps.to.check.cashing.new.customer.3",
    "steps.to.check.cashing.new.customer.4",
    "steps.to.check.cashing.new.customer.5",
    "steps.to.check.cashing.new.customer.6",
];

const overviewItemsForExistingCustomer = [
    "steps.to.check.cashing.existing.customer.1",
    "steps.to.check.cashing.existing.customer.2",
    "steps.to.check.cashing.existing.customer.3",
    "steps.to.check.cashing.existing.customer.4",
    "steps.to.check.cashing.existing.customer.5",
    "steps.to.check.cashing.existing.customer.6",
];

const CheckCashingOverview = ({ newUser, onCancel, onContinue }) => (
  <Div>
    <Div className="page-background page-background-2" />
    <Overview
      header={overviewHeader}
      items={newUser ? overviewItemsForNewCustomer : overviewItemsForExistingCustomer}
      onCancel={onCancel}
      onContinue={onContinue}
      cancelURL={"/"}
      continueURL={newUser ? "register" : "login"} /></Div>
);

const mapDispatchToProps = (dispatch) => {
    return {
        onContinue: () => {
            dispatch({ type: "INITIATE_CHECK_CASHING" });
        },
        onCancel: () => {
            dispatch({ type: "INITIATE_CHECK_CASHING" });
        },
    };
};

const mapStateToProps = (state) => {
    return {
        newUser: state.customer.newUser,
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(CheckCashingOverview);



