import React from "react";
import { connect } from "react-redux";
import { Helpers } from "common-functions";
import { NEXT_PAGE, HIDE_KEYBOARD } from "../../actions/constants";
import { Div } from "../../components/HTML";
import Overview from "../../components/Overview";

const overviewHeader = "success.print.voucher";
const overviewSubHeader = "success.print.voucher.dont.forget";
let printTimeout = null;

class PrintVoucher extends React.Component {
    componentDidMount() {
        const { onContinue, hideKeyboard } = this.props;
        hideKeyboard();
        printTimeout = window.setTimeout(() => {
            onContinue();
        }, 5000);
    }

    render() {
        const { onContinue, cashAmount, cardAmount } = this.props;
        const overviewItems = [
            "success.print.voucher.step1",
            { text: "success.print.voucher.step2" },
        ];
        if (cashAmount) {
            overviewItems.push({ text: "success.print.voucher.step.cash", args: Helpers.formatMoney(cashAmount) });
        }
        if (cardAmount) {
            overviewItems.push({ text: "success.print.voucher.step.card", args: Helpers.formatMoney(cardAmount) });
        }

        return (<Div>
            <Div className="review-page customer-review-page">
                <Overview onContinue={onContinue}
                  subheader={overviewSubHeader}
                  header={overviewHeader}
                  items={overviewItems}
                  hideCancel
                  continueBtnLabel="continue.new.transaction" />
            </Div>
        </Div>);
    }
}

const mapStateToProps = (state) => {
    return {
        present: state.checkCashing.present,
        cashAmount: state.checkCashing.voucher.receiveAmount,
        cardAmount: state.checkCashing.voucher.cardAmount,
        steps: state.checkCashing.steps,
        currentStep: 1,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onContinue: () => {
            if (printTimeout) window.clearTimeout(printTimeout);
            dispatch({ type: NEXT_PAGE });
        },
        hideKeyboard: () => {
            dispatch({ type: HIDE_KEYBOARD });
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PrintVoucher);



