import React from "react";
import { P, Button } from "common-components";

const Intervention = ({ title, description, children, className, background, show, hideIntervention }) => {
    const defaultClassName = "intervention";

    const defaultChildren = (<div>
        <P className="title">{title || "intervention.default.title"}</P> <br />
        <P className="description">{description || "intervention.default.description"}</P>
        <Button className="small" to="/" onClick={hideIntervention} data-analytics="Back to main menu">back.main.menu.btn</Button>
    </div>);

    const component = (<div className={className ? `${className} ${defaultClassName}` : `${defaultClassName}`}>
        {background ? <img className="cover" alt="Intervention background" src={`resources/images/${background}`} /> : null}
        <div className="centered">
            {children || defaultChildren}
        </div>
    </div>);
    return show ? component : null;
};

export default Intervention;



