import React from "react";
import { MessageArgs } from "common-functions";

const Helpers = require("./Helpers");

const CCGIntl = {};

CCGIntl.translate = (translationArg, key, defaultValue, args) => {
    let result = defaultValue || key;
    const lang = window.localStorage.getItem("lang") || "en";

    const translationLS = window.localStorage.getItem(`${lang}.properties`) || "{}";
    const translation = translationArg || JSON.parse(translationLS);
    if (translation) {
        if (typeof translation[key] !== "undefined" && translation[key] != null) {
            result = translation[key];
        } else if (typeof translation[defaultValue] !== "undefined" && translation[defaultValue] != null) {
            result = translation[defaultValue];
        } else if (typeof defaultValue !== "undefined" && defaultValue != null) {
            result = defaultValue;
        } else {
            result = key;
        }
    }
    return CCGIntl.replaceVars(result, args, translation);
};

CCGIntl.replaceVars = (value, args, translation) => {
    let result = value;
    if (args) {
        const argsArray = Helpers.isArray(args) ? args : [args];
        argsArray.forEach((arg, index) => {
            const search = `{${index}}`; // e.g. {0}
            const regExp = new RegExp(Helpers.escapeRegExp(search), "g");
            if (typeof arg !== "object") {
                const a = translation[arg] || arg;
                result = result.replace(regExp, a);
            } else {
                const results = result.split(regExp);
                result = <span>{results[0]}{arg}{results[1]}</span>;
            }
        });
    }
    return result;
};

CCGIntl.removeInternationalizationProps = (properties) => {
    const props = Object.assign({}, properties);
    if (props) {
        delete props.messageKey;
        delete props.translation;
        delete props.args;
    }
    return props;
};

CCGIntl.messages = (moneygramMessages, key, messageArgs) => {
    const lang = window.localStorage.getItem("lang") || "en";

    if (moneygramMessages) {
        let result = moneygramMessages[key];
        if (result) {
            result = result.replace(/\\n/g, "<br/>");
            return MessageArgs.replaceVars(result, messageArgs, lang);
        }
    }
    return key;
};

export default CCGIntl;



