import React from "react";
import { RestHost, Helpers } from "common-functions";
import ServiceUnavailable from "../components/ServiceUnavailable";

import { SHOW_INTERVENTION } from "./";

export const VALIDATE = "VALIDATE";
export const VALIDATION_FAILED = "VALIDATION_FAILED";
export const CLEAR_VALIDATION_ERRORS = "CLEAR_VALIDATION_ERRORS";
export const CLEAR_SPECIFIC_VALIDATION_ERROR = "CLEAR_SPECIFIC_VALIDATION_ERROR";

export const VALIDATE_INPUTS_FORMAT = "VALIDATE_INPUTS_FORMAT";
export const RESET_INPUTS_FORMAT_VALIDATION = "RESET_INPUTS_FORMAT_VALIDATION";

export const VALIDATIONS_REQUEST = "VALIDATIONS_REQUEST";
export const VALIDATIONS_SUCCESS = "VALIDATIONS_SUCCESS";
export const VALIDATIONS_FAILURE = "VALIDATIONS_FAILURE";

const addressprohibitedWords = ["BOX", "PMB", "BX", "PO", "POB", "POST", "OFFICE", "PMB", "NBR", "NUMBR", "NUMBER", "POSTBOKS"];

export const validate = (successCallback, errorCallback) => (dispatch, getState) => {
    dispatch({ type: "VALIDATE" });
    const hasError = getState().ui.errors.length;

    if (!hasError && successCallback) {
        successCallback(dispatch, getState);
    } else if (hasError && errorCallback) {
        errorCallback(dispatch, getState);
    }
};

export const mergeErrors = (errors, error) => {
    const errorForFieldExists = errors.find((item) => item.fieldName === error.fieldName);
    return errorForFieldExists ? errors : [...errors, error];
};

export const validationFailed = (fieldName, message) => {
    return {
        type: VALIDATION_FAILED,
        error: { fieldName, message },
    };
};

export const validationsRequest = (dispatch, isProduction) => {
    const processOnBackground = true;
    dispatch({ type: VALIDATIONS_REQUEST, processOnBackground });

    const restHost = new RestHost("resources/config/", null, null);
    const validations = isProduction ? "validations.min.json" : "validations.json";

    restHost.get(`${validations}`,
        (response) => dispatch({ type: VALIDATIONS_SUCCESS, response, processOnBackground }),
        (error) => dispatch({ type: VALIDATIONS_FAILURE, error, processOnBackground }),
        RestHost.JSON,
        processOnBackground,
    );
};

const createSpacedRegExp = (prohibitedWord) => {
    let prohibitedWordRegExp = prohibitedWord.charAt(0);
    for (let i = 1; i < prohibitedWord.length; i += 1) {
        prohibitedWordRegExp = `${prohibitedWordRegExp} *${prohibitedWord.charAt(i)}`;
    }
    prohibitedWordRegExp += "[ 0-9]*$";
    return new RegExp(prohibitedWordRegExp);
};

const doesAddressContainProhibitedWords = (address1) => {
    const address = String(address1).toUpperCase();

    for (let i = 0; i < addressprohibitedWords.length; i += 1) {
        const prohibitedWord = addressprohibitedWords[i].toUpperCase();
        const prohibitedWordRegExp = createSpacedRegExp(prohibitedWord);

        if (prohibitedWordRegExp.test(address)) {
            return true;
        }
    }
    return false;
};

const reverseNumeric = (str) => {
    if (str) {
        if (Helpers.isNumber(str) && str.length > 1) {
            const firstNum = str.charAt(0) / 1;
            const secondNum = str.charAt(1) / 1;
            if ((firstNum > secondNum) || (firstNum === 0 && secondNum === 9)) {
                const splitString = str.split("");
                const reverseArray = splitString.reverse();
                const joinArray = reverseArray.join("");
                return joinArray;
            }
        }
    }
    return str;
};

export const getPattern = (arg) => {
    const value = reverseNumeric(String(arg));

    let previousChar = null;
    let sequential = true;
    let counting = true;
    let repeating = true;

    for (let i = 0; i < value.length; i++) {
        const char = String(value[i]);
        if (previousChar) {
            if (!Helpers.isNumber(char) || Math.abs(parseInt(char)) !== Math.abs(parseInt(previousChar) + 1)) {
                sequential = false;
            }
            if (Helpers.isNumber(char) && counting) {
                const charValue = char / 1;
                const pCharValue = previousChar / 1;
                if (charValue === 0) {
                    counting = pCharValue === 9;
                } else if ((charValue - pCharValue) !== 1) {
                    counting = false;
                }
            } else {
                counting = false;
            }
            if (char !== previousChar) {
                repeating = false;
            }
        }
        previousChar = char;
    }
    return { sequential, repeating, counting };
};

export const hasSequentialSpecialCharacter = (arg) => {
    const value = arg ? arg.toString() : "";
    const hasSpecialCharacter = value.match(/[^a-zA-Z0-9 ]/);

    if (hasSpecialCharacter) {
        if (value.indexOf(",,") > -1 || value.indexOf("..") > -1 || value.indexOf("//") > -1
             || value.indexOf("--") > -1 || value.indexOf("__") > -1 || value.indexOf("@@") > -1
             || value.indexOf("''") > -1 || value.indexOf("``") > -1 || value.indexOf("‘‘") > -1
             || value.indexOf("’’") > -1 || value.indexOf("'’") > -1 || value.indexOf("'`") > -1
             || value.indexOf("'‘") > -1 || value.indexOf("`'") > -1 || value.indexOf("`’") > -1
             || value.indexOf("`‘") > -1 || value.indexOf("‘'") > -1 || value.indexOf("‘`") > -1
             || value.indexOf("‘’") > -1 || value.indexOf("’'") > -1 || value.indexOf("’`") > -1
             || value.indexOf("’‘") > -1) {
            return true;
        }
    }
    return false;
};

export const doesNameStartsWithSpecialCharacter = (value) => {
    const name = value ? value.toString() : "";
    return name.startsWith(",") || name.startsWith(".") || name.startsWith("/")
     || name.startsWith("-") || name.startsWith("_") || name.startsWith("@")
     || name.startsWith("'") || name.startsWith("`") || name.startsWith("‘")
     || name.startsWith("’") || name.startsWith(" ");
};

export const validatePostcode = (country, postcodeArg, countryValidations) => {
    const validation = countryValidations.find((item) => item.country === country);
    if (validation) {
        const { isPostcodeRequired, postcodeRegex, postcodeMinLength, postcodeMaxLength,
            prefix, postcodePreventRepeating, postcodePreventSequential } = validation;

        if (!isPostcodeRequired && !postcodeArg) {
            return null;
        }
        const postcode = postcodeArg ? postcodeArg.toString() : postcodeArg;
        const length = postcode ? postcode.length : 0;

        const isFormatValid = postcodeRegex ? new RegExp(postcodeRegex).test(postcode) : true;
        const isMinLengthValid = postcodeMinLength ? (length >= postcodeMinLength) : true;
        const isMaxLengthValid = postcodeMaxLength ? (length <= postcodeMaxLength) : true;

        const pattern = postcode ? getPattern(postcode) : undefined;
        const passedRepeatingCheck = pattern && postcodePreventRepeating ? !pattern.repeating : true;
        const passedSequentialCheck = pattern && postcodePreventSequential ? !pattern.sequential : true;

        let tempPostCode = typeof prefix !== "undefined" && !Helpers.isNumber(postcode) && postcode !== null ? postcode.replace(prefix, "") : postcode;
        tempPostCode = tempPostCode ? tempPostCode.replace(/-/, "") : ""; // make sure dash is not included on the Zero check
        const passedZeroCheck = Helpers.isNumber(tempPostCode) && !prefix ? parseInt(tempPostCode) > 0 : true; // if postcode has a prefix, accept all zero input (00000..) else don't

        if (!isFormatValid || !isMinLengthValid || !isMaxLengthValid || !passedRepeatingCheck || !passedSequentialCheck || !passedZeroCheck) {
            return { fieldName: "postcode", message: "invalid.postcode" };
        }
    }
    return null;
};

export const validateResidence = (address) => {
    let error = null;
    if (doesAddressContainProhibitedWords(address)) {
        error = { fieldName: "address1", message: "address.contains.invalid.words" };
    } else if (!/[0-9]/g.test(address)) {
        error = { fieldName: "address1", message: "address.contains.at.least.number" };
    } else if (!/[a-zA-Z]/g.test(address)) {
        error = { fieldName: "address1", message: "address.contains.street.details" };
    } else if (hasSequentialSpecialCharacter(address)) {
        error = { fieldName: "address1", message: "address.contains.repeating.specialcharacter" };
    }
    return error;
};

export const validateAgentDetails = (dispatch) => {
    const { agentId, agentSequence } = Helpers.getAgentDetails();
    if (!agentId || !agentSequence) {
        const description = "service.unavailable.description.no.agent.id";
        const children = <ServiceUnavailable description={description} />;
        dispatch({ type: SHOW_INTERVENTION, children });
    }
};

export const onContinue = validate;



