import { keyboardReducer as keyboard } from "common-components";

import combineReducers from "./combineReducers";

import customer from "./customer";
import internationalization from "./internationalization";
import codeTable from "./codeTable";
import config from "./config";
import validation from "./validation";
import ui from "./ui";
import help from "./help";
import checkCashing from "./checkCashing";
import login from "./login";
import terms from "./terms";
import resetPassword from "./resetPassword";
import moneygramMessages from "./moneygramMessages";
import { commonTownsReducer as commonTowns, otherTownsReducer as otherTowns } from "./towns";

const rootReducer = combineReducers({
    customer,
    internationalization,
    codeTable,
    config,
    login,
    resetPassword,
    validation,
    ui,
    keyboard,
    help,
    checkCashing,
    terms,
    moneygramMessages,
    commonTowns,
    otherTowns,
});

export default rootReducer;



