import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import { initiateHalfRegisterCustomer, initiateSendMoney } from "../actions";
import { P } from "../components/HTML";
import Card from "../components/Card";

const mapDispatchToProps = (dispatch) => {
    return {
        onNewUser: () => dispatch(initiateHalfRegisterCustomer()),
        onSendMoney: () => dispatch(initiateSendMoney()),
    };
};

const UserCheck = ({ onNewUser, onSendMoney }) => (
  <div>
    <img className="page-background"
      src="resources/images/background-user-check.png"
      alt="User check page background" />

    <div className="user-check-page">
      <P className="title">welcome.header</P>
      <P className="subtitle">user.check.subtitle</P>

      <Card to="/sendMoney" onClick={onSendMoney} data-analytics="Existing user">existing.user.btn</Card>
      <Card to="/registerCustomer" onClick={onNewUser} data-analytics="New user">new.user.btn</Card>
    </div>
  </div>
);

UserCheck.propTypes = {
    onNewUser: PropTypes.func.isRequired,
    onSendMoney: PropTypes.func.isRequired,
};

export default connect(null, mapDispatchToProps)(UserCheck);



