import React from "react";
import PropTypes from "prop-types";

const KeyboardComponent = ({ show, onKeypress, onBackspace, onClear, onEnter,
      allowedLetters, allowedNumbers, allowedSpecialCharacters, allowedTopLevelDomain, australiaOnly, unMapped }) => {
    const getProps = (onClick, className, extraArg) => {
        let shouldDisable = false;

        if (!unMapped) {
            if ((className.indexOf("letter") !== -1 || className.indexOf("spacebar") !== -1) && !allowedLetters) {
                shouldDisable = true;
            } else if (className.indexOf("number") !== -1 && !allowedNumbers) {
                shouldDisable = true;
            } else if (className.indexOf("string") !== -1 && !allowedTopLevelDomain) {
                shouldDisable = true;
            } else if (className.indexOf("special-character") !== -1
                    && (!allowedSpecialCharacters || allowedSpecialCharacters.indexOf(extraArg) === -1)) {
                shouldDisable = true;
            }
        }

        return {
            className: shouldDisable ? `${className} disabled` : className,
            onClick: shouldDisable ? null : onClick,
            tabIndex: 0,
        };
    };

    const className = show ? "keyboard" : "keyboard hidden";
    const spacebarClassName = `spacebar ${australiaOnly ? "has-au-tld" : ""}`;

    const australiaTopLevelDomain = <button {...getProps(() => onKeypress(".COM.AU"), "string ccTLD")}>.COM.AU</button>;
    const topLevelDomains = (<div>
        <button {...getProps(() => onKeypress(".COM"), "string TLD")}>.COM</button>
        {australiaOnly ? australiaTopLevelDomain : null}
    </div>);

    return (
        <div className={className}>
            <div className="keyboard-wrapper">
                <div className="keyboard-row">
                    <button {...getProps(() => onKeypress("Q"), "letter")}>Q</button>
                    <button {...getProps(() => onKeypress("W"), "letter")}>W</button>
                    <button {...getProps(() => onKeypress("E"), "letter")}>E</button>
                    <button {...getProps(() => onKeypress("R"), "letter")}>R</button>
                    <button {...getProps(() => onKeypress("T"), "letter")}>T</button>
                    <button {...getProps(() => onKeypress("Y"), "letter")}>Y</button>
                    <button {...getProps(() => onKeypress("U"), "letter")}>U</button>
                    <button {...getProps(() => onKeypress("I"), "letter")}>I</button>
                    <button {...getProps(() => onKeypress("O"), "letter")}>O</button>
                    <button {...getProps(() => onKeypress("P"), "letter")}>P</button>
                    <button {...getProps(onBackspace, "action backspace")} />
                    <button {...getProps(() => onKeypress("1"), "number")}>1</button>
                    <button {...getProps(() => onKeypress("2"), "number")}>2</button>
                    <button {...getProps(() => onKeypress("3"), "number last")}>3</button>
                </div>

                <div className="keyboard-row">
                    <button {...getProps(() => onKeypress("A"), "letter a")}>A</button>
                    <button {...getProps(() => onKeypress("S"), "letter")}>S</button>
                    <button {...getProps(() => onKeypress("D"), "letter")}>D</button>
                    <button {...getProps(() => onKeypress("F"), "letter")}>F</button>
                    <button {...getProps(() => onKeypress("G"), "letter")}>G</button>
                    <button {...getProps(() => onKeypress("H"), "letter")}>H</button>
                    <button {...getProps(() => onKeypress("J"), "letter")}>J</button>
                    <button {...getProps(() => onKeypress("K"), "letter")}>K</button>
                    <button {...getProps(() => onKeypress("L"), "letter")}>L</button>
                    <button {...getProps(onEnter, "action enter")} data-analytics="Enter">Enter</button>
                    <button {...getProps(() => onKeypress("4"), "number")}>4</button>
                    <button {...getProps(() => onKeypress("5"), "number")}>5</button>
                    <button {...getProps(() => onKeypress("6"), "number last")}>6</button>
                </div>

                <div className="keyboard-row">
                    <button {...getProps(() => onKeypress("Z"), "letter")}>Z</button>
                    <button {...getProps(() => onKeypress("X"), "letter")}>X</button>
                    <button {...getProps(() => onKeypress("C"), "letter")}>C</button>
                    <button {...getProps(() => onKeypress("V"), "letter")}>V</button>
                    <button {...getProps(() => onKeypress("B"), "letter")}>B</button>
                    <button {...getProps(() => onKeypress("N"), "letter")}>N</button>
                    <button {...getProps(() => onKeypress("M"), "letter")}>M</button>
                    <button {...getProps(() => onKeypress(","), "special-character", ",")}>,</button>
                    <button {...getProps(() => onKeypress("."), "special-character", ".")}>.</button>
                    <button {...getProps(() => onKeypress("/"), "special-character", "/")}>&#47;</button>
                    <button {...getProps(() => onKeypress("'"), "special-character", "'")}>&#39;</button>
                    <button {...getProps(() => onKeypress("-"), "special-character", "-")}>-</button>
                    <button {...getProps(() => onKeypress("7"), "number")}>7</button>
                    <button {...getProps(() => onKeypress("8"), "number")}>8</button>
                    <button {...getProps(() => onKeypress("9"), "number last")}>9</button>
                </div>

                <div className="keyboard-row">
                    <button {...getProps(onClear, "action clear")}>Clear</button>
                    <button {...getProps(() => onKeypress(" "), spacebarClassName)} />
                    {topLevelDomains}
                    <button {...getProps(() => onKeypress("_"), "special-character", "_")}>_</button>
                    <button {...getProps(() => onKeypress("@"), "special-character", "@")}>@</button>
                    <button {...getProps(() => onKeypress("0"), "number zero last")}>0</button>
                </div>
            </div>
        </div>
    );
};

KeyboardComponent.propTypes = {
    onKeypress: PropTypes.func.isRequired,
    onBackspace: PropTypes.func.isRequired,
    onClear: PropTypes.func.isRequired,
    onEnter: PropTypes.func.isRequired,
    allowedSpecialCharacters: PropTypes.string,
    allowedTopLevelDomain: PropTypes.bool,
    allowedNumbers: PropTypes.bool,
    allowedLetters: PropTypes.bool,
    show: PropTypes.bool,
};

KeyboardComponent.defaultProps = {
    allowedSpecialCharacters: null,
    allowedTopLevelDomain: true,
    allowedNumbers: true,
    allowedLetters: true,
    show: false,
};

export default KeyboardComponent;



