/* eslint jsx-a11y/no-noninteractive-element-interactions: 0 */
import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Div, Span } from "common-components";
import Checkbox from "../components/Checkbox";
import { TERMS_SHOW, TERMS_TOGGLE } from "../actions/terms";
import { CLEAR_VALIDATION_ERRORS } from "../actions/validation";

const TermsLink = ({ onAgreement, termsErrorMessage, openTerms, toggleTerms }) => {
    const checkImagePath = onAgreement ? "checkbox-on-large.png" : "checkbox-off-large.png";
    return (<Div className="terms-and-condition" data-analytics="Terms agreement">
        <Checkbox name="termsAgreementCB"
          label={<Span><Span>terms.agreement.prefix</Span> <Link to="/terms" onClick={openTerms}><Span className="bold-link">terms.agreement</Span></Link></Span>}
          checked={onAgreement}
          onChange={toggleTerms} />
        <Span className="terms-and-condition-error">{termsErrorMessage}</Span>
    </Div>);
};

const mapStateToProps = (state) => {
    const { onAgreement } = state.terms;
    const errors = state.ui && state.ui.errors ? state.ui.errors : [];
    const termsError = errors.find((item) => item.fieldName === "terms");

    return {
        onAgreement,
        termsErrorMessage: termsError ? termsError.message : null,
    };
};

const openTerms = (onReturnRoute, steps, currentStep) => (dispatch) => {
    dispatch({ type: CLEAR_VALIDATION_ERRORS });
    dispatch({
        type: TERMS_SHOW,
        previousRoute: onReturnRoute,
        currentStep,
        steps,
    });
};

const toggleTerms = (dispatch) => {
    dispatch({ type: CLEAR_VALIDATION_ERRORS });
    dispatch({ type: TERMS_TOGGLE });
};

const mapDispatchToProps = (dispatch, ownProps) => {
    const { onReturnRoute, steps, currentStep } = ownProps;
    return {
        openTerms: () => dispatch(openTerms(onReturnRoute, steps, currentStep)),
        toggleTerms: () => dispatch(toggleTerms),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(TermsLink);



