import React from "react";
import { connect } from "react-redux";
import { Intl } from "common-functions";
import { Label, P } from "./HTML";

const options = (items, translation) => {
    const list = [];

    if (items) {
        items.map((item, index) => {
            const displayName = Intl.translate(translation, item.name);
            const key = `${index}-${item.value}`;
            return list.push(<option key={key} value={item.value}>{displayName}</option>);
        });
    }

    return list;
};

class Select extends React.Component {

    constructor(props) {
        super(props);
        this.state = { size: 0 };
    }

    componentDidMount() {
        this.callOnChangeOnDefaultItem();
        if (this.props.items) {
            this.initiallySetSelectedItem();
        }
    }

    componentDidUpdate(prevProps, prevState) {
        this.callOnChangeOnDefaultItem();
        if (this.didItemsChange(this.props.items, prevProps.items)) {
        // console.log("PREV", JSON.stringify(prevProps.items))
        // console.log("CURRENT", JSON.stringify(this.props.items))
        // if (JSON.stringify(this.props.items) != JSON.stringify(prevProps.items)) {
            this.setState({ selectedIndex: 0 });
        } else if (this.props.hasIdenticalValues && !isNaN(this.state.selectedIndex)) {
            this.select.selectedIndex = this.state.selectedIndex;
        }
    }

    didItemsChange(currItems, prevItems) {
        if (!prevItems)
            return true;

        if (currItems.length != prevItems.length)
            return true;

        let itemsEqual = true;
        const l = currItems.length;
        for (let i = 0; i < l; i++) {
            if (`${currItems[i].name}-${currItems[i].value}` !== `${prevItems[i].name}-${prevItems[i].value}`) {
                itemsEqual = false;
                break;
            }
        }
        return !itemsEqual;
    }

    onChange(event, index) {
        this.props.onChange(event);
        const selectedIndex = typeof index !== "undefined" ? index : this.select.selectedIndex;
        this.setState({ selectedIndex });
    }

    callOnChangeOnDefaultItem() {
        const { value, defaultValue, name, items } = this.props;

        const getEventObject = (index) => {
            return {
                target: { name, value: items[index].value },
                type: "defaultChange"
            };
        }

        if (defaultValue && !value && items && items.length > 0) {
            for (let i = 0; items.length > i; i += 1) {
                if (defaultValue === items[i].value) {
                    this.onChange(getEventObject(i), i);
                }
            }
        } else if (!value && items && items.length > 0) {
            this.onChange(getEventObject(0), 0);
        }
    }

    initiallySetSelectedItem() {
        const value = this.props.value || this.props.defaultValue;
        if (value && this.select) {
            this.select.value = value;
        }
        this.hasInitiallySetSelectedItem = true;
    }

    render() {
        const { id, name, items, label, translation, showErrorMessage,
          errorMessage, customErrorMessage, defaultErrorMessage } = this.props;

        const properties = Object.assign({}, this.props);
        delete properties.items;
        delete properties.translation;
        delete properties.dispatch;
        delete properties.maxDisplayedItem;
        delete properties.labelComponent;
        delete properties.defaultValue;
        delete properties.parentClassName;
        delete properties.hasIdenticalValues;
        delete properties.showErrorMessage;
        delete properties.defaultErrorMessage;
        delete properties.defaultLabelValue;
        delete properties.errorMessage;

        const messageClass = `message error`;

        properties.onChange = (event) => {
            this.onChange(event);
        };

        if (!properties.value) {
            delete properties.value;
        }

        const labelComponent = label ? <Label htmlFor={id || name} defaultValue={this.props.defaultLabelValue}>{label}</Label> : this.props.labelComponent;
        let parentClassName = this.props.parentClassName ? `pull-left select-wrapper ${this.props.parentClassName}` : "pull-left select-wrapper";
        parentClassName = properties.disabled ? `${parentClassName} disabled` : parentClassName;
        return (<div className={parentClassName}>
            {labelComponent}
            <select {...properties}
              ref={(select) => (this.select = select)}>
                {options(items, translation)}
            </select>
            {showErrorMessage ? <P className={messageClass} id={`${id || name}-error`} defaultValue={defaultErrorMessage}>
                {errorMessage || customErrorMessage}
            </P> : null}
        </div>);
    }
}

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.id || ownProps.name;
    let error = null;
    if (state.ui && !ownProps.hideErrorMessage) {
        error = state.ui.errors.find((item) => item.fieldName === id);
    }
    return {
        errorMessage: error ? error.message : null,
        defaultErrorMessage: error ? error.defaultMessage: null,
        translation: state.internationalization.translation,
    };
};

export default connect(mapStateToProps)(Select);



