import React from "react";
import { connect } from "react-redux";
import { push } from "connected-react-router";
import CCGIntl from "../../common/CCGIntl";

import { PREV_PAGE, INITIATE_CHECK_CASHING, SCAN_CHECK_COMPLETE, HIDE_KEYBOARD, ENDED_VIDEO } from "../../actions/constants";
import { Div, P, Span, Button } from "../../components/HTML";
import { getMaxAmount } from "../../reducers/checkCashing";

import Navigation from "../../components/Navigation";

class ScanCheckPage extends React.Component {
    constructor() {
        super();
        this.state = {
            animatedImageIndex: -1,
        }
        this.animationTimer = null;
    }
    componentDidMount() {
        setTimeout(() => this.setState({ animatedImageIndex: 0 }), 100);
        this.animationTimer = setInterval(() => {
            const { animatedImageIndex } = this.state;
            this.setState({
                animatedImageIndex: (animatedImageIndex + 1) % 4,
            });
        }, 2300)
    }
    componentWillUnmount() {
        clearInterval(this.animationTimer);
    }
    renderInstructions() {
        const { animatedImageIndex } = this.state;
        const imagesCount = 4;
        const components = [];
        for (let indexer = 0; indexer < imagesCount; indexer++) {
            components.push(
                <Div className={`image-container ${animatedImageIndex === indexer ? "highlighted" : ""}`} key={`image-${indexer}`}>
                    <Span className="badge">{indexer + 1}</Span>
                    <img className="image" alt="dummy" src={`resources/images/instructions/check-image-${indexer + 1}.jpg`} />
                </Div>
            );
        }
        return components;
    }
    render() {
        const { onLoad, onNext, onPrevious, hasEndedVideo, onEndVideo, useCheckScanner, checkCashingDefaults,
              onRetry, lang, moneygramMessages, messageArgs } = this.props;
        return (<Div>
            <Div className="review-page customer-review-page">
                <P className="mo-review-subtitle">insert.check.to.scanner</P>
                <Div className="scan-check-page">
                    <Div className="pull-left images">
                        {this.renderInstructions()}
                    </Div>
                </Div>
            </Div>

            <Navigation onNext={() => onNext(useCheckScanner, checkCashingDefaults)} hidePrevious
              hideNext={useCheckScanner}
              hasFormatErrors={(!hasEndedVideo || useCheckScanner)} hasInput={(!hasEndedVideo || useCheckScanner)} />

            <img alt="dummy" src="resources/images/check.png" style={{ display: "none" }} onLoad={() => onLoad(moneygramMessages, messageArgs)} />

            <Div style={{ display: "none" }} id="retryIntervention" className="restricted-access page-background-check retry-intervention">
                <Div className="restricted-access-page">
                    <P className="title" id="retryInterventionMessage"></P>

                    <Div className="scan-check-page">
                        <Div className="pull-left images">
                            {this.renderInstructions()}
                        </Div>
                    </Div>

                    <Div className="buttons">
                        <Button className="error-scan-btn" onClick={onRetry} data-analytics="Try again">reset.password.page.try.again</Button>
                        <Span style={{ padding: "10px", display: "inline-block" }}></Span>
                        <Button className="close-btn" to="/" data-analytics="Exit">exit.btn</Button>
                    </Div>
                </Div>
            </Div>
        </Div>);
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onLoad: (moneygramMessages, messageArgs) => {
            dispatch({ type: HIDE_KEYBOARD });
            dispatch({ type: ENDED_VIDEO, hasEndedVideo2: false });
            window.open(`${window.location.href}?scanCheckStart`, "_self");
            const scanCheckComplete = (params) => {
                const { checkNumber, checkImageFilename, checkReaderRefNumber } = params;
                dispatch({ type: SCAN_CHECK_COMPLETE, checkNumber, checkImageFilename, checkReaderRefNumber, checkType: "P", dispatch });
                let url = `${window.location.href}`;
                url = url.replace("?scanCheckStart", "");
                window.open(url, "_self");
            };
            const scanCheckError = (message) => {
                let url = `${window.location.href}`;
                url = url.replace("?scanCheckStart", "");
                window.open(url, "_self");
                document.getElementById("retryInterventionMessage").innerHTML = CCGIntl.messages(moneygramMessages, message, messageArgs);
                document.getElementById("retryIntervention").style.display = "";
            };
            window.scanCheckComplete = scanCheckComplete;
            window.scanCheckError = scanCheckError;
        },
        onNext: (useCheckScanner, checkCashingDefaults) => {
            if (useCheckScanner) {
                if (String(useCheckScanner) === "prompt") {
                    const checkNumber = prompt("checkNumber") || checkCashingDefaults.checkNumber;
                    const checkImageFilename = prompt("checkImageFilename") || checkCashingDefaults.checkImageFilename;
                    const checkReaderRefNumber = prompt("checkReaderRefNumber") || checkCashingDefaults.checkReaderRefNumber;
                    const checkType = checkCashingDefaults.checkType;
                    dispatch({ type: SCAN_CHECK_COMPLETE, checkNumber, checkImageFilename, checkReaderRefNumber, checkType, dispatch });
                }
            } else {
                const { checkNumber, checkImageFilename, checkReaderRefNumber, checkType } = checkCashingDefaults;
                dispatch({ type: SCAN_CHECK_COMPLETE, checkNumber, checkImageFilename, checkReaderRefNumber, checkType, dispatch });
            }
        },
        onPrevious: () => {
            dispatch({ type: PREV_PAGE });
        },
        onCancel: () => {
            dispatch({ type: INITIATE_CHECK_CASHING });
            dispatch(push("/"));
        },
        onEndVideo: () => {
            dispatch({ type: ENDED_VIDEO, hasEndedVideo2: true });
        },
        onRetry: () => {
            window.open(`${window.location.href}?scanCheckStart`, "_self");
            document.getElementById("retryIntervention").style.display = "none";
        },
    };
};

const mapStateToProps = (state) => {
    return {
        showAmountError: state.checkCashing.showAmountError,
        hasEndedVideo: state.checkCashing.hasEndedVideo2,
        hasValidDate: state.checkCashing.hasValidDate,
        voucher: state.checkCashing.voucher,
        checkDate: state.checkCashing.checkDate,
        useCheckScanner: state.config.useCheckScanner,
        checkCashingDefaults: state.config.checkCashingDefaults,
        translation: state.internationalization.translation,
        moneygramMessages: state.moneygramMessages.messages,
        messageArgs: state.config.messageArgs,
        lang: state.internationalization.language || "en",
    };
};

const ScanCheck2 = connect(mapStateToProps, mapDispatchToProps)(ScanCheckPage);
export default ScanCheck2;



