import React from "react";
import PropTypes from "prop-types";
import { Span } from "./HTML";

const Steps = ({ items, currentStep }) => {
    const stepsComponent = items.map((item, index) => {
        const isString = typeof currentStep === "string";
        const isCurrentStep = isString ? item === currentStep : index === currentStep;
        const className = isCurrentStep ? "current step" : "step";

        return (<div key={item} className={className}>
          <Span className="index">{index + 1}</Span>
          <Span className="label">{item}</Span>
        </div>);
    });

    return (<div className="steps">
      {stepsComponent}
      <div className="clearfix" />
      <br />
    </div>);
};

Steps.propTypes = {
    items: PropTypes.arrayOf(PropTypes.string).isRequired,
    currentStep: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
    ]).isRequired,
};

export default Steps;



