import React from "react";

class ViewComponent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    getState() {
        return this.state;
    }

    handleChange(event) {
        const nextState = {};
        nextState[event.target.name] = event.target.value;
        this.setState(nextState);
    }

    isValid() {
        return true || this;
    }

}

export default ViewComponent;



