import React from "react";
import { connect } from "react-redux";
import { Div } from "../../components/HTML";

import { validate } from "../../actions/validation";
import { RESET_PASSWORD_NEXT_PAGE, RESET_PASSWORD_PREV_PAGE } from "../../actions/resetPassword";

const ResetPassword = ({ pages, currentIndex }) => {
    const page = pages[currentIndex];
    return (<Div>
        {page.component}
    </Div>);
};

const mapStateToProps = (state) => {
    return {
        pages: state.resetPassword.pages,
        currentIndex: state.resetPassword.currentIndex,
        steps: state.resetPassword.steps,
    };
};

const onContinue = (dispatch, getState) => {
    const resetPassword = getState().resetPassword;
    const { pages, currentIndex } = resetPassword;
    const currentPage = pages[currentIndex];
    const additionalValidation = currentPage.validate;

    if (additionalValidation) {
        additionalValidation(dispatch, getState);
    } else {
        dispatch({ type: RESET_PASSWORD_NEXT_PAGE });
    }
};

const mapDispatchToProps = (dispatch) => {
    const ownProps = {
        onContinue: () => dispatch(validate(onContinue)),
        onPrevious: () => dispatch({ type: RESET_PASSWORD_PREV_PAGE }),
    };
    return ownProps;
};

export default connect(mapStateToProps, mapDispatchToProps)(ResetPassword);



