import { HttpUtils, Helpers, Logger, Rest, RestHost } from "common-functions";

export const prepareCheckCashingVoucher = (checkCashing, successCallback, errorCallback) => {
    const voucher = Object.assign({}, checkCashing.voucher);
    voucher.agentDetails = Helpers.getAgentDetails();
    voucher.customerId = checkCashing.customerId;
    const { checkDD, checkMM, checkYY } = checkCashing.checkDate;
    voucher.checkDate = `20${checkYY}-${checkMM}-${checkDD}`;
    // voucher.totalReceiveAmount = (voucher.receiveAmount / 1) + (voucher.cardAmount / 1) - (voucher.fee / 1);
    delete voucher.receiveAmount;
    delete voucher.cardAmount;

    voucher.micrLine = voucher.micrLine || String(voucher.checkNumber);
    voucher.checkNumber = voucher.micrLine === voucher.checkNumber ? extractCheckNumber(voucher.micrLine).checkNumber : voucher.checkNumber;

    const requestData = {
        prepareCheckCashingVoucherRequest: voucher,
    };

    Logger.log(`preparing check cashing voucher.. ${JSON.stringify(requestData)}`);

    Rest.send(RestHost.POST, "payments/prepareCheckCashingVoucher", requestData, null,
        successCallback, errorCallback, RestHost.JSON);
};

export const printCheckCashingVoucher = ({ response, cardAmount, receiveAmount, config }) => {
    const voucher = response.prepareCheckCashingVoucherResponse.voucher;
    if (voucher) {
        const formatAmount = (amount) => Number(amount)
            .toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 });
        const checkCashingVchrReceiveAmount = formatAmount(receiveAmount || 0);
        const checkCashingVchrCardAmount = formatAmount(cardAmount || 0);

        const { productCode, voucherDisplayDateFormat } = config.defaultValues;

        const dateTime = voucher.voucherDateTimeInStoreTimeZone
            ? Helpers.parseStringDateResponse(voucher.voucherDateTimeInStoreTimeZone) : Helpers.parseDateResponse(voucher.voucherDateTime);
        const expiryDateTime = voucher.voucherExpiryDateTimeInStoreTimeZone
            ? Helpers.parseStringDateResponse(voucher.voucherExpiryDateTimeInStoreTimeZone) : Helpers.parseDateResponse(voucher.voucherExpiryDateTime);
        const voucherExpiryDateTime = Helpers.formatDate(expiryDateTime, voucherDisplayDateFormat);
        const voucherDateTime = Helpers.formatDate(dateTime, voucherDisplayDateFormat);

        let urlParam = "?print-check-cashing-voucher=true";
        const barCode = `${productCode}${voucher.voucherReferenceNumber}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingProductBarcode", barCode)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrDateTime", voucherDateTime)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrExpiry", voucherExpiryDateTime)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrRefno", voucher.voucherReferenceNumber)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrCurr", voucher.currency)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrCheckAmount", voucher.checkAmount)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrFee", voucher.fee)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrReceiveAmount", checkCashingVchrReceiveAmount)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrCardAmount", checkCashingVchrCardAmount)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrTotalReceiveAmount", voucher.totalReceiveAmount)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrPhotoIDType", voucher.customersPhotoIdentificationType)}`;
        urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("checkCashingVchrCustName", voucher.customersName)}`;
        window.open(window.location.href + urlParam, "_self");
    }
};

export const isCheckAvailableForCollection = (checkCashing, successCallback, errorCallback) => {
    const voucher = Object.assign({}, checkCashing.voucher);

    const requestData = {
        isCheckAvailableForCollectionRequest: {
            agentDetails: Helpers.getAgentDetails(),
            customerId: checkCashing.customerId,
            checkNumber: extractCheckNumber(String(voucher.checkNumber)).checkNumber,
            micrLine: voucher.checkNumber,
        },
    };

    Logger.log(`isCheckAvailableForCollection.. ${JSON.stringify(requestData)}`);

    Rest.send(RestHost.POST, "payments/isCheckAvailableForCollection", requestData, null,
        successCallback, errorCallback, RestHost.JSON);
};

export const extractCheckNumber = (micrLine) => {
    let checkNumber = micrLine;
    const start = micrLine.indexOf("O");
    if (start >= 0) {
        let end = micrLine.indexOf("O", start + 1);
        if (end < start) {
            end = micrLine.length;
        }
        checkNumber = micrLine.substring(start + 1, end);
    } else if (checkNumber === "T123456780 A5556667773100 C3100") {
        checkNumber = "3100";
    }
    return {
        checkNumber,
        extractSuccess: checkNumber !== micrLine,
    };
};



