import React from "react";
import PropTypes from "prop-types";
import { Label, Div } from "./HTML";

const Checkbox = (props) => {
    const inputProps = Object.assign({}, props, {
        className: `cbx ${props.className}`,
    });

    const label = <Label htmlFor={props.name} className="cbx-label">{inputProps.label}</Label>;
    const checkboxLabel = inputProps.label ? label : null;
    delete inputProps.label;

    const onClick = () => {
        document.getElementById(props.name).click();
    };

    return (<Div>
      <Div className="cb-wrapper"><Div className="cb-clickable-area" onClick={onClick}></Div></Div>
      <input id={props.name} {...inputProps} type="checkbox" />
      {checkboxLabel}
    </Div>);
};

Checkbox.propTypes = {
    className: PropTypes.string,
};

Checkbox.defaultProps = {
    className: "",
};

export default Checkbox;



