import { HELP_REQUEST, HELP_FAQS_SUCCESS,
  HELP_ABOUT_US_SUCCESS, HELP_COUNTRY_RESTRICTIONS_SUCCESS,
  HELP_FAQS_FAILURE, HELP_COUNTRY_RESTRICTIONS_FAILURE,
  HELP_ABOUT_US_FAILURE, HELP_FAQS_SELECT, HELP_FAQS_CATEGORY_SELECT,
  HELP_ABOUT_US_SELECT, HELP_COUNTRY_RESTRICTIONS_SELECT,
  HELP_COUNTRY_RESTRICTIONS_UNSELECT,
} from "../actions/help";

const defaultState = {
    faqsData: {},
    aboutUsData: {},
    countriesRestriction: {},
    hasCountrySelected: false,
    selectedFaq: null,
    isFetching: false,
};

const helpReducer = (state, action) => {
    const help = state.help || defaultState;

    switch (action.type) {
        case HELP_REQUEST:
            return Object.assign({}, { isFetching: true });

        case HELP_FAQS_SUCCESS:
            return Object.assign({}, help, {
                faqsData: action.data,
                selectedFaq: null,
            });

        case HELP_ABOUT_US_SUCCESS:
            return Object.assign({}, help, {
                aboutUsData: action.data,
            });

        case HELP_COUNTRY_RESTRICTIONS_SUCCESS:
            return Object.assign({}, help, {
                countriesRestriction: { countries: action.data },
                hasCountrySelected: false,
            });

        case HELP_FAQS_FAILURE:
            return Object.assign({}, help, {
                faqsData: {},
                selectedFaq: null,
            });

        case HELP_ABOUT_US_FAILURE:
            return Object.assign({}, help, {
                aboutUsData: {},
            });

        case HELP_COUNTRY_RESTRICTIONS_FAILURE:
            return Object.assign({}, help, {
                countriesRestriction: {},
                hasCountrySelected: false,
            });

        case HELP_FAQS_SELECT:
            return Object.assign({}, help, {
                faqsData: action.data,
            });

        case HELP_FAQS_CATEGORY_SELECT:
            return Object.assign({}, help, {
                faqsData: action.data,
                selectedFaq: action.selectedFaq,
            });

        case HELP_ABOUT_US_SELECT:
            return Object.assign({}, help, {
                aboutUsData: action.data,
            });

        case HELP_COUNTRY_RESTRICTIONS_SELECT:
            return Object.assign({}, help, {
                countriesRestriction: action.data,
                hasCountrySelected: true,
            });

        case HELP_COUNTRY_RESTRICTIONS_UNSELECT:
            return Object.assign({}, help, {
                countriesRestriction: action.data,
                hasCountrySelected: false,
            });

        default:
            return help;
    }
};

export default helpReducer;



