import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Intl, Helpers, Validation } from "common-functions";

import { FIELD_FOCUS } from "../actions/keyboardAction";
import { Label, P } from "./HTML";
import Input from "./Input";

const getUpdatedState = (unmaskedPassword) => {
    const nextState = {};
    if (unmaskedPassword) {
        nextState.maskedPassword = Array(unmaskedPassword.length + 1).join("*");
        nextState.unmaskedPassword = unmaskedPassword;
    }
    return nextState;
};

class PasswordInput extends React.Component {

    constructor(props) {
        super(props);
        this.state = getUpdatedState(this.props ? this.props.value : null);
        this.hasPlaceholderIssue = Helpers.hasPlaceholderIssue();
        this.handleInput = (event) => this.onChange(event);
    }

    componentDidMount() {
        this.bindKeyboardEvent();

        if (this.props.shouldAutoFocus) {
            this.input.focus();
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.sessionReplaying) {
            if (nextProps.value) {
                const maskedPassword = Array(nextProps.value.length + 1).join("*");
                this.setState({ maskedPassword });
            }
        }
    }

    componentDidUpdate() {
        this.bindKeyboardEvent();
    }

    onChange(event) {
        const name = event.target.name;
        const value = event.target.value;

        if (this.maskingInterval != null) {
            clearInterval(this.maskingInterval);
        }

        let typedKey = value.replace(/\*/g, "");
        typedKey = typedKey.length > 1 ? typedKey.slice(-1) : typedKey;

        const oldPassword = this.state.unmaskedPassword;
        let newPassword = null;

        const oldMaskedPassword = this.state.maskedPassword;
        let newMaskedPassword = null;

        if (typedKey) {
            newPassword = oldPassword ? oldPassword + typedKey : typedKey;
            newMaskedPassword = oldMaskedPassword ? Array(oldPassword.length + 1).join("*") + typedKey : typedKey;
        } else {
            newPassword = !value ? null : oldPassword.slice(0, -1);
            newMaskedPassword = !value ? null : Array(oldPassword.length).join("*");
        }

        let nextState = {};
        nextState.maskedPassword = newMaskedPassword;
        nextState.unmaskedPassword = newPassword;
        this.setState(nextState);

        this.props.onChange(Object.assign({}, event, {
            target: { name, value: newPassword },
        }));

        if (typedKey) {
            this.maskingInterval = setTimeout(() => {
                clearInterval(this.maskingInterval);

                nextState = {};
                nextState.maskedPassword = Array(value.length + 1).join("*");
                this.setState(nextState);
            }, 500);
        }
    }

    getValue() {
        return this.state.unmaskedPassword;
    }

    bindKeyboardEvent() {
        if (this.input && Helpers.isKioskApp()) {
            this.input.removeEventListener("onkeyclick", this.handleInput);
            this.input.addEventListener("onkeyclick", this.handleInput, false);
        }
    }

    render() {
        const props = this.props;
        if (!Helpers.isKioskApp()) {
            return (<Input {...props} type="password" inputRef={(input) => { this.input = input; }} />);
        }

        const id = props.id || props.name;
        const value = this.state.maskedPassword;

        const label = props.label || props.placeholder;
        const translatedLabel = Intl.translate(props.translation, label);
        const labelComponent = props.label ? <Label htmlFor={id} defaultValue={props.defaultLabelValue}>{props.label}</Label> : props.labelComponent;

        const validationAttributes = Validation.getAsAttributes(props.validation);
        const inputProps = Object.assign({}, props, validationAttributes, {
            onChange: this.handleInput,
            value: value || "",
            id,
        });

        const currentClassName = inputProps.className ? `${inputProps.className} ` : "";
        const additionalClassName = inputProps.selected ? "selected" : "";
        inputProps.className = `${currentClassName}${additionalClassName}`;

        inputProps.onClick = (event) => {
            Helpers.setCaretToEnd(id);
            if (props.onClick) {
                props.onClick(event);
            }
        };

        inputProps.onKeyPress = (event) => {
            if (props.onEnter) {
                const keyCode = event.which || event.keyCode;
                if (keyCode === 13) { // ENTER
                    props.onEnter(event);
                }
            }
            if (props.onKeyPress) {
                props.onKeyPress(event);
            }
        };

        delete inputProps.translation;
        delete inputProps.label;
        delete inputProps.validation;
        delete inputProps.errorMessage;
        delete inputProps.dispatch;
        delete inputProps.shouldAutoFocus;
        delete inputProps.parentClassName;
        delete inputProps.sessionReplaying;
        delete inputProps.onEnter;
        delete inputProps.defaultLabelValue;
        delete inputProps.defaultErrorMessage;

        let placeholderComponent = null;
        const placeholderIntl = Intl.translate(props.translation, props.placeholder);
        if (this.hasPlaceholderIssue && !inputProps.value) {
            placeholderComponent = <P id={`${id}Placeholder`} className="placeholder" onClick={() => this.input.focus()}>{placeholderIntl}</P>;
            delete inputProps.placeholder;
        } else {
            inputProps.placeholder = placeholderIntl;
        }
        const parentClassName = `${props.errorMessage ? "has-error" : ""} pull-left ${props.parentClassName}`;

        return (<div className={parentClassName}>
            {labelComponent}

            <div className="relative">
                <input {...inputProps}
                  data-label={translatedLabel}
                  onFocus={(e) => props.onFocus(this.input || e.target)}
                  ref={(input) => { this.input = input; }} />

                {placeholderComponent}
            </div>

            <div className="clearfix" />
            <P className="message error" defaultValue={props.defaultErrorMessage}>{props.errorMessage}</P>
        </div>);
    }
}

PasswordInput.propTypes = {
    onChange: PropTypes.func.isRequired,
    value: PropTypes.string,
};

PasswordInput.defaultProps = {
    translation: {},
    type: "text",
    value: "",
};

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.id || ownProps.name;
    const i18n = state.internationalization;
    const validations = state.validation || state.validations;
    const validation = validations ? validations.find((item) => item.fieldName === id) : null;
    const error = state.ui ?
      state.ui.errors.find((item) => item.fieldName === id) : null;

    const { keyboard } = state;
    const selected = keyboard && keyboard.selectedField && keyboard.selectedField.id === id;
    const sessionReplaying = state.sessionRecording && state.sessionRecording.playing;

    return {
        validation,
        selected,
        errorMessage: error ? error.message : null,
        defaultErrorMessage: error ? error.defaultMessage: null,
        translation: i18n ? i18n.translation : null,
        sessionReplaying,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onFocus: (selectedField) => dispatch({ type: FIELD_FOCUS, selectedField }),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PasswordInput);



