import React from "react";
import { connect } from "react-redux";
import { push } from "connected-react-router";

import { NEXT_PAGE, PREV_PAGE, INITIATE_CHECK_CASHING, ENDED_VIDEO } from "../../actions/constants";
import { Div, P } from "../../components/HTML";
import { getMaxAmount } from "../../reducers/checkCashing";

import Navigation from "../../components/Navigation";

const ScanCheckPage = ({ onLoad, onNext, onCancel, hasEndedVideo, onEndVideo, useCheckScanner, checkCashingDefaults,
      onRetry, translation, moneygramMessages, messageArgs }) => {
    const MAX_AMOUNT = Number(getMaxAmount()).toLocaleString();
    return (<Div>
        <Div className="review-page customer-review-page">
          <P className="mo-review-subtitle">insert.check.to.scanner</P>
          <Div className="scan-check-page">
              <Div className="pull-left">
                  <video width="720" autoPlay id="scanVideo" onEnded={onEndVideo}>
                      <source src="resources/video/check-scanning-animation-1.mp4" type="video/mp4" />
                  </video>
              </Div>
          </Div>
        </Div>

        <Navigation onNext={() => onNext(useCheckScanner, checkCashingDefaults)} hidePrevious
          hideNext={(!hasEndedVideo)} />
    </Div>);
};

const mapDispatchToProps = (dispatch) => {
    return {
        onNext: () => {
            dispatch({ type: NEXT_PAGE });
        },
        onPrevious: () => {
            dispatch({ type: PREV_PAGE });
        },
        onCancel: () => {
            dispatch({ type: INITIATE_CHECK_CASHING });
            dispatch(push("/"));
        },
        onEndVideo: () => {
            dispatch({ type: ENDED_VIDEO, hasEndedVideo1: true });
        },
    };
};

const mapStateToProps = (state) => {
    return {
        showAmountError: state.checkCashing.showAmountError,
        hasEndedVideo: state.checkCashing.hasEndedVideo1,
        hasValidDate: state.checkCashing.hasValidDate,
        voucher: state.checkCashing.voucher,
        checkDate: state.checkCashing.checkDate,
        useCheckScanner: state.config.useCheckScanner,
        checkCashingDefaults: state.config.checkCashingDefaults,
        translation: state.internationalization.translation,
        moneygramMessages: state.moneygramMessages.messages,
        messageArgs: state.config.messageArgs,
    };
};

const ScanCheck = connect(mapStateToProps, mapDispatchToProps)(ScanCheckPage);
export default ScanCheck;



