import React from "react";
import { connect } from "react-redux";
import { push } from "connected-react-router";
import { P, Button } from "common-components";

const DefaultContent = ({ title, description, onExit, phone }) => (
    <div>
        <P className="title">{title || "service.unavailable.title"}</P> <br />
        <P className="description" args={phone}>{description || "service.unavailable.description"}</P>
        <Button className="small" onClick={onExit} data-analytics="Exit">exit.btn</Button>
    </div>
);

const Loading = () => (
    <div className="loading-box-image">
        <img src="resources/images/spinner.gif" alt="Loading spinner" />
    </div>
);

const ServiceUnavailable = (props) => {
    const { showLoading } = props;
    return showLoading ? <Loading /> : <DefaultContent {...props} />;
};

const mapStateToProps = (state) => {
    const { translation } = state.internationalization;
    return { showLoading: !translation, phone: state.config.defaultValues ? state.config.defaultValues.helpHotline : "1-800-334-3570" };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onExit: () => dispatch(push("/exit")),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ServiceUnavailable);



