import { RestHost } from "common-functions";

export const HELP_REQUEST = "HELP_REQUEST";
export const HELP_FAQS_SUCCESS = "HELP_FAQS_SUCCESS";
export const HELP_ABOUT_US_SUCCESS = "HELP_ABOUT_US_SUCCESS";
export const HELP_COUNTRY_RESTRICTIONS_SUCCESS = "HELP_COUNTRY_RESTRICTIONS_SUCCESS";
export const HELP_FAQS_FAILURE = "HELP_FAQS_FAILURE";
export const HELP_ABOUT_US_FAILURE = "HELP_ABOUT_US_FAILURE";
export const HELP_COUNTRY_RESTRICTIONS_FAILURE = "HELP_COUNTRY_RESTRICTIONS_FAILURE";
export const HELP_FAQS_SELECT = "HELP_FAQS_SELECT";
export const HELP_ABOUT_US_SELECT = "HELP_ABOUT_US_SELECT";
export const HELP_COUNTRY_RESTRICTIONS_SELECT = "HELP_COUNTRY_RESTRICTIONS_SELECT";
export const HELP_COUNTRY_RESTRICTIONS_UNSELECT = "HELP_COUNTRY_RESTRICTIONS_UNSELECT";
export const HELP_FAQS_CATEGORY_SELECT = "HELP_FAQS_CATEGORY_SELECT";

const FAQS = "faq";
const ABOUT_US = "about";
const COUNTRIES = "countries";

const getHelpInfo = (src, successType, failureType, language, successCallback, dispatch) => {
    dispatch({ type: HELP_REQUEST });

    const defaultSuccessCallback = (data) => {
        dispatch({ type: successType, data });
    };

    const restHost = new RestHost("resources/help/", null, null);
    let filename = language || "en";
    filename += `-${src}`;

    restHost.get(`${filename}.json`,
    (response) => {
        if (successCallback) {
            if (typeof successCallback === "function") {
                successCallback(response);
            }
        } else {
            defaultSuccessCallback(response);
        }
    }, (error) => {
        dispatch({ type: failureType, error });
    },
    RestHost.JSON, true);
};

const getHelpInfoSelect = (dispatchType, data, dispatch) => {
    dispatch({ type: dispatchType, data });
};

export const faqsInfo = (language, successCallback, dispatch) => {
    getHelpInfo(FAQS, HELP_FAQS_SUCCESS, HELP_FAQS_FAILURE,
    language, successCallback, dispatch);
};

export const aboutUsInfo = (language, successCallback, dispatch) => {
    getHelpInfo(ABOUT_US, HELP_ABOUT_US_SUCCESS, HELP_ABOUT_US_FAILURE,
    language, successCallback, dispatch);
};

export const countriesRestrictionInfo = (language, successCallback, dispatch) => {
    getHelpInfo(COUNTRIES, HELP_COUNTRY_RESTRICTIONS_SUCCESS, HELP_COUNTRY_RESTRICTIONS_FAILURE,
    language, successCallback, dispatch);
};

export const faqsInfoSelect = (data, dispatch) => {
    getHelpInfoSelect(HELP_FAQS_SELECT, data, dispatch);
};

export const aboutUsInfoSelect = (data, dispatch) => {
    getHelpInfoSelect(HELP_ABOUT_US_SELECT, data, dispatch);
};

export const countriesRestrictionInfoSelect = (data, dispatch) => {
    getHelpInfoSelect(HELP_COUNTRY_RESTRICTIONS_SELECT, data, dispatch);
};

export const countriesRestrictionInfoUnselect = (countriesRestriction, dispatch) => {
    const data = Object.assign([], countriesRestriction);
    delete data.selectedCountryRestriction;
    dispatch({ type: HELP_COUNTRY_RESTRICTIONS_UNSELECT, data });
};



