import { compose, createStore, applyMiddleware } from "redux";
import { persistStore, autoRehydrate } from "redux-persist";
import { createLogger } from "redux-logger";
import thunkMiddleware from "redux-thunk";

import { createHashHistory } from "history";
import { connectRouter, routerMiddleware } from "connected-react-router";

import { loadAll } from "./actions/codeTable";
import { configRequest } from "./actions/config";
import { validationsRequest, validateAgentDetails } from "./actions/validation";
import { languageTranslate } from "./actions/internationalization";
import reducer from "./reducers";

import analyticsMiddleware from "./middlewares/analytics";

const history = createHashHistory();
const loggerMiddleware = createLogger();

history.listen((location, action) => {
    console.log(`Route change. Location: ${location.pathname}, Action: ${action}`);
});

const isProduction = process.env.NODE_ENV === "production";

const persistenceConfig = { whitelist: ["codeTable", "commonTowns"] };
const postRehydrate = (store, onConfigLoad) => {
    const dispatch = store.dispatch;
    const state = store.getState();
    const language = state.internationalization &&
      state.internationalization.languages ? state.internationalization.languages[0] : null;

    configRequest(dispatch, (config) => onConfigLoad({dispatch, config, isProduction}));
    validationsRequest(dispatch, isProduction);
    validateAgentDetails(dispatch);

    if (language && language.value) {
        languageTranslate(language.value, dispatch, true);
    }
};

export const getProductionStore = () => createStore(
    connectRouter(history)(reducer),
    undefined,
    compose(
        applyMiddleware(
            thunkMiddleware,
            routerMiddleware(history),
            analyticsMiddleware,
        ),
        autoRehydrate(),
    ),
);

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

export const getDevelopmentStore = () => createStore(
    connectRouter(history)(reducer),
    undefined,
    composeEnhancers(
        applyMiddleware(
            loggerMiddleware,
            thunkMiddleware,
            routerMiddleware(history),
            analyticsMiddleware,
        ),
        autoRehydrate({ log: true }),
    ),
);

export const getStore = (onConfigLoad) => {
    const store = isProduction ? getProductionStore() : getDevelopmentStore();
    persistStore(store, persistenceConfig, () => postRehydrate(store, onConfigLoad));
    return store;
};

export const getHistory = () => history;



