import React from "react";
import { connect } from "react-redux";
import { Div } from "../../components/HTML";
import Steps from "../../components/Steps";

const CheckCashing = ({ pages, steps, currentIndex }) => {
    const page = pages[currentIndex];
    return (<Div>
        <Steps items={steps} currentStep={page.step} />
        {page.component}
    </Div>);
};

const mapStateToProps = (state) => {
    return {
        pages: state.checkCashing.pages,
        steps: state.checkCashing.steps,
        currentIndex: state.checkCashing.currentIndex,
    };
};

export default connect(mapStateToProps, null)(CheckCashing);



