import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Helpers } from "common-functions";

import CCGIntl from "../common/CCGIntl";

const process = (props) => {
    const propsCopy = Object.assign({}, props);

    delete propsCopy.translation;
    delete propsCopy.dispatch;
    delete propsCopy.args;
    delete propsCopy.to;
    delete propsCopy.prefix;
    delete propsCopy.prefixClassName;
    delete propsCopy.shouldAutoFocus;
    delete propsCopy.defaultPlaceholder;

    if (typeof propsCopy.children === "string") {
        propsCopy.children = CCGIntl.translate(props.translation, propsCopy.children, props.defaultValue, props.args);
    }
    if (typeof propsCopy.placeholder === "string") {
        propsCopy.placeholder = CCGIntl.translate(props.translation, propsCopy.placeholder, props.defaultPlaceholder, props.args);
    }
    return propsCopy;
};

const HTMLDiv = (props) => {
    const propsCopy = process(props);
    return (<div {...propsCopy} />);
};

const HTMLSpan = (props) => {
    const propsCopy = process(props);
    return (<span {...propsCopy} />);
};

const HTMLP = (props) => {
    const propsCopy = process(props);
    return (<p {...propsCopy} />);
};

const HTMLLabel = (props) => {
    const propsCopy = process(props);
    return (<label htmlFor={propsCopy.htmlFor} {...propsCopy} />);
};

const HTMLButton = (props) => {
    const propsCopy = process(props);
    let component = <button {...propsCopy} />;

    if (typeof props.to !== "undefined") {
        component = <Link to={props.to}>{component}</Link>;
    }
    return component;
};

const HTMLInput = (props) => {
    const propsCopy = process(props);
    propsCopy.onClick = (event) => {
        Helpers.setCaretToEnd(props.id);
        if (props.onClick) {
            props.onClick(event);
        }
    };

    let component = <input {...propsCopy} />;

    if (typeof props.prefix !== "undefined") {
        const prefixClick = (event) => {
            if (event.target && event.target.parentElement && event.target.parentElement.children && event.target.parentElement.children[1]) {
                event.target.parentElement.children[1].focus();
            }
        }
        component = <div className={`input-div ${props.className}`} id={`${props.id}-div`}><span className="input-prefix" onClick={prefixClick}>{props.prefix}</span>{component}</div>;
    }
    return component;
};

const mapStateToProps = (state) => {
    const translation = state.internationalization.translation;
    return { translation };
};

const Div = connect(mapStateToProps)(HTMLDiv);
const Span = connect(mapStateToProps)(HTMLSpan);
const P = connect(mapStateToProps)(HTMLP);
const Label = connect(mapStateToProps)(HTMLLabel);
const Button = connect(mapStateToProps)(HTMLButton);
const Input = connect(mapStateToProps)(HTMLInput);

export { Div, Span, P, Label, Button, Input };



