import React from "react";
import { Helpers, Logger } from "common-functions";

import { NEXT_PAGE, PREV_PAGE, FILL_UP_AMOUNT, INITIATE_CHECK_CASHING, GET_MONEY_ORDER_SUCCESS,
    FILL_UP_DATE, FIELD_FOCUS, VALIDATE_DATE, ENABLE_EDIT_MODE, SHOW_MO_TERMS_AND_CONDITIONS,
    SUBMIT_CHECK_CASHING, CHECK_CASHING_ERROR, SHOW_CHECK_CASHING_ERROR, TERMS_AGREEMENT,
    CHECK_CASHING_SUCCESS, ENDED_VIDEO, SAVE_LOGIN_CUSTOMER_ID, UPDATE_ADDITIONAL_ID,
    SUBMIT_UPDATE_ADDITIONAL_ID, SCAN_CHECK_COMPLETE, APPLY_PRODUCT_SETTINGS, CHECK_AVAILABLE_SUCCESS,
    CHECK_AVAILABLE_FAILED, FILL_UP_CHECK_NUMBER, INITIATE_CHECK_NUMBER_INPUT } from "../actions/constants";
import { isCheckAvailableForCollection, prepareCheckCashingVoucher, printCheckCashingVoucher, extractCheckNumber } from "../actions/checkCashing";
import { updateCustomerAdditionalId, isPhotoIdNumberValid } from "../actions/customer";

import { AmountInput, DateInput } from "../containers/checkCashing/FormInput";
import Review from "../containers/checkCashing/Review";
import ScanCheck from "../containers/checkCashing/ScanCheck";
import ScanCheck2 from "../containers/checkCashing/ScanCheck2";
import AdditionalId from "../containers/checkCashing/AdditionalId";
import Animation3 from "../containers/checkCashing/Animation3";
import PrintVoucher from "../containers/checkCashing/PrintVoucher";
import CheckNumberInput from "../containers/checkCashing/CheckNumberInput";

let ALLOWED_PAST_DAYS = 8;
// let REQUIRE_ID_AMOUNT = 3000;
let MAX_AMOUNT = window.localStorage.maxAmount || 300;
let PAYMENT_FEE = 0.99;
let PAYMENT_INCREMENTS = 20;
let MIN_AMOUNT_FOR_CARD = 200;

export const getMaxAmount = () => MAX_AMOUNT;
export const getPaymentFee = () => PAYMENT_FEE;
export const getCardPaymentIncrements = () => PAYMENT_INCREMENTS;
// export const getRequireIdAmount = () => REQUIRE_ID_AMOUNT;
export const getMinAmountForCard = () => MIN_AMOUNT_FOR_CARD;
export const isCardDisabled = () => MAX_AMOUNT === MIN_AMOUNT_FOR_CARD;

const pages = [
    { step: 0, title: "ScanCheck", component: <AdditionalId /> },
    { step: 0, title: "ScanCheck", component: <ScanCheck2 /> },
    { step: 1, title: "FormInput", component: <AmountInput /> },
    { step: 1, title: "FormInput", component: <DateInput /> },
    { step: 2, title: "Review", component: <Review /> },
    { step: 3, title: "PrintVoucher", component: <PrintVoucher /> },
    { step: 3, title: "PrintVoucher", component: <Animation3 /> },
    { step: 3, title: "PrintVoucher", component: <CheckNumberInput /> },
];

const steps = ["check.cashing.process.step.1",
    "check.cashing.process.step.2",
    "check.cashing.process.step.3",
    "check.cashing.process.step.4"];

const defaultState = {
    voucher: {
        agentDetails: {
            agentId: null,
            agentSequence: null,
        },
        productCode: null,
        checkAmount: null,
        checkDate: null,
        currency: null,
        fee: null,
        receiveAmount: null,
        cardAmount: null,
        checkImageFilename: null,
        checkReaderRefNumber: null,
        checkNumber: null,
        checkType: null,
    },
    checkDate: {
        checkDD: "",
        checkMM: "",
        checkYY: "",
    },
    pages,
    steps,
    customerId: null,
    currentIndex: 0,
    reviewEditMode: false,
    hasEndedVideo1: false,
    hasEndedVideo2: false,
    updateCustomerData: {},
};

const checkCashingReducer = (state, action) => {
    const checkCashing = state.checkCashing || defaultState;

    switch (action.type) {
        case FILL_UP_AMOUNT: {
            let checkAmount = null;
            if (action.target) {
                checkAmount = action.target.value;
                if (!/^[0-9]{0,}[.]{0,1}[0-9]{0,2}$/.test(String(checkAmount))) {
                    checkAmount = checkCashing.voucher.checkAmount || 0;
                }
            } else {
                checkAmount = checkCashing.voucher.checkAmount || 0;
            }
            const showAmountError = (checkAmount / 1) > getMaxAmount();
            const paymentFee = computePaymentFee(state, checkAmount / 1);
            const totalPayment = (checkAmount / 1) - paymentFee;
            const { receiveAmount, cardAmount } = computeReceiveAmounts(totalPayment);
            const hasValue = action.target ? String(action.target.value).length > 0 : false;
            const newVoucher = Object.assign({}, checkCashing.voucher, {
                checkAmount: action.target ? checkAmount : checkCashing.voucher.checkAmount,
                fee: paymentFee, // arseInt(paymentFee * 100, 10) / 100,
                receiveAmount: parseInt(Math.round(receiveAmount * 100), 10) / 100,
                cardAmount: parseInt(Math.round(cardAmount * 100), 10) / 100,
                totalReceiveAmount: parseInt(Math.round(totalPayment * 100), 10) / 100,
            });
            let errorMessage = null;
            let hasValidInput = true;
            if (isNaN(checkAmount) || (checkAmount / 1) === 0) {
                errorMessage = "money.order.amount.empty.message";
                hasValidInput = false;
            } else if (showAmountError) {
                errorMessage = "money.order.amount.error.message";
                hasValidInput = false;
            } 
            else if (totalPayment <= 0) {
                errorMessage = "check.cashing.amount.error.message";
                hasValidInput = false;
            }
            return Object.assign({}, checkCashing, {
                voucher: newVoucher,
                showAmountError: hasValue || action.showError ? errorMessage : "",
                hasValidInput,
            });
        }

        case FILL_UP_CHECK_NUMBER: {
            const { voucher } = checkCashing;
            if (action.target) {
                voucher.checkNumber = action.target.value;
            }
            return Object.assign({}, checkCashing, { voucher });;
        }

        case FILL_UP_DATE: {
            const newCheckDate = Object.assign({}, checkCashing.checkDate);
            const target = action.target;
            newCheckDate[target.name] = target.value;
            newCheckDate.selected = target.name;
            validateDate(newCheckDate);
            let error = ""; // !newCheckDate.invalidDate && newCheckDate.pastDate ? "past.date" : "";
            if (!newCheckDate.invalidDate) {
                if (newCheckDate.pastDate) {
                    error = "past.date";
                } else if (newCheckDate.futureDate) {
                    error = "future.date";
                }
            }
        /* let error = newCheckDate.invalidDate ? "invalid.date" : null;
        error = error || (newCheckDate.pastDate ? "past.date" : null);
        error = error || (newCheckDate.futureDate ? "future.date" : null); */
            return Object.assign({}, checkCashing, {
                checkDate: newCheckDate,
                hasValidDate: !(newCheckDate.invalidDate || newCheckDate.pastDate || newCheckDate.futureDate),
                showAmountError: error,
            });
        }
        case VALIDATE_DATE: {
            const newCheckDate = Object.assign({}, checkCashing.checkDate);
        // const newCheckDate = validateDate(checkCashing.checkDate);
            const target = action.target;
            newCheckDate[target.name] = target.value;
            newCheckDate.selected = target.name;
            validateDate(newCheckDate);
            let error = newCheckDate.invalidDate ? "invalid.date" : null;
            error = error || (newCheckDate.pastDate ? "past.date" : null);
            error = error || (newCheckDate.futureDate ? "future.date" : null);
            return Object.assign({}, checkCashing, {
                checkDate: newCheckDate,
                hasValidDate: !(newCheckDate.invalidDate || newCheckDate.pastDate),
                showAmountError: error,
                currentIndex: error ? checkCashing.currentIndex : checkCashing.currentIndex + 1,
            });
        }
        case FIELD_FOCUS: {
            const newCheckDate = Object.assign({}, checkCashing.checkDate);
            const target = action.selectedField;
            newCheckDate.selected = target.name;
            return Object.assign({}, checkCashing, {
                checkDate: newCheckDate,
            });
        }
        case ENDED_VIDEO: {
            return Object.assign({}, checkCashing, action);
        }
        case NEXT_PAGE: {
            return Object.assign({}, checkCashing, {
                currentIndex: checkCashing.currentIndex + 1,
                showAmountError: null,
            });
        }
        case PREV_PAGE: {
            return Object.assign({}, checkCashing, {
                currentIndex: checkCashing.currentIndex - 1,
                showAmountError: null,
            });
        }
        case INITIATE_CHECK_NUMBER_INPUT: {
            return Object.assign({}, checkCashing, {
                currentIndex: pages.length - 1,
                showAmountError: null
            });
        }
        case UPDATE_ADDITIONAL_ID: {
            const additionalIdNumber = action.additionalIdNumber;
            const result = validateAdditionalId(additionalIdNumber);
            const newUpdateCustomerData = Object.assign({}, checkCashing.updateCustomerData, {
                additionalIdNumber,
                hasValidSSN: result.isValid,
            });
            return Object.assign({}, checkCashing, {
                updateCustomerData: newUpdateCustomerData,
            });
        }
        case SUBMIT_UPDATE_ADDITIONAL_ID: {
            const result = validateAdditionalId(checkCashing.updateCustomerData.additionalIdNumber);
            if (result.isValid) {
                const onSuccess = (customerId, customerData) => {
                    action.dispatch({ type: CHECK_CASHING_SUCCESS });
                };
                const onError = (error) => {
                    action.dispatch({ type: CHECK_CASHING_ERROR });
                    action.dispatch({ type: SHOW_CHECK_CASHING_ERROR,
                        title: "cannot.proceed",
                        description: {
                            text: "cannot.proceed.desc",
                            args: state.config.defaultValues.helpHotline,
                            url: "/",
                        } });
                    Logger.error(`error preparing check cashing voucher.. ${JSON.stringify(error)}`);
                };
                updateCustomerAdditionalId(state, onSuccess, onError);
                return Object.assign({}, checkCashing, { showLoadingBox: true });
            }

            const newUpdateCustomerData = Object.assign({}, checkCashing.updateCustomerData, {
                errorMessage: result.error,
            });
            return Object.assign({}, checkCashing, {
                updateCustomerData: newUpdateCustomerData,
            });
        }
        case INITIATE_CHECK_CASHING: {
            const customerData = action.customerData;
        // newState.customerData = customerData;
            const customerId = customerData ? customerData.customerId : null;

            const isSecondaryIdSSN = customerData && customerData.secondaryPhotoIdType === "SSN";
            const currentIndex = isSecondaryIdSSN || action.proceedToTransaction ? 1 : 0;
            return Object.assign({}, defaultState, {
                customerId,
                customerData,
                currentIndex,
            });
        }

        case "UPDATE_CUSTOMER_SUCCESS": {
            const customerData = action.customerData;
            const customerId = customerData ? customerData.customerId : null;
            const currentIndex = customerData && customerData.secondaryPhotoIdType === "SSN" ? 1 : 0;
            return Object.assign({}, defaultState, {
                customerId,
                customerData,
                currentIndex,
            });
        }

        case ENABLE_EDIT_MODE: {
            const isSave = !action.reviewEditMode;
            const result = action.result;
            if (isSave) {
                const checkAmount = checkCashing.voucher.checkAmount / 1;
                if (isNaN(checkAmount) || checkAmount === 0) {
                    result.success = false;
                    return Object.assign({}, checkCashing, { showAmountError: "money.order.amount.empty.message" });
                }
                const showAmountError = checkAmount > getMaxAmount();
                if (showAmountError) {
                    result.success = false;
                    return Object.assign({}, checkCashing, { showAmountError: "money.order.amount.error.message" });
                }
            }
            result.success = true;
            return Object.assign({}, checkCashing, {
                reviewEditMode: action.reviewEditMode,
                showPopUp: false,
            });
        }
        case SHOW_MO_TERMS_AND_CONDITIONS: {
            return Object.assign({}, checkCashing, {
                showTerms: action.showTermsAndConditions,
            });
        }
        case GET_MONEY_ORDER_SUCCESS: {
            const { fee, increment } = action.response.getMoneyOrderSettingsResponse;
        // MAX_AMOUNT = 2999;
        // REQUIRE_ID_AMOUNT = 3000;
            PAYMENT_FEE = fee / 1;
            PAYMENT_INCREMENTS = increment / 1;
            return checkCashing;
        }

        case SUBMIT_CHECK_CASHING: {
            if (!checkCashing.termsAgreement && action.event) {
                action.event.preventDefault();
                return Object.assign({}, checkCashing, {
                    showTermsError: true,
                    showLoadingBox: false,
                });
            }
            if (checkCashing.customerId) {
                const { checkNumber, micrLine } = checkCashing.voucher;
                const { extractSuccess } = extractCheckNumber(String(checkNumber));
                checkCashing.voucher.micrLine = micrLine ? micrLine : String(checkNumber);

                if (!extractSuccess && !checkCashing.willInputCheckNumber) {
                    checkCashing.voucher.checkNumber = "";
                    checkCashing.willInputCheckNumber = true;
                    return Object.assign({}, checkCashing, {
                        showLoadingBox: false,
                        currentIndex: pages.length - 1,
                    });
                }

                checkCashing.voucher.productCode = state.config.defaultValues.productCode;
                checkCashing.voucher.currency = state.config.defaultValues.currency;
                prepareCheckCashingVoucher(checkCashing, (response) => {
                    Logger.log(`done preparing check cashing voucher.. ${JSON.stringify(response)}`);
                    const { config } = state;
                    const { receiveAmount, cardAmount } = state.checkCashing.voucher;
                    printCheckCashingVoucher({ response, receiveAmount, cardAmount, config });
                    action.dispatch({ type: CHECK_CASHING_SUCCESS });
                }, (error) => {
                    action.dispatch({ type: CHECK_CASHING_ERROR });
                    action.dispatch({ type: SHOW_CHECK_CASHING_ERROR,
                        title: "cannot.proceed",
                        description: {
                            text: "cannot.proceed.desc",
                            args: state.config.defaultValues.helpHotline,
                            url: "/",
                        } });
                    Logger.error(`error preparing check cashing voucher.. ${JSON.stringify(error)}`);
                });
                return Object.assign({}, checkCashing, { showLoadingBox: true });
            }
        }
        case SAVE_LOGIN_CUSTOMER_ID: {
            const customerData = action.customerData;
            const currentIndex = customerData.secondaryPhotoIdType !== "SSN" ? 0 : 1;
            return Object.assign({}, checkCashing, {
                customerId: action.customerId,
                customerData,
                currentIndex,
            });
        }
        case TERMS_AGREEMENT: {
            return Object.assign({}, checkCashing, {
                termsAgreement: action.termsAgreement,
                showTermsError: false,
            });
        }
        case CHECK_CASHING_SUCCESS: {
            const reviewPageIndex = pages.findIndex((item) => item.title === "Review");
            const currentIndex = checkCashing.currentIndex === pages.length - 1 ? reviewPageIndex : checkCashing.currentIndex;
            const newCheckCashing = Object.assign({}, checkCashing, {
                currentIndex: currentIndex + 1,
                showLoadingBox: false,
                willInputCheckNumber: false,
            });
            return newCheckCashing;
        }
        case CHECK_CASHING_ERROR: {
            const newCheckCashing = Object.assign({}, checkCashing, {
                showLoadingBox: false,
            });
            return newCheckCashing;
        }
        case SHOW_CHECK_CASHING_ERROR: {
            const newCheckCashing = Object.assign({}, checkCashing, {
                showIntervention: !action.hideIntervention,
                interventionTitle: action.title,
                interventionDesc: action.description,
                interventionOnClick: action.onClose,
                interventionURL: action.url,
                button: action.button,
                additionalClass: action.additionalClass,
                destinationPage: action.destinationPage,
                isForceUpdatePhotoIdDetails: true,
            });
            return newCheckCashing;
        }
        case SCAN_CHECK_COMPLETE: {
            const newVoucher = Object.assign({}, checkCashing.voucher, {
                checkImageFilename: action.checkImageFilename,
                checkReaderRefNumber: action.checkReaderRefNumber,
                checkNumber: action.checkNumber,
                checkType: action.checkType,
            });
            const newCheckCashing = Object.assign({}, checkCashing, {
                voucher: newVoucher,
                showLoadingBox: true,
            });
            isCheckAvailableForCollection(newCheckCashing, (response) => {
                Logger.log(`isCheckAvailableForCollection done.. ${JSON.stringify(response)}`);
                if (response && response.isCheckAvailableForCollectionResponse) {
                    const result = response.isCheckAvailableForCollectionResponse;
                    if (result.status === "AVAILABLE") {
                        action.dispatch({ type: CHECK_AVAILABLE_SUCCESS });
                    } else {
                        action.dispatch({ type: CHECK_AVAILABLE_FAILED, message: "check.is.blocked" });
                    }
                } else {
                    action.dispatch({ type: CHECK_AVAILABLE_FAILED, message: "check.is.blocked" });
                }
            }, (error) => {
                action.dispatch({ type: CHECK_CASHING_ERROR });
                action.dispatch({ type: SHOW_CHECK_CASHING_ERROR,
                    title: "cannot.proceed",
                    description: {
                        text: "cannot.proceed.desc",
                        args: state.config.defaultValues.helpHotline,
                        url: "/",
                    } });
                Logger.error(`isCheckAvailableForCollection error.. ${JSON.stringify(error)}`);
            });
            return newCheckCashing;
        }
        case CHECK_AVAILABLE_SUCCESS: {
            return Object.assign({}, checkCashing, {
                currentIndex: checkCashing.currentIndex + 1,
                showLoadingBox: false,
            });
        }
        case CHECK_AVAILABLE_FAILED: {
            const newCheckCashing = Object.assign({}, checkCashing, {
                showIntervention: true,
                interventionTitle: action.message,
                interventionDesc: {
                    text: "cannot.proceed.desc",
                    args: state.config.defaultValues.helpHotline,
                },
                interventionOnClick: action.onClose,
                interventionURL: "/",
                showLoadingBox: false,
            });
            return newCheckCashing;
        }
        case APPLY_PRODUCT_SETTINGS: {
            const productSettings = state.codeTable.productSettings;
            const configDefaults = state.config.checkCashingDefaults;
            ALLOWED_PAST_DAYS = productSettings.allowedDays || configDefaults.allowedDays || ALLOWED_PAST_DAYS;
            MAX_AMOUNT = productSettings.maxAmount || configDefaults.maxAmount || MAX_AMOUNT;
            PAYMENT_FEE = productSettings.feeAmount || configDefaults.feeAmount || PAYMENT_FEE;
            PAYMENT_INCREMENTS = productSettings.cardIncrements || configDefaults.cardIncrements || PAYMENT_INCREMENTS;
            MIN_AMOUNT_FOR_CARD = productSettings.minCardAmount || configDefaults.minCardAmount || MIN_AMOUNT_FOR_CARD;
            window.localStorage.maxAmount = MAX_AMOUNT;
            return Object.assign({}, checkCashing, { productSettingsApplied: true });
        }
        default:
            return checkCashing;
    }
};

const validateDate = (newCheckDate) => {
    // const newCheckDate = Object.assign({}, checkDate);
    const inputDate = Helpers.parseDateDDMMYYYY(`${newCheckDate.checkDD}-${newCheckDate.checkMM}-20${newCheckDate.checkYY}`);
    const today = new Date();
    const allowedDay = new Date();
    allowedDay.setDate(today.getDate() - ALLOWED_PAST_DAYS);

    newCheckDate.invalidDate = !Helpers.isDateValid(`20${newCheckDate.checkYY}`, (newCheckDate.checkMM / 1) - 1, newCheckDate.checkDD);
    newCheckDate.pastDate = allowedDay > inputDate;
    newCheckDate.futureDate = today < inputDate;
    newCheckDate.value = inputDate;
    return newCheckDate;
};

const computeReceiveAmounts = (totalAmount) => {
    let receiveAmount = 0;
    let cardAmount = 0;
    if (totalAmount <= getMinAmountForCard()) {
        return { receiveAmount: totalAmount, cardAmount };
    }
    receiveAmount = (totalAmount * 100) % (getCardPaymentIncrements() * 100);
    cardAmount = (totalAmount * 100) - receiveAmount;
    return { receiveAmount: receiveAmount / 100, cardAmount: cardAmount / 100 };
};

const validateAdditionalId = (additionalIdNumber) => {
    const idRegEx = /^[0-9]{3}-[0-9]{2}-[0-9]{4}$/;
    if (idRegEx.test(additionalIdNumber) && isPhotoIdNumberValid(additionalIdNumber)) {
        return { isValid: true };
    }
    return { isValid: false, error: "invalid.ssn.error" };
};

const computePaymentFee = (state, checkAmount) => {
    const { feeType, feeAmount: settingsFeeAmount } = state.codeTable.productSettings;
    const defaultFeeAmount = getPaymentFee();
    const feeAmount = settingsFeeAmount || defaultFeeAmount;

    if (checkAmount) {
        if (feeType === "PERCENTAGE") {
            const feePercentage = feeAmount / 100;
            return Math.round(checkAmount * feePercentage * 100) / 100;
        }
        return feeAmount;
    }
    return 0;
};

export default checkCashingReducer;



