import React from "react";
import { connect } from "react-redux";
import { push } from "connected-react-router";

import { INITIATE_CHECK_CASHING } from "../../actions/constants";
import { INITIATE_HALF_REGISTER_CUSTOMER, INITIATE_RESET_PASSWORD } from "../../actions";
import { Div } from "../../components/HTML";
import Card from "../../components/Card";

const AnimationPage3 = ({ onContinue, lang }) => {
    return (<Div>
        <Div className="review-page customer-review-page">
            <Div className="scan-check-page">
                <Div className="pull-left">
                    <img className="success-page-image" src={`resources/images/final-frame-succes-page-${lang}.jpg`} alt="Success" />
                    <br /><br />
                </Div>

                <div className="actions">
                    <Card onClick={onContinue} data-analytics="ok.btn">ok.btn</Card>
                </div>
            </Div>
        </Div>
    </Div>);
};

const mapDispatchToProps = (dispatch) => {
    return {
        onContinue: () => {
            dispatch({ type: INITIATE_CHECK_CASHING });
            dispatch({ type: INITIATE_HALF_REGISTER_CUSTOMER });
            dispatch({ type: INITIATE_RESET_PASSWORD });
            dispatch(push("/"));
        },
    };
};

const mapStateToProps = (state) => {
    return {
        showAmountError: state.checkCashing.showAmountError,
        hasEndedVideo: state.checkCashing.hasEndedVideo1,
        hasValidDate: state.checkCashing.hasValidDate,
        voucher: state.checkCashing.voucher,
        checkDate: state.checkCashing.checkDate,
        useCheckScanner: state.config.useCheckScanner,
        checkCashingDefaults: state.config.checkCashingDefaults,
        translation: state.internationalization.translation,
        moneygramMessages: state.moneygramMessages.messages,
        messageArgs: state.config.messageArgs,
        lang: state.internationalization.language,
    };
};

const Animation3 = connect(mapStateToProps, mapDispatchToProps)(AnimationPage3);
export default Animation3;



