import React from "react";
import { Label, Div } from "./HTML";

const Radio = ({ id, value, label, checked, onChange }) => (
    <Div className="radio-container">
        <input id={id} type="radio" className="radio with-sound" value={value} checked={checked} onChange={onChange} />
        <Label htmlFor={id} className="with-sound">{label}</Label>
    </Div>
);

export default Radio;



