import React from "react";
import { connect } from "react-redux";
import { P, Div, Button } from "../../components/HTML";
import { getMaxAmount } from "../../reducers/checkCashing";
import { NEXT_PAGE, INITIATE_HALF_REGISTER_CUSTOMER, INITIATE_UPDATE_CUSTOMER } from "../../actions";
import { MESSAGES_LANGUAGE_UPDATE } from "../../actions/moneygramMessages";
import { languageTranslate } from "../../actions/internationalization";
import Card from "../../components/Card";

const Welcome = ({ languages, selectedLanguage, changeLanguage, onUpdate, onClick, onRegister, productSettingsApplied }) => {
    const languageOption = languages.find((language) => selectedLanguage !== language.value);
    const onLanguageChange = () => changeLanguage(languageOption.value);
    const MAX_AMOUNT = Number(getMaxAmount()).toLocaleString();

    return (<Div>
        <Div className="page-background page-background-1" />
        <Div className="welcome-page">
            <Div id="i18n">
                <P defaultValue="Cambiar el idioma a:" style={{ width: "190px" }}>change.language.label</P>
                <Button onClick={onLanguageChange} data-analytics={`Language ${languageOption.name}`}>{languageOption.name}</Button>
            </Div>

            <Div className="welcome-messages">
                <P className="title" defaultValue="Welcome to Check Cashing">welcome.to.check.cashing</P>
                <P args={MAX_AMOUNT}
                  className="order-limit"
                  defaultValue={productSettingsApplied ? "" : `We ONLY ACCEPT PRINTED PAYROLL and GOVERNMENT checks up to $${MAX_AMOUNT / 1} PER TRANSACTION.`}>
                  welcome.to.check.cashing.limit
                </P>
            </Div>

            <Div className="actions">
                <Card onClick={onClick} to="/checkCashingOverview" defaultValue="Existing Customer" data-analytics="Existing customer">check.cashing.existing.user</Card>
                <Card onClick={onRegister} to="/checkCashingOverview" defaultValue="I am new" data-analytics="New customer">check.cashing.i.am.new</Card>
            </Div>
            <Div className="update-customer-action">
                <Button to="/updateCustomer" className="update-customer-btn" onClick={onUpdate} defaultValue="Update my details" data-analytics="Update account">check.cashing.update.account</Button>
                <Button to="/faq" className="update-customer-btn money-order-faq-btn" defaultValue="Help & Support" data-analytics="Help and support">help.and.support.btn</Button>
            </Div>
        </Div>
    </Div>);
};


const mapStateToProps = (state) => {
    return {
        selectedLanguage: state.internationalization.selectedLanguage || state.internationalization.language || "en",
        languages: state.internationalization.languages,
        productSettingsApplied: state.checkCashing.productSettingsApplied,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onFocus: (event) => {
            dispatch({ type: "FIELD_FOCUS", selectedField: event.target });
        },
        onRegister: () => {
            dispatch({ type: "INITIATE_LOGIN" });
            dispatch({ type: INITIATE_HALF_REGISTER_CUSTOMER });
            dispatch({ type: "TERMS_DISAGREE" });
        },
        onUpdate: () => {
            dispatch({ type: INITIATE_UPDATE_CUSTOMER });
            dispatch({ type: "TERMS_DISAGREE" });
        },
        onClick: () => {
            dispatch({ type: "RESET_STATE" });
            dispatch({ type: "INITIATE_LOGIN" });
            dispatch({ type: NEXT_PAGE });
        },
        changeLanguage: (value) => {
            languageTranslate(value, dispatch, true);
            dispatch({ type: MESSAGES_LANGUAGE_UPDATE, selectedLanguage: value });
        },
        dispatch,
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Welcome);



