import { loginCall } from "../actions/moneyOrderCustomer";
// import { SDN_DATA_MATCH } from "../actions/customer";

export const FILL_UP_FORM = "FILL_UP_FORM";
export const INITIATE_LOGIN = "INITIATE_LOGIN";
export const MONEY_ORDER_LOGIN_FAILURE = "MONEY_ORDER_LOGIN_FAILURE";
export const SUBMIT_LOGIN = "SUBMIT_LOGIN";
export const SAVE_LOGIN_CUSTOMER_ID = "SAVE_LOGIN_CUSTOMER_ID";

const defaultState = {
    username: null,
    password: null,
    customerId: null,
};

const loginReducer = (state, action) => {
    const login = state.login || defaultState;

    switch (action.type) {
        case FILL_UP_FORM: {
            const loginData = action.loginData;
            return Object.assign({}, login, loginData);
        }
        case SUBMIT_LOGIN: {
            const dispatch = action.dispatch;
            loginCall(login.username, login.password, (customerId, response) => {
                dispatch({ type: "HIDE_KEYBOARD" });
                dispatch({ type: "SHOW_LOADING_BOX", showLoadingBox: false });
                dispatch({ type: SAVE_LOGIN_CUSTOMER_ID, customerId, customerData: response.customerData });
            }, () => {
    //            dispatch({ type: MONEY_ORDER_LOGIN_FAILURE, errorMessage: error, error: { fieldName: "username", message: "invalid.customer.credentials" } });
                dispatch({ type: "SHOW_LOADING_BOX", showLoadingBox: false });
            });
            return login;
        }
        case MONEY_ORDER_LOGIN_FAILURE: {
            return Object.assign({}, login, {
                errorMessage: "invalid.customer.credentials",
            });
        }
        case SAVE_LOGIN_CUSTOMER_ID: {
            return Object.assign({}, login, {
                customerId: action.customerId,
            });
        }
        case INITIATE_LOGIN: {
            return defaultState;
        }
        default:
            return login;
    }
};

export default loginReducer;



