import "babel-polyfill";
import "custom-event-polyfill";

import React from "react";
import { render } from "react-dom";
import { Provider } from "react-redux";
import { ConnectedRouter } from "connected-react-router";
import { Logger } from "common-functions";

import { PAGE_CLICK } from "./actions";
import { loadAll } from "./actions/codeTable";

import App from "./containers/App";
import { getStore, getHistory } from "./store";

window.onerror = (message, url, lineNumber) => {
    Logger.error(`Unhandled error occured ${url} ${lineNumber}: ${message}`);
    return false;
};

const listenPageClick = () => {
    document.body.addEventListener("click", (event) => {
        if (event.clientX && event.clientY) {
            store.dispatch({
                type: PAGE_CLICK,
                x: event.clientX,
                y: event.clientY,
            });
        }
    });
};

const onConfigLoad = ({ dispatch, config, isProduction }) => {
    loadAll(dispatch, isProduction);
    if (config && (config.enableAnalytics)) {
        listenPageClick();
    }
};

const store = getStore(onConfigLoad);

render(
    (<Provider store={store}>
        <ConnectedRouter history={getHistory()}>
            <App />
        </ConnectedRouter>
    </Provider>),
    document.getElementById("app"),
);



