export const FIELD_FOCUS = "FIELD_FOCUS";
export const SHOW_KEYBOARD = "SHOW_KEYBOARD";
export const HIDE_KEYBOARD = "HIDE_KEYBOARD";

export const KEYPRESS = "KEYPRESS";
export const BACKSPACE = "BACKSPACE";
export const CLEAR = "CLEAR";
export const ENTER = "ENTER";

export const onKeyclick = (element) => {
    if (document.createEvent && element.dispatchEvent) {
        const event = document.createEvent("HTMLEvents");
        event.initEvent("onkeyclick", true, false);
        element.dispatchEvent(event);
    } else {
        element.fireEvent("onkeyclick");
    }
};

export const onKeypress = (key, state) => {
    const field = state.selectedField;
    let fieldValue = null;
    if (field) {
        const { value, maxLength } = field;
        const noMaxLength = !maxLength || maxLength <= 0;
        fieldValue = noMaxLength || value.length < maxLength ? value + key : value;
        field.value = fieldValue;
    }
    return Object.assign({}, state, {
        selectedField: field,
        selectedFieldValue: fieldValue,
    });
};

export const onBackspace = (state) => {
    const field = state.selectedField;
    let fieldValue = null;
    if (field && field.value.length > 0) {
        field.value = field.value.slice(0, -1);
        fieldValue = field.value;
    }
    return Object.assign({}, state, {
        selectedField: field,
        selectedFieldValue: fieldValue,
    });
};

export const onClear = (state) => {
    const field = state.selectedField;
    if (field) {
        field.value = "";
    }
    return Object.assign({}, state, {
        selectedField: field,
        selectedFieldValue: "",
    });
};

export const onEnter = (state) => {
    const activeElement = state.selectedField;
    const inputs = document.querySelectorAll("input:not([disabled]):not([type='checkbox']), textarea:not([disabled]), button[type='submit']:not([disabled])");

    const activeElementId = activeElement.id || activeElement.name;
    let currentIndex = -1;

    for (let i = 0; i < inputs.length; i += 1) {
        const element = inputs[i];
        const elementId = element.id || element.name;

        if (!element.readOnly && !element.disabled && elementId === activeElementId) {
            currentIndex = i;
        }
    }

    if (currentIndex !== -1) {
        const nextElement = inputs[currentIndex + 1] || inputs[0];
        const focusOrSubmit = () => {
            if (nextElement.type === "submit") {
                nextElement.classList.add("no-sound");
                nextElement.click();
                nextElement.classList.remove("no-sound");
            } else {
                nextElement.focus();
            }
        };
        setTimeout(focusOrSubmit, 0);
    }
    return state;
};



