import React from "react";
import { connect } from "react-redux";
import { InputV2 as Input, SelectV2, Dropdown, Div, P, Span, Label } from "common-components";
import { Helpers } from "common-functions";

import { VALIDATE, VALIDATION_FAILED, CLEAR_VALIDATION_ERRORS,
    CLEAR_SPECIFIC_VALIDATION_ERROR, RESET_INPUTS_FORMAT_VALIDATION } from "../../actions/validation";

import Steps from "../../components/Steps";
import Navigation from "../../components/Navigation";

import { FILL_UP_REQUEST_DATA, onContinue as customerContinue, onPrevious as customerPrevious } from "../../actions/customer";

const monthsSelection = [
    { value: "0", name: "months.jan" },
    { value: "1", name: "months.feb" },
    { value: "2", name: "months.mar" },
    { value: "3", name: "months.apr" },
    { value: "4", name: "months.may" },
    { value: "5", name: "months.jun" },
    { value: "6", name: "months.jul" },
    { value: "7", name: "months.aug" },
    { value: "8", name: "months.sep" },
    { value: "9", name: "months.oct" },
    { value: "10", name: "months.nov" },
    { value: "11", name: "months.dec" },
];

const getLabel = (errors, name, label) => <Label className="photo-id">{label}</Label>;

const Date = ({ onChange, onDayChange, value, dayAttribute, monthAttribute, yearAttribute, errors, name, label, lang }) => (
    <Div className="customer-form">
        <Div>
            <Dropdown name={monthAttribute}
              value={value[monthAttribute]}
              items={monthsSelection}
              onChange={onChange}
              placeholder="register.customer.month.placeholder"
              className="col-6"
              parentClassName={`${lang}-dropdown`}
              labelComponent={getLabel(errors, name, label)}
              focusOnCompleteFillUp={dayAttribute}
              hideErrorMessage={false}
              maxDisplayedItem={3} />
        </Div>
        <Div>
            <Input name={dayAttribute}
              value={value[dayAttribute]}
              className="col-3"
              onChange={(event) => onDayChange(event, onChange)}
              label="&nbsp;"
              parentClassName={`${lang}-input`}
              placeholder="register.customer.day.placeholder"
              focusOnCompleteFillUp={yearAttribute}
              hideErrorMessage={false} />
        </Div>
        <Div>
            <Input name={yearAttribute}
              value={value[yearAttribute]}
              className="col-3"
              onChange={onChange}
              placeholder="register.customer.year.placeholder"
              label="&nbsp;"
              parentClassName={`${lang}-input`}
              hideErrorMessage={false} />
        </Div>
    </Div>
);

const getPhotoIDPageTitle = (forPrimary, isAdditionalRequirement) => {
    const additionalTitlePrefix = isAdditionalRequirement ? "additional" : "register.customer";
    const idTypeTitle = forPrimary ? "." : ".secondary.";
    return `${additionalTitlePrefix}${idTypeTitle}photo.id.label`;
};

const getPhotoIDLocation = (props, photoIdTypes, photoIdType, photoIdState) => {
    const { requestData, onChange, photoIdOptions, countries, allowedCountries, states, ausStates, errors, forPrimary } = props;
    const photoIdKey = forPrimary ? "photoId" : "secondaryPhotoId";

    const { customerData } = requestData;
    const mapping = photoIdTypes.find((idType) => idType.value === photoIdType);

    const getMappedCountry = (type) => {
        const countryTypeMapping = photoIdOptions.countryTypeMappings.find((item) => item.type === type);
        return countryTypeMapping ? countryTypeMapping.country : null;
    };
    const photoIdCountry = getMappedCountry(photoIdType) || customerData[`${photoIdKey}Country`];

    const noPhotoIdCountry = !mapping.fields.find((field) => field === "Country");
    const noPhotoIdState = !mapping.fields.find((field) => field === "State");

    const countryStates = photoIdCountry === "AUS" ? ausStates : states.filter((item) => item.countryCode === photoIdCountry);
    const disableStates = countryStates.length === 0 || noPhotoIdState;

    const photoIdCountryName = `${photoIdKey}Country`;
    const photoIdCountryLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.country.placeholder`;

    const photoIdStateName = `${photoIdKey}State`;
    const photoIdStateLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.state.placeholder`;

    return (<Div key="photoIdLocation">
        <Dropdown name={photoIdCountryName}
          disabled
          hidden={noPhotoIdCountry}
          value={photoIdCountry}
          items={countries}
          onChange={onChange}
          placeholder={photoIdCountryLabel}
          labelComponent={getLabel(errors, photoIdCountryName, photoIdCountryLabel)}
          className="col-4 right-align"
          parentClassName={`${props.lang}-dropdown`}
          hideErrorMessage={false} />
        <Dropdown name={photoIdStateName}
          value={disableStates ? null : photoIdState}
          items={countryStates}
          onChange={onChange}
          placeholder={photoIdStateLabel}
          labelComponent={getLabel(errors, photoIdStateName, photoIdStateLabel)}
          className="col-4 right-align"
          parentClassName={`${props.lang}-dropdown`}
          disabled={disableStates}
          hidden={disableStates}
          hideErrorMessage={false} />
    </Div>);
};

const PhotoIDPage = (props) => {
    const { requestData, photoIdOptions, errors, forPrimary, isComponentType,
        steps, currentStep, currentIndex, onContinue, onPrevious,
        isAdditionalRequirement, hideSteps, disablePrevious, newUser,
        onChange, onDayChange, onPhotoIdTypeChange, onSecondaryPhotoIdTypeChange } = props;

    const requiredFields = [];
    const photoIdKey = forPrimary ? "photoId" : "secondaryPhotoId";

    const { customerData } = requestData;
    const photoIdNumber = customerData[`${photoIdKey}Number`];
    let photoIdTypes = photoIdOptions ? photoIdOptions[`${photoIdKey}Types`] : [];

    if (forPrimary) {
        if (customerData.secondaryPhotoIdType) {
            photoIdTypes = photoIdTypes.filter((item) => item.value !== customerData.secondaryPhotoIdType);
        }
    } else {
        photoIdTypes = photoIdTypes.filter((item) => item.value !== customerData.photoIdType);
    }

    if (newUser) {
        photoIdTypes = photoIdTypes.filter((item) => item.invalidForCheckCashing !== true);
    }

    const photoIdType = customerData[`${photoIdKey}Type`];
    const photoIdState = customerData[`${photoIdKey}State`];

    if (photoIdOptions && photoIdType) {
        const photoIdCardType = customerData[`${photoIdKey}CardType`];
        const photoIdReferenceNumber = customerData[`${photoIdKey}ReferenceNumber`];

        const mapping = photoIdTypes.find((idType) => idType.value === photoIdType);

        if (mapping && mapping.fields && mapping.fields.length) {
            const photoIdLocation = getPhotoIDLocation(props, photoIdTypes, photoIdType, photoIdState);
            const photoIdExpiryDate = (<Date key="expiryDate"
              label={`register.customer${forPrimary ? "." : ".secondary."}photo.id.expiry.date.label`}
              name={`${photoIdKey}ExpiryDate`}
              value={requestData.customerData}
              dayAttribute={`${photoIdKey}ExpiryDay`}
              monthAttribute={`${photoIdKey}ExpiryMonth`}
              yearAttribute={`${photoIdKey}ExpiryYear`}
              onChange={onChange}
              onDayChange={onDayChange}
              errors={errors} />);

            const photoIdIssueDate = (<Date key="issueDate"
              label={`register.customer${forPrimary ? "." : ".secondary."}photo.id.issue.date.label`}
              name={`${photoIdKey}IssueDate`}
              value={requestData.customerData}
              dayAttribute={`${photoIdKey}IssueDay`}
              monthAttribute={`${photoIdKey}IssueMonth`}
              yearAttribute={`${photoIdKey}IssueYear`}
              onChange={onChange}
              onDayChange={onDayChange}
              errors={errors} />);

            const photoIdCardTypeName = `${photoIdKey}CardType`;
            const photoIdCardTypeLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.card.type.placeholder`;

            const photoIdRefNumberName = `${photoIdKey}ReferenceNumber`;
            const photoIdRefNumberLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.reference.number.placeholder`;

            const medicardDetails = (<Div key="medicardDetails" className="customer-form">
                <SelectV2 name={photoIdCardTypeName}
                  labelComponent={getLabel(errors, photoIdCardTypeName, photoIdCardTypeLabel)}
                  value={photoIdCardType}
                  className="col-4"
                  parentClassName={`${props.lang}-input`}
                  items={photoIdOptions.medicardTypes}
                  focusOnCompleteFillUp={photoIdRefNumberName}
                  onChange={onChange} />
                <Input name={photoIdRefNumberName}
                  labelComponent={getLabel(errors, photoIdRefNumberName, photoIdRefNumberLabel)}
                  value={photoIdReferenceNumber}
                  className="col-4"
                  parentClassName={`${props.lang}-input`}
                  onChange={onChange}
                  placeholder={photoIdRefNumberLabel}
                  hideErrorMessage={false} />
            </Div>);

            const componentMappings = new Map();
            componentMappings.set("IssueDate", photoIdIssueDate);
            componentMappings.set("ExpiryDate", photoIdExpiryDate);
            componentMappings.set("Country", photoIdLocation);
            componentMappings.set("State", photoIdLocation);
            componentMappings.set("CardType", medicardDetails);

            mapping.fields.forEach((field) => {
                const component = componentMappings.get(field);
                if (component && requiredFields.indexOf(component) === -1) {
                    requiredFields.push(component);
                }

                if (mapping.stateFieldMappings && photoIdState) {
                    const stateFieldMapping = mapping.stateFieldMappings.find((item) => item.state === photoIdState);
                    if (stateFieldMapping) {
                        const additionalComponent = componentMappings.get(stateFieldMapping.field);
                        if (additionalComponent && requiredFields.indexOf(additionalComponent) === -1) {
                            requiredFields.push(additionalComponent);
                        }
                    }
                }
            });
        }
    }

    const pageTitle = getPhotoIDPageTitle(forPrimary, isAdditionalRequirement);

    const photoIdTypeName = `${photoIdKey}Type`;
    const photoIdTypeLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.type.placeholder`;

    const photoIdNumberName = `${photoIdKey}Number`;
    const photoIdNumberLabel = `register.customer${forPrimary ? "." : ".secondary."}photo.id.number.placeholder`;

    const simpleUI = photoIdTypes.length === 1 && requiredFields.length === 0;

    photoIdTypes.forEach((elem) => {
        const type = elem;
        type.disabled = elem.invalidForCheckCashing;
    });

    const photoIdTypeComponent = (<SelectV2 name={photoIdTypeName}
      value={photoIdType}
      className="col-6 photo-id-type"
      errorClassName="col-6"
      parentClassName={`${props.lang}-input`}
      items={photoIdTypes}
      placeholder={photoIdTypeLabel}
      labelComponent={getLabel(errors, photoIdTypeName, photoIdTypeLabel)}
      onChange={forPrimary ? onPhotoIdTypeChange : onSecondaryPhotoIdTypeChange}
      hideDisabled={newUser} />);

    const photoIdComponent = (<Div className="customer-form">
        {isComponentType ? <P className="title">{pageTitle}</P> : null}
        <Div>
            {simpleUI ? null : photoIdTypeComponent}

            <Input name={photoIdNumberName}
              value={photoIdNumber}
              className={simpleUI ? "col-12" : "col-4"}
              parentClassName={`${props.lang}-input`}
              onChange={onChange}
              placeholder={photoIdNumberLabel}
              labelComponent={simpleUI ? null : getLabel(errors, photoIdNumberName, photoIdNumberLabel)}
              hideErrorMessage={false}
              shouldAutoFocus={!isComponentType} />

            {requiredFields}
        </Div>
    </Div>);

    if (isComponentType) {
        return photoIdComponent;
    }

    return (<Div>
        {hideSteps !== true ? <Steps items={steps} currentStep={currentStep} /> : <br />}
        <Div className="register-customer has-badge-panel indented-body">
            <Span className="badge">{hideSteps !== true ? currentIndex + 1 : 1}</Span>
            <P className="title">{pageTitle}</P>
            {photoIdComponent}
        </Div>
        <Navigation onNext={() => onContinue(forPrimary)} onPrevious={onPrevious} disablePrevious={disablePrevious} nextOnEnter />
    </Div>);
};

const SecondaryPhotoIDField = (props) => PhotoIDPage({ ...props, forPrimary: false, isComponentType: true });

const PrimaryPhotoIDPage = (props) => PhotoIDPage({ ...props, forPrimary: true });
const SecondaryPhotoIDPage = (props) => PhotoIDPage({ ...props, forPrimary: false });

const mapStateToProps = (state, ownProps) => {
    const { pages, steps, currentIndex, requestData, newUser, hasLogin } = state.customer;
    const { states, ausStates, countries } = state.codeTable;
    const { photoIdOptions, countryTypeMappings, allowedCountryCodes } = state.config;
    const { errors } = state.ui;
    const pageOffset = state.customer.pages.findIndex((page) => page.title === "name");

    const page = pages[currentIndex];
    const currentStep = page.step;

    const { pageBadge } = ownProps;
    const pageCurrentIndex = pageBadge ? pages.findIndex((page) => page.title === pageBadge) : currentIndex - pageOffset;

    return {
        pages,
        steps,
        requestData,
        currentStep,
        states,
        ausStates,
        photoIdOptions,
        errors,
        newUser,
        disablePrevious: hasLogin,
        countries,
        countryTypeMappings,
        allowedCountries: countries.filter((country) => allowedCountryCodes.includes(country.value)),
        currentIndex: !newUser && pageBadge ? pageCurrentIndex - pageOffset : pageCurrentIndex,
        lang: state.internationalization.language || "en",
    };
};

const onDayChange = (event, onChange) => (dispatch) => {
    const { name, value } = event.target;
    onChange(event);

    if (value) {
        if (!Helpers.isDayValid(value)) {
            dispatch({ type: VALIDATION_FAILED, error: { fieldName: name, message: "invalid.day" } });
        } else {
            dispatch({ type: CLEAR_SPECIFIC_VALIDATION_ERROR, filedName: name });
        }
    }
};

const mapDispatchToProps = (dispatch, ownProps) => {
    const disableEditing = false;
    const onContinue = () => dispatch(ownProps.onContinue || customerContinue);
    const onPrevious = () => dispatch(ownProps.onPrevious || customerPrevious(disableEditing));

    const eventToCustomerData = (event) => {
        const fieldName = event.target.name;
        const fieldValue = event.target.value;
        const customerData = {};
        customerData[fieldName] = fieldValue;
        return customerData;
    };

    return {
        onContinue,
        onPrevious,
        onDayChange: (event, onChange) => dispatch(onDayChange(event, onChange)),
        onChange: (event) => {
            const customerData = eventToCustomerData(event);
            if (event.target.name === "secondaryPhotoIdNumber") {
                const secondaryPhotoIdNumber = customerData.secondaryPhotoIdNumber;
                if (secondaryPhotoIdNumber && secondaryPhotoIdNumber.length > 0) {
                    customerData.secondaryPhotoIdNumber = Helpers.formatStringPattern(secondaryPhotoIdNumber, [3, 2, 4], "-");
                }
            }
            dispatch({ type: FILL_UP_REQUEST_DATA, customerData });
        },
        onPhotoIdTypeChange: (event) => {
            const customerData = eventToCustomerData(event);
            customerData.photoIdCountry = null;
            customerData.photoIdState = null;
            customerData.photoIdExpiryDate = null;
            customerData.photoIdExpiryDay = null;
            customerData.photoIdExpiryMonth = null;
            customerData.photoIdExpiryYear = null;
            customerData.photoIdNumber = null;
            dispatch({ type: FILL_UP_REQUEST_DATA, customerData });
            dispatch({ type: CLEAR_VALIDATION_ERRORS, customerData });
            dispatch({ type: RESET_INPUTS_FORMAT_VALIDATION, customerData });
        },
        onSecondaryPhotoIdTypeChange: (event) => {
            const customerData = eventToCustomerData(event);
            customerData.secondaryPhotoIdCountry = null;
            customerData.secondaryPhotoIdState = null;
            customerData.secondaryPhotoIdExpiryDate = null;
            customerData.secondaryPhotoIdExpiryDay = null;
            customerData.secondaryPhotoIdExpiryMonth = null;
            customerData.secondaryPhotoIdExpiryYear = null;
            customerData.secondaryPhotoIdNumber = null;
            dispatch({ type: FILL_UP_REQUEST_DATA, customerData });
        },
    };
};

const PrimaryPhotoID = connect(mapStateToProps, mapDispatchToProps)(PrimaryPhotoIDPage);
const SecondaryPhotoID = connect(mapStateToProps, mapDispatchToProps)(SecondaryPhotoIDPage);
const SecondaryPhotoIDComponent = connect(mapStateToProps, mapDispatchToProps)(SecondaryPhotoIDField);
export { PrimaryPhotoID, SecondaryPhotoID, SecondaryPhotoIDComponent };



