import { Helpers, Validation } from "common-functions";
import { SHOW_INTERVENTION, HIDE_INTERVENTION, INITIATE_SEND_MONEY, INITIATE_RECEIVE_MONEY, RESET_STATE } from "../actions";
import { VALIDATE, VALIDATION_FAILED, CLEAR_VALIDATION_ERRORS, VALIDATE_INPUTS_FORMAT,
    RESET_INPUTS_FORMAT_VALIDATION, CLEAR_SPECIFIC_VALIDATION_ERROR } from "../actions/validation";

import { FILL_UP_REQUEST_DATA, FILL_UP_CREDENTIALS_REQUEST_DATA, FILL_UP_CUSTOMER_QA_REQUEST_DATA } from "../actions/customer";
import { FILL_UP_RESET_PASSWORD_REQUEST_DATA, FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA, FILL_UP_FORCE_CHANGE_PASSWORD } from "../actions/resetPassword";

const revalidateActions = [
    VALIDATE_INPUTS_FORMAT, FILL_UP_REQUEST_DATA, FILL_UP_CREDENTIALS_REQUEST_DATA,
    FILL_UP_CUSTOMER_QA_REQUEST_DATA, FILL_UP_RESET_PASSWORD_REQUEST_DATA, FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA, FILL_UP_FORCE_CHANGE_PASSWORD,
];

const defaultState = {
    onGoingRequestCount: 0,
    errors: [],
    hasInput: undefined,
    hasFormatErrors: undefined,
};

const processRequestErrors = (action) => {
    let errors = [];
    const value = action.error || action.errors;

    if (value) {
        if (Helpers.isArray(value)) {
            errors = value;
        } else if (value.fieldName) {
            errors = [{
                fieldName: value.fieldName,
                message: value.message,
            }];
        }
    }
    return errors;
};

const uiReducer = (state, action) => {
    const uiState = state.ui || defaultState;

    const requestStarted = action.type.endsWith("_REQUEST");
    const requestSuccesful = action.type.endsWith("_SUCCESS");
    const requestFailed = action.type.endsWith("_FAILURE");

    const isNextPage = action.type.indexOf("NEXT_PAGE") !== -1;
    const isPreviousPage = action.type.indexOf("PREVIOUS_PAGE") !== -1;
    const isNavigate = action.type.indexOf("PAGE_NAVIGATE") !== -1;
    const isPageChange = isNextPage || isPreviousPage || isNavigate;

    if (requestStarted && !action.processOnBackground) {
        return Object.assign({}, uiState, { onGoingRequestCount: uiState.onGoingRequestCount + 1 });
    } else if (requestSuccesful && !action.processOnBackground) {
        let updatedCount = uiState.onGoingRequestCount - 1;
        updatedCount = updatedCount < 0 ? 0 : updatedCount;
        return Object.assign({}, uiState, { onGoingRequestCount: updatedCount });
    } else if (requestFailed && (!action.processOnBackground || action.error.fieldName)) {
        const previousCount = uiState.onGoingRequestCount;
        let updatedCount = action.processOnBackground ? previousCount : previousCount - 1;
        updatedCount = updatedCount < 0 ? 0 : updatedCount;
        return Object.assign({}, uiState, { onGoingRequestCount: updatedCount, errors: processRequestErrors(action) });
    } else if (action.type.startsWith("INITIATE_") || isPageChange) {
        const initiateSend = action.type === INITIATE_SEND_MONEY;
        const initiateReceive = action.type === INITIATE_RECEIVE_MONEY;
        const sendingMoney = initiateSend || uiState.sendingMoney;
        const receivingMoney = initiateReceive || uiState.receivingMoney;
        return Object.assign({}, uiState, { errors: [], sendingMoney, receivingMoney, hasFormatErrors: undefined, hasInput: undefined });
    }

    if (revalidateActions.includes(action.type)) {
        const result = Validation.validate();
        const { hasInput, hasFormatErrors } = result;
        return Object.assign({}, uiState, { hasInput, hasFormatErrors });
    }

    switch (action.type) {
        case VALIDATION_FAILED: {
            const hasFormatErrors = action.hasFormatErrors || uiState.hasFormatErrors;
            return Object.assign({}, uiState, { errors: processRequestErrors(action), hasFormatErrors });
        }
        case "CLEAR_INPUT": {
            return Object.assign({}, uiState, { hasInput: action.hasInput });
        }

        case CLEAR_VALIDATION_ERRORS:
            return Object.assign({}, uiState, { errors: [] });

        case CLEAR_SPECIFIC_VALIDATION_ERROR:
            const filteredErrors = uiState.errors.filter((error) => error.fieldName === action.fieldName);
            return Object.assign({}, uiState, { errors: filteredErrors });

        case RESET_INPUTS_FORMAT_VALIDATION:
            return Object.assign({}, uiState, { hasFormatErrors: undefined, hasInput: undefined });

        case VALIDATE: {
            const { internationalization } = state;
            const translation = internationalization ? internationalization.translation : null;
            const result = Validation.validate(translation, action.fieldID);
            const { errors, hasInput, hasFormatErrors } = result;
            return Object.assign({}, uiState, { errors, hasInput, hasFormatErrors: hasFormatErrors || action.hasFormatErrors });
        }

        case SHOW_INTERVENTION:
            return Object.assign({}, uiState, {
                showIntervention: true,
                interventionTitle: action.title,
                interventionDescription: action.description,
                interventionChildren: action.children,
                interventionClassName: action.className,
            });

        case HIDE_INTERVENTION:
            return Object.assign({}, uiState, { showIntervention: false });

        case RESET_STATE:
            return defaultState;

        default:
            return uiState;
    }
};

export default uiReducer;



