import { RestHost, Helpers, Preloader } from "common-functions";
import { restSendHandler } from "./moneygramMessages";
import { APPLY_PRODUCT_SETTINGS } from "./constants";

export const COUNTRIES_REQUEST = "COUNTRIES_REQUEST";
export const COUNTRIES_SUCCESS = "COUNTRIES_SUCCESS";
export const COUNTRIES_FAILURE = "COUNTRIES_FAILURE";

export const COUNTRY_SELECT = "COUNTRY_SELECT";

export const STATES_REQUEST = "STATES_REQUEST";
export const STATES_SUCCESS = "STATES_SUCCESS";
export const STATES_FAILURE = "STATES_FAILURE";

export const AUS_STATES_REQUEST = "AUS_STATES_REQUEST";
export const AUS_STATES_SUCCESS = "AUS_STATES_SUCCESS";
export const AUS_STATES_FAILURE = "AUS_STATES_FAILURE";

export const CURRENCIES_REQUEST = "CURRENCIES_REQUEST";
export const CURRENCIES_SUCCESS = "CURRENCIES_SUCCESS";
export const CURRENCIES_FAILURE = "CURRENCIES_FAILURE";

export const CURRENCY_SELECT = "CURRENCY_SELECT";

export const COUNTRY_CURRENCIES_REQUEST = "COUNTRY_CURRENCIES_REQUEST";
export const COUNTRY_CURRENCIES_SUCCESS = "COUNTRY_CURRENCIES_SUCCESS";
export const COUNTRY_CURRENCIES_FAILURE = "COUNTRY_CURRENCIES_FAILURE";

export const TOWNS_REQUEST = "TOWNS_REQUEST";
export const TOWNS_SUCCESS = "TOWNS_SUCCESS";
export const TOWNS_FAILURE = "TOWNS_FAILURE";

export const PRODUCT_SETTINGS_REQUEST = "PRODUCT_SETTINGS_REQUEST";
export const PRODUCT_SETTINGS_SUCCESS = "PRODUCT_SETTINGS_SUCCESS";
export const PRODUCT_SETTINGS_FAILURE = "PRODUCT_SETTINGS_FAILURE";

function getCodeTableRequestData() {
    return {
        codeTableRequest: {
            lastUpdateDate: null,
        },
    };
}

const getNextUpdate = (lastUpdate, value) => {
    later.date.localTime();

    let schedule = later.parse.text(value);
    if (schedule.error >= 0) {
        schedule = later.parse.cron(value);
    }
    const instances = later.schedule(schedule).next(2, lastUpdate);
    return lastUpdate.getTime() !== instances[0].getTime() ? instances[0] : instances[1];
};

const shouldRefreshCodeTable = (key, state, customLastUpdate) => {
    let shouldRefresh = false;
    const schedules = state.config.cacheRefreshSchedule;
    if (schedules) {
        const schedule = schedules.find((item) => item.key === key);

        if (schedule) {
            const storedLastUpdate = customLastUpdate || state.codeTable[`${key}LastUpdateDate`];
            if (storedLastUpdate != null) {
                const now = new Date();
                const lastUpdate = new Date(JSON.parse(storedLastUpdate));
                const nextUpdate = getNextUpdate(lastUpdate, schedule.value);

                if (lastUpdate < nextUpdate && nextUpdate < now) {
                    shouldRefresh = true;
                }
            } else {
                shouldRefresh = true;
            }
        }
    }
    return shouldRefresh;
};

export const getCountryTowns = (countryCode, state) => {
    let countryTowns = [];

    const { commonTowns, otherTowns } = state;
    const { commonCountries, enableTownAutoComplete } = state.config;

    if (enableTownAutoComplete) {
        const isCommonCountry = commonCountries && commonCountries.find((item) => item.value === countryCode);
        const towns = isCommonCountry ? commonTowns.items : otherTowns.items;
        if (towns && towns[countryCode]) {
            countryTowns = towns[countryCode].map((item) => {
                return { name: item, value: item };
            });
        }
    }
    return countryTowns;
};

export const countriesRequest = (processOnBackground) => (dispatch, getState) => {
    if (shouldRefreshCodeTable("countries", getState())) {
        dispatch({ type: COUNTRIES_REQUEST, processOnBackground });

        restSendHandler(getState, RestHost.POST, "codeTable/getCountries", getCodeTableRequestData(), null,
            (response) => dispatch({ type: COUNTRIES_SUCCESS, response, processOnBackground }),
            (error) => dispatch({ type: COUNTRIES_FAILURE, error, processOnBackground }),
            RestHost.JSON,
            processOnBackground,
        );
    }
};

export const statesRequest = (processOnBackground) => (dispatch, getState) => {
    if (shouldRefreshCodeTable("states", getState())) {
        dispatch({ type: STATES_REQUEST, processOnBackground });

        restSendHandler(getState, RestHost.POST, "codeTable/getStateProvinces", getCodeTableRequestData(), null,
            (response) => dispatch({ type: STATES_SUCCESS, response, processOnBackground }),
            (error) => dispatch({ type: STATES_FAILURE, error, processOnBackground }),
            RestHost.JSON,
            processOnBackground,
        );
    }
};

export const ausStatesRequest = (processOnBackground, isProduction) => (dispatch, getState) => {
    if (shouldRefreshCodeTable("ausStates", getState())) {
        const restHost = new RestHost("resources/config/", null, null);

        dispatch({ type: AUS_STATES_REQUEST, processOnBackground });

        const ausStates = isProduction ? "aus-states.min.json" : "aus-states.json";
        restHost.get(ausStates,
            (response) => dispatch({ type: AUS_STATES_SUCCESS, response, processOnBackground }),
            (error) => dispatch({ type: AUS_STATES_FAILURE, error, processOnBackground }),
            RestHost.JSON,
            processOnBackground,
        );
    }
};

