export const RESET_STATE = "RESET_STATE";
export const NEXT_PAGE = "NEXT_PAGE";
export const PREVIOUS_PAGE = "PREVIOUS_PAGE";

export const PAGE_CLICK = "PAGE_CLICK";

export const SHOW_KEYBOARD = "SHOW_KEYBOARD";
export const HIDE_KEYBOARD = "HIDE_KEYBOARD";

export const INITIATE_OVERVIEW = "INITIATE_OVERVIEW";
export const INITIATE_REGISTER_CUSTOMER = "INITIATE_REGISTER_CUSTOMER";
export const INITIATE_HALF_REGISTER_CUSTOMER = "INITIATE_HALF_REGISTER_CUSTOMER";
export const INITIATE_FULL_REGISTER_CUSTOMER = "INITIATE_FULL_REGISTER_CUSTOMER";
export const INITIATE_UPDATE_CUSTOMER = "INITIATE_UPDATE_CUSTOMER";
export const INITIATE_MANAGE_RECIPIENTS = "INITIATE_MANAGE_RECIPIENTS";
export const INITIATE_SEND_MONEY = "INITIATE_SEND_MONEY";
export const INITIATE_RECEIVE_MONEY = "INITIATE_RECEIVE_MONEY";
export const INITIATE_REFUND_MONEY = "INITIATE_REFUND_MONEY";
export const INITIATE_LOCATION = "INITIATE_LOCATION";

export const INITIATE_REGISTER_RECIPIENT = "INITIATE_REGISTER_RECIPIENT";
export const INITIATE_UPDATE_RECIPIENT = "INITIATE_UPDATE_RECIPIENT";
export const INITIATE_RESET_PASSWORD = "INITIATE_RESET_PASSWORD";
export const INITIATE_TRANSACTION_STATUS = "INITIATE_TRANSACTION_STATUS";

export const SHOW_INTERVENTION = "SHOW_INTERVENTION";
export const HIDE_INTERVENTION = "HIDE_INTERVENTION";

export const nextPage = () => {
    return { type: NEXT_PAGE };
};

export const previousPage = () => {
    return { type: PREVIOUS_PAGE };
};

export const initiateOverview = () => {
    return { type: INITIATE_OVERVIEW };
};

export const initiateRegisterCustomer = () => {
    return { type: INITIATE_REGISTER_CUSTOMER };
};

export const initiateHalfRegisterCustomer = () => {
    return { type: INITIATE_HALF_REGISTER_CUSTOMER };
};

export const initiateUpdateCustomer = () => {
    return { type: INITIATE_UPDATE_CUSTOMER };
};

export const initiateSendMoney = () => {
    return { type: INITIATE_SEND_MONEY };
};

export const initiateReceiveMoney = () => {
    return { type: INITIATE_RECEIVE_MONEY };
};

export const initiateRefundMoney = () => {
    return { type: INITIATE_REFUND_MONEY };
};

export const initiateTransactionStatus = () => {
    return { type: INITIATE_TRANSACTION_STATUS };
};

export const initiateMangeRecipients = () => {
    return { type: INITIATE_MANAGE_RECIPIENTS };
};

export const initiateRegisterRecipient = () => {
    return { type: INITIATE_REGISTER_RECIPIENT };
};

export const initiateUpdateRecipient = () => {
    return { type: INITIATE_UPDATE_RECIPIENT };
};

export const initiateLocation = () => {
    return { type: INITIATE_LOCATION };
};

export const initiateResetPassword = () => {
    return { type: INITIATE_RESET_PASSWORD };
};



