import React from "react";
import { connect } from "react-redux";
import { Helpers } from "common-functions";
import { Dropdown, InputV2 as Input, Div, P, Label } from "common-components";

import { FILL_UP_AMOUNT, NEXT_PAGE, PREV_PAGE, FILL_UP_DATE, VALIDATE_DATE, FILL_UP_CHECK_NUMBER, SUBMIT_CHECK_CASHING } from "../../actions/constants";
import { VALIDATE, VALIDATION_FAILED, CLEAR_SPECIFIC_VALIDATION_ERROR, VALIDATE_INPUTS_FORMAT } from "../../actions/validation";

import { getMaxAmount, getPaymentFee } from "../../reducers/checkCashing";

import Navigation from "../../components/Navigation";

class CheckNumberInput extends React.Component {
    componentDidMount() {
        this.props.validateOnFirstLoad();
    }

    render() {
        const { onNext, checkNumber, onCheckNumberChange } = this.props;
        const checkNumberLabel = "check.cashing.check.number.placeholder";
        return (<Div>
            <Div className="check-number-page">
                <P className="mo-review-subtitle">details.of.check.label</P>
                <Div className="pull-left">
                    <Input name="checkNumber"
                      value={checkNumber}
                      className="col-12"
                      onChange={onCheckNumberChange}
                      label={checkNumberLabel}
                      placeholder={checkNumberLabel}
                      shouldAutoFocus />
                </Div>
            </Div>
            <Navigation onNext={onNext} hidePrevious nextOnEnter />
        </Div>);
    }
}

const onContinue = (dispatch, getState) => {
    dispatch({ type: VALIDATE });
    const { errors } = getState().ui;
    if (errors.length === 0) {
        dispatch({ type: SUBMIT_CHECK_CASHING, dispatch });
        let url = `${window.location.href}`;
        if (url.indexOf("?scanCheckStart") > -1) {
            url = url.replace("?scanCheckStart", "");
            window.open(url, "_self");
        }
    }
}

const mapStateToProps = (state) => {
    return {
        checkNumber: state.checkCashing.voucher.checkNumber,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onCheckNumberChange: (event) => {
            dispatch({ type: FILL_UP_CHECK_NUMBER, target: event.target });
            dispatch({ type: VALIDATE_INPUTS_FORMAT });
        },
        validateOnFirstLoad: () => {
            dispatch({ type: VALIDATE_INPUTS_FORMAT });
        },
        onNext: () => {
            // const printvoucherComponentIndex = 5;
            dispatch(onContinue);
            // dispatch({ type: NEXT_PAGE, currentIndex: printvoucherComponentIndex });
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(CheckNumberInput);



