/* eslint camelcase: 0 */

import { Analytics, RestHost, Logger, Helpers } from "common-functions";

import { PAGE_CLICK } from "../actions";

Analytics.av = __webpack_version__;

let initialized = false;
let previousPage;
let button;

const pagesMapping = new Map();
pagesMapping.set("/", "Welcome");
pagesMapping.set("/login", "Check_Cashing_Login");
pagesMapping.set("/checkCashingOverview", "Check_Cashing_Overview");
pagesMapping.set("/userCheck", "User_Check");
pagesMapping.set("/overview", "Overview");
pagesMapping.set("/soon", "Unimplemented");
pagesMapping.set("/terms", "Terms");
pagesMapping.set("/restricted", "Restricted_Access");
pagesMapping.set("/faq", "Faqs");
pagesMapping.set("/register", getRegisterCustomerPage);
pagesMapping.set("/updateCustomer", getUpdateCustomerPage);
pagesMapping.set("/resetPassword", getResetPasswordPage);
pagesMapping.set("/checkCashing", getCheckCashingPage);


function getRegisterCustomerPage(state) {
    const { pages, currentIndex } = state.customer;
    const page = pages[currentIndex];
    return page ? `Register_Customer_${Helpers.toTitleCase(page.title)}` : "Register_Customer";
}

function getUpdateCustomerPage(state) {
    const { pages, currentIndex } = state.customer;
    const page = pages[currentIndex];
    return page ? `Update_Customer_${Helpers.toTitleCase(page.title)}` : "Update_Customer";
}

function getResetPasswordPage(state) {
    const { pages, currentIndex } = state.resetPassword;
    const page = pages[currentIndex];
    return page ? `Reset_Password_${Helpers.toTitleCase(page.title)}` : "Reset_Password";
}

function getCheckCashingPage(state) {
    const { pages, currentIndex } = state.checkCashing;
    const page = pages[currentIndex];
    return page ? `Check_Cashing_${Helpers.toTitleCase(page.title)}` : "Check_Cashing";
}

const getCurrentPath = (state, action) => {
    if (action && action.payload && action.payload.location) {
        return action.payload.location.name;
    } else if (state.router && state.router.location) {
        return state.router.location.pathname;
    }
    return undefined;
};

const getCurrentPage = (state, action) => {
    const path = getCurrentPath(state, action);
    if (path) {
        const mapping = pagesMapping.get(path);
        const isFunction = typeof mapping === "function";
        return isFunction ? mapping(state) : mapping;
    }
    return undefined;
};

const getAnalyticsAttribute = (x, y) => {
    let element = document.elementFromPoint(x, y);
    while (element && element.tagName !== "BODY") {
        const attribute = element.getAttribute("data-analytics");
        if (attribute) {
            return attribute;
        }
        element = element.parentNode;
    }
    return undefined;
};

const sanitize = (string) => Helpers.replaceSpacesWith(Helpers.toTitleCase(string), "_");

const initAnalyticsConfig = () => {
    if (!initialized) {
        const restHost = new RestHost("resources/config/", null, null);
        restHost.get("analytics.json",
            (response) => Analytics.initialize(response),
            (error) => Logger.error(error),
            RestHost.JSON,
            false,
        );
        initialized = true;
    }
};

const analyze = (state, action) => {
    initAnalyticsConfig();

    const currentPage = getCurrentPage(state, action);
    if (currentPage) {
        let usageDataType;

        const pageChanged = currentPage !== previousPage;
        if (pageChanged) {
            usageDataType = "Page_View";
            previousPage = currentPage;
        }

        if (action.type === PAGE_CLICK) {
            const attribute = getAnalyticsAttribute(action.x, action.y);
            if (attribute) {
                usageDataType = "Button_Click";
                button = attribute;
            }
        }

        setTimeout(() => {
            if (usageDataType) {
                Analytics.page = `${currentPage}_Screen`;
                if (button) {
                    Analytics.button = button;
                    Analytics.pagebutton = `${currentPage}_${sanitize(button)}`;
                }
                Analytics.sendUsageData(usageDataType);
            }
        }, 0);
    }
};

const analyticsMiddleware = (store) => (next) => (action) => {
    const state = store.getState();
    const { config } = state;

    if (config.enableAnalytics) {
        analyze(state, action);
    }
    next(action);
};

export default analyticsMiddleware;



