import { REHYDRATE } from "redux-persist/constants";
import { TOWNS_SUCCESS, TOWNS_FAILURE } from "../actions/codeTable";

const defaultCommonTownsState = { items: {}, lastUpdateDate: null };
const defaultOtherTownsState = { items: {} };

const commonTownsReducer = (state, action) => {
    const commonTowns = state.commonTowns || defaultCommonTownsState;
    const items = commonTowns.items || {};

    switch (action.type) {
        case REHYDRATE: {
            const rehydratedState = action.payload.commonTowns;
            return Object.assign({}, rehydratedState);
        }
        case TOWNS_SUCCESS: {
            if (action.isCommonCountry) {
                items[action.country] = action.response;
                return Object.assign({}, commonTowns, { items, lastUpdateDate: JSON.stringify(new Date()) });
            }
            return commonTowns;
        }
        case TOWNS_FAILURE: {
            if (action.isCommonCountry) {
                return Object.assign({}, commonTowns, { items: [] });
            }
            return commonTowns;
        }
        default:
            return commonTowns;
    }
};

const otherTownsReducer = (state, action) => {
    const otherTowns = state.otherTowns || defaultOtherTownsState;
    const items = otherTowns.items || {};

    switch (action.type) {
        case TOWNS_SUCCESS: {
            if (!action.isCommonCountry) {
                items[action.country] = action.response;
                return Object.assign({}, otherTowns, { items });
            }
            return otherTowns;
        }
        case TOWNS_FAILURE: {
            if (!action.isCommonCountry) {
                return Object.assign({}, otherTowns, { items: [] });
            }
            return otherTowns;
        }
        default:
            return otherTowns;
    }
};

export { commonTownsReducer, otherTownsReducer };



