import React from "react";
import Card from "./Card";
import { P, Span } from "./HTML";

const Overview = ({ onContinue, onCancel, cancelURL, continueURL, items, header, subheader,
      cancelBtnLabel = "overview.cancel.btn", continueBtnLabel = "overview.continue.btn", hideCancel = false }) => {
    const itemsComponent = items.map((item, index) => {
        const text = (typeof item === "string" ? item : item.text);
        const args = (typeof item === "string" ? "" : item.args);
        return (<li key={text}>
            <Span className="badge">{index + 1}</Span>
            <P className="overview-item" args={args}>{text}</P>
        </li>);
    });
    const subheaderComponent = subheader ? <P className="mo-review-subtitle">{subheader}</P> : null;

    return (<div className="overview-page">
        <P className="title">{header}</P>
        {subheaderComponent}
        <ol className="has-badge-panel">{itemsComponent}</ol>

        <div className="actions">
            <Card className="secondary" onClick={onCancel} to={cancelURL} style={{ display: hideCancel ? "none" : "" }} data-analytics={cancelBtnLabel}>{cancelBtnLabel}</Card>
            <Card onClick={onContinue} to={continueURL} data-analytics={continueBtnLabel}>{continueBtnLabel}</Card>
        </div>
    </div>);
};

export default Overview;



