export const TERMS_SHOW = "TERMS_SHOW";
export const TERMS_TOGGLE = "TERMS_TOGGLE";
export const TERMS_AGREE = "TERMS_AGREE";
export const TERMS_DISAGREE = "TERMS_DISAGREE";

export const showTerms = (steps, currentStep, previousRoute) => {
    return { type: TERMS_SHOW, steps, currentStep, previousRoute };
};



