import { CONFIG_SUCCESS, CONFIG_FAILURE } from "../actions/config";

const defaultState = {};

const configReducer = (state, action) => {
    const config = state.config || defaultState;

    switch (action.type) {
        case CONFIG_SUCCESS: {
            return postProcess(action.response);
        }

        case CONFIG_FAILURE:
            return defaultState;

        default:
            return config;
    }
};

const postProcess = (response) => {
    const newConfig = Object.assign({}, response);
    const messageArgs = newConfig.messageArgs || [];
    if (response.checkCashingDefaults && response.checkCashingDefaults.maxAmount) {
        const maxAmount = response.checkCashingDefaults.maxAmount;
        const messageArg = { key: "#MAX_AMOUNT", value: Number(maxAmount).toLocaleString() };
        messageArgs.push(messageArg);
    }
    return Object.assign(newConfig, { messageArgs });
};

export default configReducer;



