import React from "react";
import { connect } from "react-redux";
import { Div, P, InputV2 as Input } from "common-components";
import { Helpers } from "common-functions";

// import Input from "../../components/Input";
import Navigation from "../../components/Navigation";
import { UPDATE_ADDITIONAL_ID, SUBMIT_UPDATE_ADDITIONAL_ID, SHOW_KEYBOARD } from "../../actions/constants";

class AdditionalId extends React.Component {
    componentDidMount() {
        this.props.onLoad();
    }

    render() {
        const { requestData, onChange, onContinue } = this.props;
        return (<Div>
            <Div className="indented-body">
                <P className="add-id-title">additional.secondary.photo.id.label</P>
                <P className="add-id-subtitle">additional.sss.id.label</P>
                <Div className="customer-form">
                    <Div>
                        <Input name="additionalIdNumber" value={requestData.additionalIdNumber} className="col-12"
                          onChange={onChange} placeholder="photo.id.type.ssn" shouldAutoFocus />
                        <P className="additional-id-error">{requestData.errorMessage}</P>
                    </Div>
                </Div>
                <Navigation onNext={onContinue} disablePrevious hasFormatErrors={!requestData.hasValidSSN} hasInput={!requestData.hasValidSSN} />
            </Div>
        </Div>);
    }
}

const mapStateToProps = (state) => {
    return {
        requestData: state.checkCashing.updateCustomerData,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onChange: (event) => {
            let secondaryPhotoIdNumber = event.target.value;
            if (secondaryPhotoIdNumber && String(secondaryPhotoIdNumber).length > 0) {
                secondaryPhotoIdNumber = Helpers.formatStringPattern(secondaryPhotoIdNumber, [3, 2, 4], "-");
            }
            dispatch({ type: UPDATE_ADDITIONAL_ID, additionalIdNumber: secondaryPhotoIdNumber });
        },
        onContinue: () => {
            dispatch({ type: SUBMIT_UPDATE_ADDITIONAL_ID, dispatch });
        },
        onLoad: () => dispatch({ type: SHOW_KEYBOARD }),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(AdditionalId);



