import { Rest, RestHost, MessageArgs } from "common-functions";

export const MESSAGES_REQUEST = "MESSAGES_REQUEST";
export const MESSAGES_SUCCESS = "MESSAGES_SUCCESS";
export const MESSAGES_FAILURE = "MESSAGES_FAILURE";
export const MESSAGES_LANGUAGE_UPDATE = "MESSAGES_LANGUAGE_UPDATE";

export const moneygramMessagesLoader = (language, dispatch, processOnBackground) => {
    dispatch({ type: MESSAGES_REQUEST, processOnBackground });

    const restHost = new RestHost("resources/messages/", null, null);
    const selectedLanguage = language || "en";

    restHost.get(`${selectedLanguage}.properties`,
        (response) => dispatch({ type: MESSAGES_SUCCESS, moneygramMessages: response, selectedLanguage, processOnBackground }),
        (error) => dispatch({ type: MESSAGES_FAILURE, error, processOnBackground }),
        RestHost.PROPERTIES,
        true,
    );
};

const getKeyCode = (errorCodes, moneygramException) => {
    const code = moneygramException.errorCode;
    const subCode = moneygramException.subErrorCode;

    if (errorCodes) {
        for (let i = 0; errorCodes.length > i; i++) {
            const errorCode = errorCodes[i];
            if (String(errorCode.code) === String(code)) {
                if (subCode) {
                    if (String(errorCode.subCode) === String(subCode)) {
                        return errorCode.key;
                    }
                } else {
                    return errorCode.key;
                }
            }
        }
    }
    return null;
};

const getSpecialExceptionMessageKeyCode = (errMsg, newUser) => {
    const err = errMsg.toLowerCase();
    const isDuplicateCustomer = (err.includes("duplicate") && err.includes("validation") && err.includes("customer"))
        || err.includes("customer credential already exists") || err.includes("found multiple (2) objects using query provided")
        || (err.includes("duplicate") && err.includes("credentials"));
    if (err === "question type incorrect." || err === "answer incorrect." || isDuplicateCustomer) {
        if (newUser) {
            return "strBITValidationErr";
        }
        return "strBITValidationErrUpdateCustomer";
    } else if (err.includes("unable to update credentials with registrationretrievalnumber") || err.includes("customer credential does not exist")) {
        return "strInvalidUserid";
    } else if (err.includes("receive checker: dollar amount exceeds receive limit")) {
        return "strBITReceiveLimit";
    }
    return null;
};

const moneygramExceptionHandler = (errMsg, error, getState, retainErrorCodes) => {
    const state = getState();
    const { additionalFieldsRequirement } = state.config;
    const { newUser } = state.customer;

    if (additionalFieldsRequirement) {
        if (additionalFieldsRequirement.find((item) => item.message === errMsg)) {
            return errMsg;
        }
    }

    const errorCodes = state.config.errorCodes ? state.config.errorCodes.moneyGram : [];
    const key = error && error.moneygramException ? getKeyCode(errorCodes, error.moneygramException) : getSpecialExceptionMessageKeyCode(errMsg, newUser);
    if (key) {
        const message = state.moneygramMessages.messages[key];
        if (message) {
            const processedMessage = MessageArgs.replaceVars(message, state.config.messageArgs, state.internationalization.language);
            const processedErrorObj = { ...error, processedMessage, mappedKeyCode: key };
            return retainErrorCodes ? processedErrorObj : processedMessage;
        }
    }

    const processedMessage = MessageArgs.replaceVars(state.moneygramMessages.messages.BITDefault, state.config.messageArgs, state.internationalization.language);
    const processedErrorObj = { ...error, processedMessage };
    return retainErrorCodes ? processedErrorObj : processedMessage;
};

export const restSendHandler = (getState, sendType, restUrl, data, urlParams, successCallback,
    errorCallback, responseType, processOnBackground, retainErrorCodes) => {
    const errorHandler = (errMsg, errObj) => {
        if (getState) {
            errorCallback(moneygramExceptionHandler(errMsg, errObj, getState, retainErrorCodes));
        } else {
            errorCallback(errMsg);
        }
    };
    Rest.send(sendType, restUrl, data, urlParams, successCallback,
          errorHandler, responseType, processOnBackground);
};

export const restPostHandler = (getState, restUrl, data, successCallback, errorCallback, isXml, retainErrorCodes) => {
    const errorHandler = (errMsg, errObj) => {
        if (getState) {
            errorCallback(moneygramExceptionHandler(errMsg, errObj, getState, retainErrorCodes));
        } else {
            errorCallback(errMsg);
        }
    };
    Rest.post(restUrl, data, successCallback, errorHandler, isXml);
};



