import React from "react";
import { push } from "connected-react-router";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Helpers } from "common-functions";
import { Div, InputV2 as Input, PasswordInput, P, Span, Button } from "common-components";

import ViewComponent from "../components/ViewComponent";

import { initiateResetPassword, SHOW_INTERVENTION, HIDE_INTERVENTION, INITIATE_FULL_REGISTER_CUSTOMER } from "../actions";
import { loginRequest, forceChangePassword, LOGIN_SUCCESS, UPDATE_PREVIOUS_PASSWORD } from "../actions/customer";
import { onContinue, customerNotesRestricted } from "../actions/validation";

import { ForceChangePassword } from "./customer/ResetPasswordPages";

class Login extends ViewComponent {

    componentDidMount() {
        this.props.showKeyboard(document.getElementById("userName"));
    }

    renderForChangePassword() {
        const { changeSystemGeneratedPassword } = this.props;
        return <ForceChangePassword onContinue={changeSystemGeneratedPassword} />;
    }

    renderLogin() {
        const { userName, password } = this.state;
        const { loginTitle, loginDescription, resetPassword, lang } = this.props;
        const onLoginClicked = () => {
            const callback = () => this.setState({ forChangePassword: true });
            this.props.login(userName, password, callback);
        };

        return (<div>
            <div className="login-page">
                <P className="title">{loginTitle}</P>
                <P className="description">{loginDescription}</P>

                <div className="form">
                    <Input name="userName"
                      label="login.label.username"
                      value={this.state.userName}
                      onChange={(event) => super.handleChange(event)}
                      parentClassName={`${lang}-input`}
                      shouldAutoFocus />

                    <PasswordInput name="password"
                      label="login.label.password"
                      value={this.state.password}
                      parentClassName={`${lang}-input`}
                      onChange={(event) => super.handleChange(event)} />

                    <Button onClick={onLoginClicked} type="submit" data-analytics="Login">login.btn</Button>
                </div>

                <div className="clearfix" />
                <Link to="/resetPassword" onClick={resetPassword} data-analytics="Reset password">
                    <Span className="password-reset">reset.password.link</Span>
                </Link>
            </div>
        </div>);
    }

    render() {
        if (this.state.forChangePassword) {
            return this.renderForChangePassword();
        }
        return this.renderLogin();
    }
}

const isPasswordSystemGenerated = (password, customer) => {
    if (customer.passwordSystemGenerated) {
        return true;
    }
    const regex = /(MG|mg)\d\d(0[1-9]|1[012])(0[1-9]|[12][0-9]|3[01])/;
    const passwordYear = password.substr(2, 2);
    const year = new Date().getFullYear().toString().substr(2, 2);
    const yearCondition = passwordYear === year || (parseInt(year, 10) - 1) === parseInt(passwordYear, 10);
    const helpdeskGenerated = regex.test(password) && yearCondition;

    return helpdeskGenerated;
};

const changePassword = (successCallback) => (dispatch, getState) => {
    forceChangePassword(dispatch, getState, successCallback);
};

const nextPageCallback = (ownProps) => (dispatch, getState) => {
    const { customer, config } = getState();
    const url = window.location.href;
    const shouldVerify = url.indexOf("checkCashing") >= 0;
    if (shouldVerify && customerNotesRestricted(customer, config)) {
        dispatch(push("/restricted"));
    } else {
        dispatch(ownProps.onLoginAction);
    }
};

const mapStateToProps = (state) => {
    const isUpdatingCustomer = state.router && state.router.location
        && state.router.location.pathname === "/updateCustomer";

    return {
        loginTitle: isUpdatingCustomer ? "login.header.update.customer" : "login.header.default",
        loginDescription: "login.description",
        lang: state.internationalization.language,
    };
};

const mapDispatchToProps = (dispatch, ownProps) => {
    const processOnBackground = false;

    const successCallback = (customerId, response, userName, password, callback) => {
        dispatch({ type: LOGIN_SUCCESS, customerId, response, userName, password, processOnBackground });
        const customer = response.getCustomerResponse.customerData;
        if (isPasswordSystemGenerated(password, customer)) {
            dispatch(initiateResetPassword());
            callback();
        } else {
            dispatch(nextPageCallback(ownProps));
        }
    };

    const login = (userName, password, callback) => {
        const customSuccessCallback = (customerId, response, usr, pword) => {
            successCallback(customerId, response, usr, pword, callback);
        };
        const onValidateSuccess = () => dispatch(loginRequest(userName, password,
            customSuccessCallback, processOnBackground));
        dispatch(onContinue(onValidateSuccess));
    };

    const changeSystemGeneratedPassword = () => {
        const onChangePasswordContinue = () => {
            dispatch(nextPageCallback(ownProps));
            dispatch({ type: HIDE_INTERVENTION });
        };

        const onChangePasswordSuccess = (d, getState, customerCredentialsData) => {
            const { previousCustomerCredentialsData } = getState().customer;
            const { password } = customerCredentialsData;
            const newCustomerCredentialsData = Object.assign({}, previousCustomerCredentialsData, { password });
            const children = (<Div>
                <P className="title">force.password.success.title</P>
                <P className="description">force.password.success.subtitle</P>
                <Button className="small" onClick={onChangePasswordContinue} data-analytics="Continue">continue.btn</Button>
            </Div>);
            dispatch({ type: UPDATE_PREVIOUS_PASSWORD, previousCustomerCredentialsData: newCustomerCredentialsData })
            dispatch({ type: SHOW_INTERVENTION, children });
        };

        dispatch(changePassword(onChangePasswordSuccess));
    };

    const resetPassword = () => dispatch(initiateResetPassword());
    const showKeyboard = (selectedField) => dispatch({ type: "FIELD_FOCUS", selectedField });

    return { login, resetPassword, changeSystemGeneratedPassword, showKeyboard };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);



