import cloneDeep from "lodash.clonedeep";
import React from "react";
import { Helpers } from "common-functions";

import Login from "../containers/Login";
import { Review, RegisterSuccess, UpdateSuccess } from "../containers/customer/SpecialPages";
import { PrimaryPhotoID, SecondaryPhotoID } from "../containers/customer/PhotoIDPages";
import { Name, Email, Mobile, EmailAndMobile, Occupation, Birthdate, BirthdateAndSSN, Birthplace, Country, Address, CustomerQA, Password } from "../containers/customer/Pages";
import { onLoginAction } from "../containers/customer/UpdateCustomer";

import { INITIATE_REGISTER_CUSTOMER, INITIATE_HALF_REGISTER_CUSTOMER, INITIATE_FULL_REGISTER_CUSTOMER,
    INITIATE_UPDATE_CUSTOMER, HIDE_INTERVENTION, RESET_STATE } from "../actions";
import { LOGIN_SUCCESS, LOGIN_FAILURE, UPDATE_CUSTOMER_SUCCESS,
    FILL_UP_REQUEST_DATA, FILL_UP_CREDENTIALS_REQUEST_DATA, FILL_UP_CUSTOMER_QA_REQUEST_DATA,
    CUSTOMER_NEXT_PAGE, CUSTOMER_PREVIOUS_PAGE, CUSTOMER_PAGE_NAVIGATE, PROMOTION_FLAG_TOGGLE,
    EDIT_CUSTOMER_DETAILS, EDIT_CUSTOMER_DETAILS_PAGE_NAVIGATE, UPDATE_PREVIOUS_PASSWORD,
    EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE, REGISTER_CUSTOMER_SUCCESS, validateAnswerDate,
    validateAddress, validateEmail, validateBirthdate, validatePassword, validateOccupution, validateName,
    validatePhotoId, validateSecondaryPhotoId, registerCustomer, updateCustomer,
    validatePhoneNumber, onCustomPhotoIdContinue } from "../actions/customer";

const onShowSecondaryPhotoIdOptionCheck = (dispatch, getState) => {
    const state = getState();
    const { editing, newUser, requestData, pages, currentIndex } = state.customer;
    if (!editing && newUser && !requestData.customerData.secondaryPhotoIdNumber) { // } && allowedSecondaryID) {
        dispatch({ type: HIDE_INTERVENTION });
        dispatch({ type: CUSTOMER_NEXT_PAGE });
    } else {
        const currentPage = pages[currentIndex];
        const atReviewPage = currentPage.title === "review";
        if (editing && !atReviewPage) {
            dispatch({ type: EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE, persist: true });
        } else if (requestData.customerData.secondaryPhotoIdNumber) {
            dispatch(onCustomPhotoIdContinue);
        } else {
            dispatch({ type: CUSTOMER_PAGE_NAVIGATE, pageTitle: "secondaryPhotoId" });
        }
    }
};


const customerPages = [
    { step: "steps.personal", title: "name", component: <Name />, validate: validateName },
    { step: "steps.personal", title: "email", component: <Email />, validate: validateEmail },
    { step: "steps.personal", title: "mobile", component: <Mobile />, validate: validatePhoneNumber },
    { step: "steps.personal", title: "occupation", component: <Occupation />, validate: validateOccupution },
    { step: "steps.personal", title: "birthDate", component: <Birthdate />, validate: validateBirthdate },
    { step: "steps.personal", title: "birthplace", component: <Birthplace /> },
    { step: "steps.residence", title: "country", component: <Country /> },
    { step: "steps.residence", title: "address", component: <Address />, validate: validateAddress },
    { step: "steps.identification", title: "photoId", component: <PrimaryPhotoID />, validate: validatePhotoId, onContinue: onShowSecondaryPhotoIdOptionCheck },
    { step: "steps.personal", title: "secondaryPhotoId", component: <SecondaryPhotoID />, validate: validateSecondaryPhotoId, onContinue: onCustomPhotoIdContinue },
    { step: "steps.security", title: "customerQA", component: <CustomerQA />, validate: validateAnswerDate },
    { step: "steps.security", title: "password", component: <Password />, validate: validatePassword },
];

const halfCustomerPages = [
    { step: "steps.personal", title: "name", component: <Name />, validate: validateName },
    { step: "steps.personal", title: "emailmobile", component: <EmailAndMobile />, validate: [validateEmail, validatePhoneNumber] },
    { step: "steps.personal", title: "birthDateSSN", component: <BirthdateAndSSN />, validate: [validateBirthdate, validateSecondaryPhotoId] },
    { step: "steps.residence", title: "address", component: <Address />, validate: validateAddress },
    { step: "steps.identification", title: "photoId", component: <PrimaryPhotoID />, validate: validatePhotoId, onContinue: onShowSecondaryPhotoIdOptionCheck },
    { step: "steps.security", title: "customerQA", component: <CustomerQA />, validate: validateAnswerDate },
    { step: "steps.security", title: "password", component: <Password />, validate: validatePassword },
    { step: "steps.personal", title: "email", component: <Email pageBadge="emailmobile" />, validate: validateEmail },
    { step: "steps.personal", title: "mobile", component: <Mobile pageBadge="emailmobile" />, validate: validatePhoneNumber },
    { step: "steps.personal", title: "birthDate", component: <Birthdate pageBadge="birthDateSSN" />, validate: validateBirthdate },
    { step: "steps.personal", title: "secondaryPhotoId", component: <SecondaryPhotoID pageBadge="birthDateSSN" />, validate: validateSecondaryPhotoId, onContinue: onCustomPhotoIdContinue },
];

const registerCustomerSteps = ["steps.personal", "steps.residence", "steps.identification", "steps.security", "steps.review"];
const registerCustomerPages = [
    ...customerPages,
    { step: "steps.review", title: "review", component: <Review showSteps />, onContinue: registerCustomer },
    { title: "Sucess", component: <RegisterSuccess /> },
];

const updateCustomerSteps = ["steps.personal", "steps.residence", "steps.identification", "steps.security"];
const updateCustomerPages = [
    { step: "steps.login", title: "login", component: <Login onLoginAction={onLoginAction} /> },
    { step: "steps.review", title: "review", component: <Review />, onContinue: updateCustomer },
    { title: "Sucess", component: <UpdateSuccess /> },
    ...customerPages,
];

const updateHalfCustomerSteps = ["steps.personal", "steps.residence", "steps.identification", "steps.security"];
const updateHalfCustomerPages = [
    { step: "steps.login", title: "login", component: <Login onLoginAction={onLoginAction} /> },
    { step: "steps.review", title: "review", component: <Review />, onContinue: updateCustomer },
    { title: "Sucess", component: <UpdateSuccess /> },
    ...halfCustomerPages,
];

const halfRegisterCustomerSteps = ["steps.personal", "steps.residence", "steps.identification", "steps.security", "steps.review"];
const halfRegisterCustomerPages = [
    { step: "steps.personal", title: "name", component: <Name />, validate: validateName },
    { step: "steps.personal", title: "emailmobile", component: <EmailAndMobile />, validate: [validateEmail, validatePhoneNumber] },
    { step: "steps.personal", title: "birthDateSSN", component: <BirthdateAndSSN />, validate: [validateBirthdate, validateSecondaryPhotoId] },
    { step: "steps.residence", title: "address", component: <Address />, validate: validateAddress },
    { step: "steps.identification", title: "photoId", component: <PrimaryPhotoID />, validate: validatePhotoId },
    { step: "steps.security", title: "customerQA", component: <CustomerQA />, validate: validateAnswerDate },
    { step: "steps.security", title: "password", component: <Password />, validate: validatePassword },
    { step: "steps.review", title: "review", component: <Review showSteps />, onContinue: registerCustomer },
    { title: "Sucess", component: <RegisterSuccess /> },
    { step: "steps.personal", title: "email", component: <Email pageBadge="emailmobile" />, validate: validateEmail },
    { step: "steps.personal", title: "mobile", component: <Mobile pageBadge="emailmobile" />, validate: validatePhoneNumber },
    { step: "steps.personal", title: "birthDate", component: <Birthdate pageBadge="birthDateSSN" />, validate: validateBirthdate },
    { step: "steps.personal", title: "secondaryPhotoId", component: <SecondaryPhotoID pageBadge="birthDateSSN" />, validate: validateSecondaryPhotoId, onContinue: onCustomPhotoIdContinue },
];

const defaultState = {
    requestData: {
        customerData: {
            promotionFlag: false,
            country: "USA"
        },
        customerCredentialsData: {
            customerQA: {},
        },
    },
    currentIndex: 0,
    pages: registerCustomerPages,
    steps: registerCustomerSteps,
    editing: false,
    newUser: false,
    halfRegister: false,
};

const getDisallowedPhotoIdFields = (photoIdKey, customer, state) => {
    let disallowedFields = [];

    const allowedFields = [];
    const photoIdFields = ["Country", "State", "CardType", "ReferenceNumber",
        "IssueDate", "IssueDay", "IssueMonth", "IssueYear", "ExpiryDate", "ExpiryDay", "ExpiryMonth", "ExpiryYear"]
        .map((photoIdField) => `${photoIdKey}${photoIdField}`);

    const addAllowedField = (field) => {
        allowedFields.push(field);
        if (field.endsWith("Date")) {
            allowedFields.push(field.replace("Date", "Day"));
            allowedFields.push(field.replace("Date", "Month"));
            allowedFields.push(field.replace("Date", "Year"));
        }
    };

    const { states, ausStates } = state.codeTable;
    const photoIdTypes = state.config.photoIdOptions[`${photoIdKey}Types`];

    const { countryTypeMappings } = state.config.photoIdOptions;

    const getMappedCountry = (type) => {
        const countryTypeMapping = countryTypeMappings.find((item) => item.type === type);
        return countryTypeMapping ? countryTypeMapping.country : null;
    };

    const photoIdType = customer[`${photoIdKey}Type`];
    if (photoIdType) {
        const photoIdState = customer[`${photoIdKey}State`];
        const mapping = photoIdTypes.find((idType) => idType.value === photoIdType);

        if (mapping && mapping.fields && mapping.fields.length) {
            mapping.fields.forEach((item) => addAllowedField(`${photoIdKey}${item}`));

            if (mapping.stateFieldMappings && photoIdState) {
                const stateFieldMapping = mapping.stateFieldMappings.find((item) => item.state === photoIdState);
                if (stateFieldMapping) {
                    addAllowedField(`${photoIdKey}${stateFieldMapping.field}`);
                }
            }

            if (allowedFields.includes(`${photoIdKey}Country`)) {
                const photoIdCountry = getMappedCountry(photoIdType) || customer[`${photoIdKey}Country`];
                const countryStates = photoIdCountry === "AUS" ? ausStates : states.filter((item) => item.countryCode === photoIdCountry);

                if (countryStates.length !== 0) {
                    addAllowedField(`${photoIdKey}State`);
                }
            }
        }
        disallowedFields = photoIdFields.filter((field) => !allowedFields.includes(field));
    }

    return disallowedFields;
};

const getUpdatedCustomerUsingPhotoId = (forPrimary, customer, state) => {
    const photoIdKey = forPrimary ? "photoId" : "secondaryPhotoId";
    const updatedCustomer = customer;

    const photoIdNumber = updatedCustomer[`${photoIdKey}Number`];
    const photoIdTypes = state.config.photoIdOptions[`${photoIdKey}Types`];
    if (photoIdTypes && photoIdTypes.length === 1) {
        updatedCustomer[`${photoIdKey}Type`] = photoIdNumber ? photoIdTypes[0].value : null;
    }

    const photoIdExpiryYear = updatedCustomer[`${photoIdKey}ExpiryYear`];
    const photoIdExpiryMonth = updatedCustomer[`${photoIdKey}ExpiryMonth`];
    const photoIdExpiryDay = updatedCustomer[`${photoIdKey}ExpiryDay`];

    if (Helpers.isDateValid(photoIdExpiryYear, photoIdExpiryMonth, photoIdExpiryDay)) {
        updatedCustomer[`${photoIdKey}ExpiryDate`] = `${photoIdExpiryYear}-${parseInt(photoIdExpiryMonth, 10) + 1}-${photoIdExpiryDay}`;
    }

    if (!photoIdExpiryYear && !photoIdExpiryMonth && !photoIdExpiryDay) {
        updatedCustomer[`${photoIdKey}ExpiryDate`] = null;
    }

    const photoIdIssueYear = updatedCustomer[`${photoIdKey}IssueYear`];
    const photoIdIssueMonth = updatedCustomer[`${photoIdKey}IssueMonth`];
    const photoIdIssueDay = updatedCustomer[`${photoIdKey}IssueDay`];

    if (Helpers.isDateValid(photoIdIssueYear, photoIdIssueMonth, photoIdIssueDay)) {
        updatedCustomer[`${photoIdKey}IssueDate`] = `${photoIdIssueYear}-${parseInt(photoIdIssueMonth, 10) + 1}-${photoIdIssueDay}`;
    }

    if (!photoIdIssueYear && !photoIdIssueMonth && !photoIdIssueDay) {
        updatedCustomer[`${photoIdKey}IssueDate`] = null;
    }

    const disallowedFields = getDisallowedPhotoIdFields(photoIdKey, customer, state);
    disallowedFields.forEach((field) => delete updatedCustomer[field]);
    return customer;
};

const getUpdatedCustomer = (customer, state) => {
    let newCustomerData = customer.requestData.customerData;
    newCustomerData = getUpdatedCustomerUsingPhotoId(true, newCustomerData, state);
    newCustomerData = getUpdatedCustomerUsingPhotoId(false, newCustomerData, state);
    Object.assign(customer.requestData.customerData, newCustomerData);
    return customer;
};

const getUpdatedCustomerDates = (customer) => {
    const customerData = customer;
    const { birthDate, photoIdExpiryDate, photoIdIssueDate, secondaryPhotoIdExpiryDate, secondaryPhotoIdIssueDate } = customerData;

    if (birthDate) {
        const date = Helpers.parseDate(birthDate);
        customerData.birthDay = date.getDate();
        customerData.birthMonth = date.getMonth();
        customerData.birthYear = date.getFullYear();
    }

    if (photoIdExpiryDate) {
        const date = Helpers.parseDate(photoIdExpiryDate);
        customerData.photoIdExpiryDay = date.getDate();
        customerData.photoIdExpiryMonth = date.getMonth();
        customerData.photoIdExpiryYear = date.getFullYear();
    }

    if (photoIdIssueDate) {
        const date = Helpers.parseDate(photoIdIssueDate);
        customerData.photoIdIssueDay = date.getDate();
        customerData.photoIdIssueMonth = date.getMonth();
        customerData.photoIdIssueYear = date.getFullYear();
    }

    if (secondaryPhotoIdExpiryDate) {
        const date = Helpers.parseDate(secondaryPhotoIdExpiryDate);
        customerData.secondaryPhotoIdExpiryDay = date.getDate();
        customerData.secondaryPhotoIdExpiryMonth = date.getMonth();
        customerData.secondaryPhotoIdExpiryYear = date.getFullYear();
    }

    if (secondaryPhotoIdIssueDate) {
        const date = Helpers.parseDate(secondaryPhotoIdIssueDate);
        customerData.secondaryPhotoIdIssueDay = date.getDate();
        customerData.secondaryPhotoIdIssueMonth = date.getMonth();
        customerData.secondaryPhotoIdIssueYear = date.getFullYear();
    }
    return customerData;
};

const customerReducer = (state, action) => {
    const customer = state.customer || defaultState;
    let nextCustomer = cloneDeep(customer);

    switch (action.type) {
        case FILL_UP_REQUEST_DATA: {
            Object.assign(nextCustomer.requestData.customerData, action.customerData);
            nextCustomer.pages[nextCustomer.currentIndex].hasChanged = true;
            return getUpdatedCustomer(nextCustomer, state);
        }

        case FILL_UP_CUSTOMER_QA_REQUEST_DATA: {
            Object.assign(nextCustomer.requestData.customerCredentialsData.customerQA, action.customerQA);
            nextCustomer.pages[nextCustomer.currentIndex].hasChanged = true;
            return nextCustomer;
        }

        case FILL_UP_CREDENTIALS_REQUEST_DATA: {
            Object.assign(nextCustomer.requestData.customerCredentialsData, action.customerCredentialsData);
            nextCustomer.pages[nextCustomer.currentIndex].hasChanged = true;
            return nextCustomer;
        }

        case CUSTOMER_NEXT_PAGE: {
            nextCustomer.currentIndex += 1;
            return nextCustomer;
        }

        case CUSTOMER_PREVIOUS_PAGE: {
            nextCustomer.currentIndex -= 1;
            if (action.disableEditing) {
                return Object.assign(nextCustomer, { editing: false });
            }
            return nextCustomer;
        }

        case CUSTOMER_PAGE_NAVIGATE: {
            return Object.assign(nextCustomer, {
                currentIndex: customer.pages.findIndex((page) => page.title === action.pageTitle),
            });
        }

        case EDIT_CUSTOMER_DETAILS:
            return Object.assign(nextCustomer, { editing: true });

        case EDIT_CUSTOMER_DETAILS_PAGE_NAVIGATE:
            return Object.assign(nextCustomer, {
                currentIndex: customer.pages.findIndex((page) => page.title === action.pageTitle),
                requestDataCopy: cloneDeep(customer.requestData),
            });

        case EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE: {
            let requestData = customer.requestData;
            if (!action.persist) {
                requestData = customer.requestDataCopy || customer.requestData;
            }
            return Object.assign(nextCustomer, {
                currentIndex: customer.pages.findIndex((page) => page.title === "review"),
                requestData,
            });
        }

        case PROMOTION_FLAG_TOGGLE: {
            const promotionFlag = nextCustomer.requestData.customerData.promotionFlag;
            nextCustomer.requestData.customerData.promotionFlag = !promotionFlag;
            return nextCustomer;
        }

        case REGISTER_CUSTOMER_SUCCESS: {
            const { registerCustomerResponse } = action.response;
            return Object.assign(nextCustomer, { registerCustomerResponse });
        }

        case LOGIN_SUCCESS: {
            const { customerData, customerQA } = action.response.getCustomerResponse;
            const { photoIdType, secondaryPhotoIdType, photoIdCountry, secondaryPhotoIdCountry,
                customerId, secondaryPhotoIdNumber, postcode, email } = customerData;

            const defaultCountry = state.config.defaultValues.country;
            const defaultPostcode = state.config.defaultValues.postcode;
            const defaultEmail = state.config.defaultValues.email;
            const defaultDriversLicenseType = state.config.defaultValues.driversLicenseType;
            const allowedCountryCodes = state.config.allowedCountryCodes;
            const countries = state.codeTable.countries;

            customerData.previousSelectedCountry = customerData.country;

            if (String(postcode) === String(defaultPostcode)) {
                customerData.postcode = null;
            }

            if (String(email) === String(defaultEmail)) {
                customerData.email = null;
            }

            if (photoIdType === "DRV") {
                customerData.photoIdType = photoIdCountry === defaultCountry ? defaultDriversLicenseType : "FDRV";
            }

            if (photoIdCountry && !allowedCountryCodes.includes(photoIdCountry)) {
                const country = countries.find((item) => item.value === photoIdCountry);
                customerData.photoIdCountry = country ? country.name : photoIdCountry;
            }

            if (secondaryPhotoIdType === "DRV") {
                customerData.secondaryPhotoIdType = secondaryPhotoIdCountry === defaultCountry ? defaultDriversLicenseType : "FDRV";
            } else if (secondaryPhotoIdType === "SSN") {
                customerData.secondaryPhotoIdNumber = Helpers.formatStringPattern(secondaryPhotoIdNumber, [3, 2, 4], "-");
            }

            if (customerId) {
                customerData.customerId = action.customerId;
            }

            if (customerQA.questionType === "DOB") {
                const date = Helpers.parseDate(customerQA.answer);
                if (date) {
                    customerQA.answerDay = date.getDate();
                    customerQA.answerMonth = date.getMonth();
                    customerQA.answerYear = date.getFullYear();
                }
            }

            if (Helpers.isHalfRegistered(customerData)) {
                customerData.occupation = null;
                customerData.birthCountry = null;
                customerData.birthTown = null;
            }

            getUpdatedCustomerDates(customerData);
            const customerCredentialsData = { registrationRetrievalNumber: action.userName, customerQA };
            Object.assign(nextCustomer.requestData, { customerCredentialsData, customerData });

            nextCustomer.id = action.customerId;
            nextCustomer.previousCustomerCredentialsData = cloneDeep(customerCredentialsData);
            nextCustomer.previousCustomerCredentialsData.password = action.password;
            nextCustomer.previousCustomerData = cloneDeep(customerData);
            return nextCustomer;
        }

        case UPDATE_CUSTOMER_SUCCESS: {
            nextCustomer.previousCustomerData = cloneDeep(action.customerData);
            return nextCustomer;
        }

        case UPDATE_PREVIOUS_PASSWORD: {
            nextCustomer.previousCustomerCredentialsData = cloneDeep(action.previousCustomerCredentialsData);
            return nextCustomer;
        }

        case LOGIN_FAILURE: {
            Object.assign(nextCustomer, { id: null, customerData: null, customerCredentialsData: null, previousCustomerCredentialsData: null });
            return nextCustomer;
        }

        case INITIATE_REGISTER_CUSTOMER: {
            nextCustomer = cloneDeep(defaultState);
            nextCustomer.pages = registerCustomerPages;
            nextCustomer.steps = registerCustomerSteps;
            nextCustomer.newUser = true;
            nextCustomer.editing = false;
            return nextCustomer;
        }

        case INITIATE_HALF_REGISTER_CUSTOMER: {
            nextCustomer = cloneDeep(defaultState);
            nextCustomer.pages = halfRegisterCustomerPages;
            nextCustomer.steps = halfRegisterCustomerSteps;
            nextCustomer.newUser = true;
            nextCustomer.halfRegister = true;
            nextCustomer.editing = false;
            return nextCustomer;
        }

        case INITIATE_UPDATE_CUSTOMER: {
            nextCustomer = (action.hasLogin) ? nextCustomer : cloneDeep(defaultState);
            nextCustomer.pages = updateHalfCustomerPages;
            nextCustomer.steps = updateHalfCustomerSteps;
            nextCustomer.newUser = false;
            nextCustomer.afterUpdate = action.afterUpdate;
            nextCustomer.afterUpdateUrl = action.afterUpdateUrl;
            nextCustomer.hasLogin = action.hasLogin;
            return nextCustomer;
        }

        case INITIATE_FULL_REGISTER_CUSTOMER: {
            nextCustomer.pages = updateCustomerPages;
            nextCustomer.steps = updateCustomerSteps;
            nextCustomer.newUser = false;
            nextCustomer.editing = true;
            nextCustomer.currentIndex = 1;
            nextCustomer.afterUpdate = action.afterUpdate;
            nextCustomer.prevUrl = action.prevUrl;
            return nextCustomer;
        }

        case RESET_STATE:
            return defaultState;

        default:
            return customer;
    }
};

export default customerReducer;



