import { FIELD_FOCUS, KEYPRESS, BACKSPACE, CLEAR, ENTER, SHOW_KEYBOARD, HIDE_KEYBOARD,
    onKeypress, onBackspace, onClear, onEnter } from "../actions/keyboardAction";

const createKeyboardReducer = (initialState) => {
    const defaultState = initialState || {
        show: false,
        selectedField: null,
        selectedFieldValue: null,
        allowedNumbers: true,
        allowedLetters: true,
        allowedTopLevelDomain: true,
        allowedSpecialCharacters: null,
        australiaOnly: false,
    };

    return (state, action) => {
        const keyboard = state.keyboard || defaultState;
        const defaultCountry = state.config && state.config.defaultValues && state.config.defaultValues.country;
        const isNextPage = action.type.indexOf("NEXT_PAGE") !== -1;
        const isPreviousPage = action.type.indexOf("PREVIOUS_PAGE") !== -1;
        const isNavigate = action.type.indexOf("PAGE_NAVIGATE") !== -1;
        const isPageChange = isNextPage || isPreviousPage || isNavigate;
        if (isPageChange) {
            return Object.assign({}, keyboard, { show: false });
        }

        switch (action.type) {
        case FIELD_FOCUS: {
            const type = action.selectedField.getAttribute("data-type");
            const allowedSpecialCharacters = action.selectedField.getAttribute("data-allowed-special-characters");

            const { allowFallbackValues } = defaultState;

            const getAllowedLettersFallbackValue = () => allowFallbackValues && !type ? keyboard.allowedLetters : null;
            const getAllowedNumbersFallbackValue = () => allowFallbackValues && !type ? keyboard.allowedNumbers : null;
            const getAllowedSpecialCharsFallbackValue = () => allowFallbackValues && !type ? keyboard.allowedSpecialCharacters : null;

            const allowedLetters = action.selectedField.getAttribute("data-allowed-letters");
            const attributeAllowedLetters = JSON.parse(allowedLetters);
            const typeAllowedLetters = type === "RRN" || type === "EMAIL" || type === "TEXT" || type === "ALPHANUMERIC" || getAllowedLettersFallbackValue();

            const allowedNumbers = action.selectedField.getAttribute("data-allowed-numbers");
            const attributeAllowedNumbers = JSON.parse(allowedNumbers);
            const typeAllowedNumbers = type === "RRN" || type === "EMAIL" || type === "NUMERIC" || type === "ALPHANUMERIC" || getAllowedNumbersFallbackValue();

            const nullOrUndefined = (value) => value === undefined || value === null;
            const unMapped = nullOrUndefined(type) && nullOrUndefined(allowedLetters)
                && nullOrUndefined(allowedNumbers) && nullOrUndefined(allowedSpecialCharacters);

            return Object.assign({}, keyboard, {
                show: true,
                selectedField: action.selectedField,
                selectedFieldValue: action.selectedField.value,
                allowedLetters: allowedLetters ? attributeAllowedLetters : typeAllowedLetters,
                allowedNumbers: allowedNumbers ? attributeAllowedNumbers : typeAllowedNumbers,
                allowedTopLevelDomain: type === "RRN" || type === "EMAIL",
                allowedSpecialCharacters: allowedSpecialCharacters || getAllowedSpecialCharsFallbackValue(),
                unMapped: unMapped && !allowFallbackValues,
                australiaOnly: defaultCountry === "AUS",
            });
        }

        case SHOW_KEYBOARD:
            return Object.assign({}, keyboard, { show: true });

        case HIDE_KEYBOARD:
            return Object.assign({}, keyboard, { show: false });

        case KEYPRESS:
            return onKeypress(action.key, keyboard);

        case BACKSPACE:
            return onBackspace(keyboard);

        case CLEAR:
            return onClear(keyboard);

        case ENTER:
            return onEnter(keyboard);

        default:
            return keyboard;
        }
    };
};

const keyboardReducer = createKeyboardReducer(null);
export { keyboardReducer, createKeyboardReducer };



