import { INTERNATIONALIZATION_SUCCESS, INTERNATIONALIZATION_FAILURE } from "../actions/internationalization";


const defaultState = {
    translation: {},
    language: null,
    languages: [
      { name: "English", value: "en" },
      { name: "Español", value: "es" },
    ],
};

const internationalizationReducer = (state, action) => {
    const i18n = state.internationalization || defaultState;
    switch (action.type) {
    case INTERNATIONALIZATION_SUCCESS:
        return Object.assign({}, i18n, {
            language: action.selectedLanguage,
            translation: action.translation,
        });

    case INTERNATIONALIZATION_FAILURE:
        return Object.assign({}, defaultState);

    default:
        return i18n;
    }
};

export default internationalizationReducer;



