import React from "react";
import { connect } from "react-redux";
import { push } from "connected-react-router";
import { Helpers } from "common-functions";
import { Panel } from "common-components";

import TermsLink from "../../components/TermsLink";
import { RESTRICT_CUSTOMER_TRANSACTION } from "../../actions/constants";
import { EDIT_CUSTOMER_DETAILS, EDIT_CUSTOMER_DETAILS_PAGE_NAVIGATE, PROMOTION_FLAG_TOGGLE,
    onContinue as customerContinue, onPrevious as customerPrevious, SDN_DATA_MATCH } from "../../actions/customer";

import Steps from "../../components/Steps";
import Navigation from "../../components/Navigation";

import Checkbox from "../../components/Checkbox";
import OverviewPage from "../../components/Overview";
import { Div, P, Span, Button } from "../../components/HTML";

export const overviewHeader = "four.steps.to.money.order.header";

export const overviewItems = [
    "new.user.four.steps.to.money.order.step1",
    "new.user.four.steps.to.money.order.step2",
    "new.user.four.steps.to.money.order.step3",
    "new.user.four.steps.to.money.order.step4",
];

const getPersonalItems = (halfRegister, customer, countries, defaultValues) => {
    const { firstName, lastName, middleInitial, secondLastName, birthCountry, birthTown,
      birthDay, birthMonth, birthYear, email, phone, occupation } = customer;
    const { displayDateFormat } = defaultValues;

    const customerMiddleInitial = middleInitial ? ` ${middleInitial}` : "";
    const customerSecondLastName = secondLastName ? ` ${secondLastName}` : "";
    const name = `${firstName}${customerMiddleInitial} ${lastName}${customerSecondLastName}`;

    let birthLocation = birthTown ? `${birthTown}, ` : "";
    if (birthCountry) {
        const country = countries.find((item) => item.value === birthCountry);
        birthLocation += country ? country.name : "";
    }

    const isBirthDateValid = Helpers.isDateValid(birthYear, birthMonth, birthDay);
    const birthDateString = `${birthYear}-${parseInt(birthMonth, 10) + 1}-${birthDay}`;
    const birthDate = isBirthDateValid ? Helpers.formatDate(birthDateString, displayDateFormat) : null;
    return [
        { label: "review.label.name", value: firstName && lastName ? name : null, pageTitle: "name" },
        { label: "review.label.email.address", value: email, pageTitle: "email" },
        { label: "review.label.mobile.number", value: phone, pageTitle: "mobile" },
        { label: "review.label.birthday", value: birthDate, pageTitle: "birthDate" },
    ];
    // if (halfRegister) {
    //     return items;
    // }

    // return [
    //     ...items,
    //     { label: "review.label.birthplace", value: birthLocation, pageTitle: "birthplace" },
    //     { label: "review.label.occupation", value: occupation, pageTitle: "occupation" },
    // ];
};

const getAddressItems = (customer, countries, states, ausStates) => {
    const addressItems = [
        { label: "review.label.address", value: customer.address1 },
        { label: "review.label.town", value: customer.town },
    ];

    if (customer.state) {
        const customerStates = customer.country === "AUS" ? ausStates : states;
        const stateResidence = customerStates.find((state) => state.value === customer.state);
        addressItems.push({ label: "review.label.state", value: stateResidence.name });
    }

    if (customer.postcode) {
        addressItems.push({ label: "review.label.postal.code", value: customer.postcode });
    }
    return addressItems;
};

const addOptionalPhotoIdFields = (customer, idType, photoIdTypes, countries, states, ausStates, defaultValues, forPrimary) => {
    const { displayDateFormat } = defaultValues;

    const items = [];
    const photoIdTypeField = forPrimary ? "photoIdType" : "secondaryPhotoIdType";
    const allowedFields = photoIdTypes.find((photoIdType) => photoIdType.value === customer[photoIdTypeField]).fields;
    const mapping = photoIdTypes.find((type) => type.value === idType.value);
    const disableStates = mapping && mapping.fields && !mapping.fields.find((field) => field === "State");

    const photoIdCountryLabel = forPrimary ? "review.label.id.country" : "review.label.secondary.id.country";
    const photoIdCountryField = forPrimary ? "photoIdCountry" : "secondaryPhotoIdCountry";

    if (allowedFields && allowedFields.includes("Country") && customer[photoIdCountryField]) {
        const photoIdCountry = countries.find((country) => country.value === customer[photoIdCountryField]);
        const photoIdCountryValue = photoIdCountry ? photoIdCountry.name : customer[photoIdCountryField];
        items.push({ label: photoIdCountryLabel, value: photoIdCountryValue });
    }

    const idStates = customer[photoIdCountryField] === "AUS" ? ausStates : states;
    const displayStates = idStates != null && idStates.length > 0 && !disableStates;

    const photoIdStateLabel = forPrimary ? "review.label.id.state" : "review.label.secondary.id.state";
    const photoIdStateField = forPrimary ? "photoIdState" : "secondaryPhotoIdState";
    const locationOnAllowedFields = allowedFields && (allowedFields.includes("State") || allowedFields.includes("Country"));

    if (displayStates && locationOnAllowedFields && customer[photoIdStateField]) {
        const photoIdState = idStates.find((state) => state.value === customer[photoIdStateField]);
        if (photoIdState) {
            items.push({ label: photoIdStateLabel, value: photoIdState.name });
        }
    }

    const expiryDateLabel = forPrimary ? "review.label.id.expiry.date" : "review.label.secondary.id.expiry.date";
    const expiryDateField = forPrimary ? "photoIdExpiryDate" : "secondaryPhotoIdExpiryDate";
    if (customer[expiryDateField]) {
        items.push({ label: expiryDateLabel, value: Helpers.formatDate(customer[expiryDateField], displayDateFormat) });
    }

    const issueDateLabel = forPrimary ? "review.label.id.issue.date" : "review.label.secondary.id.issue.date";
    const issueDateField = forPrimary ? "photoIdIssueDate" : "secondaryPhotoIdIssueDate";
    if (customer[issueDateField]) {
        items.push({ label: issueDateLabel, value: Helpers.formatDate(customer[issueDateField], displayDateFormat) });
    }

    const cardTypeField = forPrimary ? "cardType" : "secondaryCardType";
    const photoIdReferenceNumberField = forPrimary ? "photoIdReferenceNumber" : "secondaryPhotoIdReferenceNumber";

    if (customer[cardTypeField]) {
        const cardType = photoIdTypes.find((item) => item.value === customer[cardTypeField]).name;
        items.push({ label: "review.label.card.type", value: cardType });
        items.push({ label: "review.label.reference.number", value: customer[photoIdReferenceNumberField] });
    }
    return items;
};

const getPhotoIdItems = (customer, photoIdTypes, countries, states, ausStates, defaultValues) => {
    const idType = photoIdTypes.find((item) => item.value === customer.photoIdType);

    let idTypeName = null;
    let idTypeNumber = null;

    if (idType) {
        idTypeName = idType.name;
        idTypeNumber = customer.photoIdNumber;
    }

    const photoIdItems = [
        { label: "review.label.id.type", value: idTypeName },
        { label: "review.label.id.number", value: idTypeNumber },
    ];

    if (idType) {
        const photoIdFields = addOptionalPhotoIdFields(customer, idType, photoIdTypes, countries, states, ausStates, defaultValues, true);
        return photoIdItems.concat(photoIdFields);
    }
    return photoIdItems;
};

const getSecondaryPhotoIdItems = (customer, secondaryPhotoIdTypes, countries, states, ausStates, defaultValues) => {
    let secondaryPhotoIdItems = [];
    let photoIdFields = [];

    if (customer.secondaryPhotoIdNumber) {
        const secondaryIdType = secondaryPhotoIdTypes.find((item) => item.value === customer.secondaryPhotoIdType);

        let secondaryIdTypeName = null;
        let secondaryIdTypeNumber = null;

        if (secondaryIdType) {
            secondaryIdTypeName = secondaryIdType.name;
            secondaryIdTypeNumber = customer.secondaryPhotoIdNumber;
        }

        secondaryPhotoIdItems = [
            { label: "review.label.secondary.id.type", value: secondaryIdTypeName },
            { label: "review.label.secondary.id.number", value: secondaryIdTypeNumber },
        ];

        if (secondaryIdType) {
            photoIdFields = addOptionalPhotoIdFields(customer, secondaryIdType, secondaryPhotoIdTypes, countries, states, ausStates, defaultValues, false);
        }
    } else {
        photoIdFields = [{ label: "review.label.secondary.id", pageTitle: "secondaryPhotoId", allowAdd: true }];
    }
    return secondaryPhotoIdItems.concat(photoIdFields);
};

const getCustomerQAItems = (customerCredentials, securityQuestions, defaultValues) => {
    const { displayDateFormat } = defaultValues;
    const { questionType, answer, answerDay, answerMonth, answerYear } = customerCredentials.customerQA;
    const questionName = securityQuestions.find((item) => item.value === questionType).name;

    const isQuestionTypeDOB = questionType === "DOB";
    const answerDate = `${answerYear}-${parseInt(answerMonth, 10) + 1}-${answerDay}`;

    return [{
        label: "review.label.security.question",
        value: questionName,
    }, {
        label: "review.label.security.answer",
        value: isQuestionTypeDOB ? Helpers.formatDate(answerDate, displayDateFormat) : answer,
    }];
};

const getPasswordItem = (customerCredentials) => {
    const password = customerCredentials.password;
    return [{
        label: "review.label.password",
        value: password ? Array(password.length + 1).join("* ") : null,
    }];
};


const renderItem = (item, index, editing, onEditItem) => {
    const actionLabel = item.allowAdd ? "add.btn" : "edit.btn";
    const actionClassName = item.allowAdd ? "review-item-action add" : "review-item-action edit";

    const actionComponent = <Span onClick={() => onEditItem(item.pageTitle)} className={actionClassName}>{actionLabel}</Span>;
    return (<Div key={index} className="review-item-row">
        <P className="review-item-name">{item.label}</P>
        <Div className="review-item-value-wrapper">
            <Span className="review-item-value">{item.value || "-"}</Span>
            {editing && item.pageTitle ? actionComponent : null}
            <Div className="clearfix"></Div>
        </Div>
    </Div>);
};

const ReviewPage = (props) => {
    const { customer, codeTable, config, onEdit, onEditItem, onPromotionFlagToggle,
        steps, currentStep, onContinue, onPrevious, showSteps } = props;
    const { requestData, editing, newUser, pages, currentIndex, halfRegister } = customer;
    const { customerData, customerCredentialsData } = requestData;
    const { countries, states, ausStates } = codeTable;
    const { securityQuestions, photoIdOptions, defaultValues } = config;
    const { photoIdTypes, secondaryPhotoIdTypes } = photoIdOptions;

    const createDivision = (items, pageTitle, editLabel) => {
        if (items && items.length) {
            const editComponent = (<Div className="edit-group">
                <Span onClick={() => onEditItem(pageTitle)} className="review-item-action edit" data-analytics="Edit">{editLabel}</Span>
            </Div>);

            const components = items.map((item, index) => renderItem(item, index, editing, onEditItem));
            return (<Div className="division">
                {editing && pageTitle ? editComponent : null}
                {components}
                <Div className="clearfix"></Div>
            </Div>);
        }
        return null;
    };

    const customPrevious = () => {
         const atReviewPage = pages[currentIndex].title === "review";
         onPrevious(atReviewPage);
    }

    const personalItemsComponent = createDivision(getPersonalItems(halfRegister, customerData, countries, defaultValues));

    const addressItems = getAddressItems(customerData, countries, states, ausStates);
    const addressItemsComponent =  createDivision(addressItems, "address", "edit.group.address");

    const photoIdItems = getPhotoIdItems(customerData, photoIdTypes, countries, states, ausStates, defaultValues);
    const photoIdItemsComponent = createDivision(photoIdItems, "photoId", "edit.group.photo.id");

    const secondaryPhotoIdItems = getSecondaryPhotoIdItems(customerData, secondaryPhotoIdTypes, countries, states, ausStates, defaultValues);
    const secondaryPhotoIdEditPageTitle = customerData.secondaryPhotoIdNumber ? "secondaryPhotoId" : null;
    const secondaryPhotoIdItemsComponent = createDivision(secondaryPhotoIdItems, secondaryPhotoIdEditPageTitle, "edit.group.secondary.photo.id");

    const customerQAItems = getCustomerQAItems(customerCredentialsData, securityQuestions, defaultValues);
    const customerQAItemsComponent = createDivision(customerQAItems, "customerQA", "edit.group.security.question");

    const passwordItem = getPasswordItem(customerCredentialsData);
    const passwordItemComponent = createDivision(passwordItem, "password", "edit.group.password");

    const termsAgreementComponent = (<Div>
        <TermsLink steps={steps}
          currentStep={currentStep}
          onReturnRoute={customer.newUser ? "/register" : "/updateCustomer"} />
        <Div className="promotion-flag">
            <Checkbox name="customerPromotionFlag"
              label="promotions.agreement"
              checked={customerData.promotionFlag}
              onChange={onPromotionFlagToggle} />
        </Div>
    </Div>);

    return (<Div>
        {showSteps ? <Steps items={steps} currentStep={currentStep} /> : null}
        <Div className="register-customer">
            <Div className="review">
                <P className="title">register.customer.review.details</P>

                <Panel updateOnMount>
                    <Div className="items">
                        {personalItemsComponent}
                        {addressItemsComponent}
                        {photoIdItemsComponent}
                        {secondaryPhotoIdItemsComponent}
                        {customerQAItemsComponent}
                        {passwordItemComponent}
                    </Div>
                </Panel>
            </Div>

            {customer.newUser ? termsAgreementComponent : null}

            <Div className="actions fixed-bottom">
                <Button className="small secondary" onClick={onEdit} data-analytics="Edit details">edit.details.btn</Button>
                <Button className="small" onClick={onContinue} data-analytics="Looks good">looks.good.btn</Button>
            </Div>
        </Div>
        <Navigation onPrevious={customPrevious} disableNext />
    </Div>);
};

const RegisterSuccessPage = ({ customer, onAfterRegister }) => {
    const onContinuePath = "/checkCashing";
    const firstName = customer.requestData.customerData.firstName;
    const registeredUsername = customer.registerCustomerResponse.type === "PHONE" ?
        "register.customer.success.mobile.number.label" : "register.customer.success.email.label";
    const customerId = customer.registerCustomerResponse.customerIdStr.replace("L", "");

    return (<Div>
        <img className="page-background"
          src="resources/images/background-success.png"
          alt="Registration success page background" />

        <Div className="registration-success-page">
            <P className="title" args={Helpers.toTitleCase(firstName)}>register.customer.success.title</P>
            <P className="subtitle" args={registeredUsername}>register.customer.success.subtitle</P>

            <Div className="actions">
                <Button className="small" to={onContinuePath} onClick={() => onAfterRegister(customerId, customer.requestData.customerData, customer.registerCustomerResponse)} data-analytics="Continue">continue.btn</Button>
            </Div>
        </Div>
    </Div>);
};

const UpdateSuccessPage = ({ customer, hasLogin, onAfterUpdate }) => {
    const afterUpdate = customer.afterUpdate;
    const afterUpdateUrl = customer.afterUpdateUrl || "/checkCashing";
    return (<Div>
        <img className="page-background"
          src="resources/images/background-success.png"
          alt="Update customer success page background" />

        <Div className="update-success-page">
            <P className="title">update.customer.success.title</P>
            <Div className="actions">
                { afterUpdate ? <Button className="small" onClick={() => onAfterUpdate(afterUpdate, afterUpdateUrl)} data-analytics="Continue">continue.btn</Button>
                : <Button className="small" to={hasLogin ? "/checkCashing" : "/"} data-analytics="Continue">continue.btn</Button>}
            </Div>
        </Div>
    </Div>);
};

const mapStateToProps = (state) => {
    const { config, codeTable, customer, ui } = state;
    const { pages, steps, currentIndex, hasLogin } = customer;
    const page = pages[currentIndex];
    const currentStep = page.step;

    return {
        config,
        codeTable,
        customer,
        steps,
        currentStep,
        hasLogin,
        items: overviewItems,
        header: overviewHeader,
        sendingMoney: ui.sendingMoney,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onContinue: () => dispatch(customerContinue),
        onPrevious: (disableEditing) => dispatch(customerPrevious(disableEditing)),
        onEdit: () => dispatch({ type: EDIT_CUSTOMER_DETAILS }),
        onEditItem: (pageTitle) => dispatch({ type: EDIT_CUSTOMER_DETAILS_PAGE_NAVIGATE, pageTitle }),
        onPromotionFlagToggle: () => dispatch({ type: PROMOTION_FLAG_TOGGLE }),
        onAfterRegister: (customerId, customerData, response) => {
            if (response.customerNotes && response.customerNotes.indexOf(SDN_DATA_MATCH) >= 0) {
                dispatch({ type: RESTRICT_CUSTOMER_TRANSACTION, restricted: true });
            } else {
                dispatch({ type: "REVIEW_MONEY_ORDER", customerId, customerData });
            }
        },
        onAfterUpdate: (afterUpdate, url) => {
            dispatch(afterUpdate);
            dispatch(push(url));
        },
    };
};

const mapStateToOverviewProps = () => {
    return {
        items: overviewItems,
        header: overviewHeader,
        cancelURL: "/moneyOrder",
    };
};

const mapDispatchToOverviewProps = (dispatch) => {
    return { onContinue: () => dispatch(customerContinue) };
};

const Review = connect(mapStateToProps, mapDispatchToProps)(ReviewPage);
const RegisterSuccess = connect(mapStateToProps, mapDispatchToProps)(RegisterSuccessPage);
const UpdateSuccess = connect(mapStateToProps, mapDispatchToProps)(UpdateSuccessPage);
const Overview = connect(mapStateToOverviewProps, mapDispatchToOverviewProps)(OverviewPage);

export { Review, RegisterSuccess, UpdateSuccess, Overview };



