import { REHYDRATE } from "redux-persist/constants";
import { CONFIG_SUCCESS } from "../actions/config";
import { COUNTRIES_REQUEST, COUNTRIES_SUCCESS, COUNTRIES_FAILURE,
  AUS_STATES_REQUEST, AUS_STATES_SUCCESS, AUS_STATES_FAILURE,
  STATES_REQUEST, STATES_SUCCESS, STATES_FAILURE,
  CURRENCIES_REQUEST, CURRENCIES_SUCCESS, CURRENCIES_FAILURE,
  COUNTRY_CURRENCIES_REQUEST, COUNTRY_CURRENCIES_SUCCESS, COUNTRY_CURRENCIES_FAILURE,
  PRODUCT_SETTINGS_REQUEST, PRODUCT_SETTINGS_SUCCESS, PRODUCT_SETTINGS_FAILURE,
  COUNTRY_SELECT, CURRENCY_SELECT } from "../actions/codeTable";


const defaultState = {
    countries: [],
    states: [],
    ausStates: [],
    currencies: [],
    countryCurrencies: [],
    productSettings: {},

    countriesLastUpdateDate: null,
    statesLastUpdateDate: null,
    ausStatesLastUpdateDate: null,
    currenciesLastUpdateDate: null,
    countryCurrenciesLastUpdateDate: null,
    productSettingsLastUpdateDate: null,
};

function arrangeCountries(response) {
    if (response && response.getCountriesResponse && response.getCountriesResponse.countries
        && response.getCountriesResponse.countries.country) {
        const sortCountryFunction = (countryA, countryB) => countryA.name.localeCompare(countryB.name);
        const countries = response.getCountriesResponse.countries.country.sort(sortCountryFunction);
        return countries.map((item) => {
            return {
                name: item.name,
                value: item.code,
            };
        });
    }
    return null;
}

function arrangePrioritizedCountries(config, countries) {
    if (config && config.commonCountries && config.commonCountries.length > 0) {
        const { commonCountries } = config;
        const commonCountryCodes = commonCountries.map((country) => country.value);
        const filteredCountries = countries.filter((country) => !commonCountryCodes.includes(country.value));

        const upperCaseName = (country) => {
            return { ...country, name: country.name.toUpperCase() };
        };
        return commonCountries.map(upperCaseName).concat(filteredCountries);
    }
    return countries;
}

function arrangeStates(response) {
    if (response && response.getStateProvincesResponse) {
        const sortStatesFunction = (stateA, stateB) => stateA.name.localeCompare(stateB.name);
        const states = response.getStateProvincesResponse.stateProvinces.stateProvince.sort(sortStatesFunction);
        return states.map((item) => {
            return { ...item, value: item.code };
        });
    }
    return [];
}

function arrangeAUSStates(response) {
    if (response) {
        const ausStates = response.sort((stateA, stateB) => stateA.name.localeCompare(stateB.name));
        return ausStates.map((item) => {
            return { ...item, value: item.code };
        });
    }
    return [];
}

function arrangeCurrencies(response) {
    if (response && response.getCurrenciesResponse) {
        return response.getCurrenciesResponse.currencies.currency.map((item) => {
            return { name: item.name, value: item.code };
        });
    }
    return [];
}

function arrangeCountryCurrencies(response) {
    if (response && response.getCountryCurrenciesResponse) {
        return response.getCountryCurrenciesResponse.countryCurrencies.countryCurrency;
    }
    return [];
}

function arrangeProductSettings(response) {
    const productSettings = {};
    if (response && response.getProductUISettingsResponse) {
        response.getProductUISettingsResponse.settings.setting.forEach((setting) => {
            productSettings[setting.key] = setting.value;
        });
    }
    return productSettings;
}

function getAllowedCurrencies(state, countryCode) {
    const countryCurrencies = state.codeTable.countryCurrencies.filter((countryCurrency) => countryCurrency.countryCode === countryCode);
    const currencyCodes = countryCurrencies.map((countryCurrency) => countryCurrency.receiveCurrency);
    const filterCurrencies = (currency) => currencyCodes.includes(currency.value);
    return state.codeTable.currencies.filter(filterCurrencies);
}

const countriesReducer = (state, action) => {
    const codeTable = state.codeTable || defaultState;

    switch (action.type) {
        case CONFIG_SUCCESS:
        case COUNTRIES_SUCCESS: {
            const countries = arrangeCountries(action.response);
            return Object.assign({}, codeTable, {
                countries,
                prioritizedCountries: arrangePrioritizedCountries(state.config, countries),
                countriesLastUpdateDate: JSON.stringify(new Date()),
            });
        }

        case COUNTRIES_FAILURE:
            return Object.assign({}, codeTable, {
                countries: [],
                selectedCountry: null,
            });

        case COUNTRY_SELECT: {
            const selectedCountry = codeTable.countries.find((item) => item.value === action.value);
            const allowedCurrencies = getAllowedCurrencies(state, selectedCountry.value);
            const selectedCurrency = allowedCurrencies != null ? allowedCurrencies[0] : null;

            return Object.assign({}, codeTable, {
                selectedCountry,
                selectedCurrency,
                allowedCurrencies,
            });
        }

        default:
            return codeTable;
    }
};

const statesReducer = (codeTable = defaultState, action) => {
    switch (action.type) {
        case STATES_SUCCESS:
            return Object.assign({}, codeTable, {
                states: arrangeStates(action.response),
                statesLastUpdateDate: JSON.stringify(new Date()),
            });

        case STATES_FAILURE:
            return Object.assign({}, codeTable, {
                states: [],
            });

        default:
            return codeTable;
    }
};

const ausStatesReducer = (codeTable = defaultState, action) => {
    switch (action.type) {
        case AUS_STATES_SUCCESS:
            return Object.assign({}, codeTable, {
                ausStates: arrangeAUSStates(action.response),
                ausStatesLastUpdateDate: JSON.stringify(new Date()),
            });

        case AUS_STATES_FAILURE:
            return Object.assign({}, codeTable, {
                ausStates: [],
            });

        default:
            return codeTable;
    }
};

const currenciesReducer = (state, action) => {
    const codeTable = state.codeTable || defaultState;

    switch (action.type) {
        case CURRENCIES_SUCCESS:
            return Object.assign({}, codeTable, {
                currencies: arrangeCurrencies(action.response),
                currenciesLastUpdateDate: JSON.stringify(new Date()),
            });

        case CURRENCIES_FAILURE:
            return Object.assign({}, codeTable, {
                currencies: [],
            });

        case CURRENCY_SELECT: {
            const equalityCheck = (currency) => currency.value === action.value;
            const currency = codeTable.currencies.find(equalityCheck);

            return Object.assign({}, codeTable, {
                selectedCurrency: currency,
            });
        }

        default:
            return codeTable;
    }
};

const countryCurrenciesReducer = (codeTable = defaultState, action) => {
    switch (action.type) {
        case COUNTRY_CURRENCIES_SUCCESS:
            return Object.assign({}, codeTable, {
                countryCurrencies: arrangeCountryCurrencies(action.response),
                countryCurrenciesLastUpdateDate: JSON.stringify(new Date()),
            });

        case COUNTRY_CURRENCIES_FAILURE:
            return Object.assign({}, codeTable, {
                countryCurrencies: [],
            });

        default:
            return codeTable;
    }
};

const productSettingsReducer = (codeTable = defaultState, action) => {
    switch (action.type) {
        case PRODUCT_SETTINGS_SUCCESS:
            return Object.assign({}, codeTable, {
                productSettings: arrangeProductSettings(action.response),
                productSettingsLastUpdateDate: JSON.stringify(new Date()),
            });

        case PRODUCT_SETTINGS_FAILURE:
            return Object.assign({}, codeTable, {
                productSettings: {},
            });

        default:
            return codeTable;
    }
};

const codeTableReducer = (state, action) => {
    const codeTable = state.codeTable || defaultState;

    switch (action.type) {
        case REHYDRATE: {
            const rehydratedState = action.payload.codeTable;
            if (rehydratedState) {
                delete rehydratedState.selectedCountry;
                delete rehydratedState.selectedCurrency;
                delete rehydratedState.selectedDeliveryOption;
            }
            return Object.assign({}, rehydratedState);
        }

        case COUNTRIES_REQUEST:
        case COUNTRIES_SUCCESS:
        case COUNTRIES_FAILURE:
        case COUNTRY_SELECT:
            return countriesReducer(state, action);

        case STATES_REQUEST:
        case STATES_SUCCESS:
        case STATES_FAILURE:
            return statesReducer(codeTable, action);

        case AUS_STATES_REQUEST:
        case AUS_STATES_SUCCESS:
        case AUS_STATES_FAILURE:
            return ausStatesReducer(codeTable, action);

        case CURRENCIES_REQUEST:
        case CURRENCIES_SUCCESS:
        case CURRENCIES_FAILURE:
        case CURRENCY_SELECT:
            return currenciesReducer(state, action);

        case COUNTRY_CURRENCIES_REQUEST:
        case COUNTRY_CURRENCIES_SUCCESS:
        case COUNTRY_CURRENCIES_FAILURE:
            return countryCurrenciesReducer(codeTable, action);

        case PRODUCT_SETTINGS_REQUEST:
        case PRODUCT_SETTINGS_SUCCESS:
        case PRODUCT_SETTINGS_FAILURE:
            return productSettingsReducer(codeTable, action);

        default:
            return codeTable;
    }
};

export default codeTableReducer;



