import { RestHost } from "common-functions";

import CCGRest from "../common/CCGRest";
import { forceChangePassword as changePassword } from "./customer";

const getCustomerIdRequestData = (userName, password) => {
    return {
        getCustomerIdRequest: {
            customerCredentials: {
                registrationRetrievalNumber: userName,
                password,
            },
        },
    };
};

export const loginCall = (userName, password, successCallback, errorCallback) => {
    CCGRest.post("customers/getCustomerId", getCustomerIdRequestData(userName, password), (response) => {
        let customerId = null;
        if (response && response.getCustomerIdResponse
        && response.getCustomerIdResponse.customerIdStr) {
            customerId = response.getCustomerIdResponse.customerIdStr.replace("L", "");
        }

        const requestData = { getCustomerRequest: { customerId } };
        CCGRest.send(RestHost.POST, "customers/getCustomer", requestData, null,
          (getCustomerResponse) => successCallback(customerId, getCustomerResponse.getCustomerResponse),
          errorCallback,
          RestHost.JSON,
        );
    }, errorCallback);
};

export const isPasswordSystemGenerated = (password, customer) => {
    if (customer.passwordSystemGenerated) {
        return true;
    }
    const regex = /(MG|mg)\d\d(0[1-9]|1[012])(0[1-9]|[12][0-9]|3[01])/;
    const passwordYear = password.substr(2, 2);
    const year = new Date().getFullYear().toString().substr(2, 2);
    const yearCondition = passwordYear === year || (parseInt(year, 10) - 1) === parseInt(passwordYear, 10);
    const helpdeskGenerated = regex.test(password) && yearCondition;

    return helpdeskGenerated;
};

export const forceChangePassword = (dispatch, getState) => {
    const changePasswordSuccess = () => {
        const onClose = () => {
            dispatch({ type: "HIDE_MONEY_ORDER_ERROR" });
            dispatch({ type: "REVIEW_MONEY_ORDER", fromLogin: false });
        }
        dispatch({ type: "HIDE_KEYBOARD" });
        dispatch({ type: "SHOW_MONEY_ORDER_ERROR",
            title: "force.password.success.title",
            description: "force.password.success.subtitle",
            onClose, url: "#" });
    }
    changePassword(dispatch, getState, changePasswordSuccess);
};



