import React from "react";
import { push } from "connected-react-router";

import Login from "../Login";
import { INITIATE_UPDATE_CUSTOMER } from "../../actions";
import { Div, Span } from "../../components/HTML";
import { CUSTOMER_NEXT_PAGE } from "../../actions/customer";
import { INITIATE_CHECK_CASHING } from "../../actions/constants";

const onLoginAction = (dispatch, getState) => {
    const { customer, config } = getState();
    dispatch({ type: INITIATE_CHECK_CASHING, customerData: customer.previousCustomerData });

    const photoIdType = customer.previousCustomerData.photoIdType;
    let invalidForCheckCashing = null;
    if (photoIdType === "PAS") {
        invalidForCheckCashing = customer.previousCustomerData.photoIdCountry !== "USA";

        if (invalidForCheckCashing) {
            dispatch({ type: "FILL_UP_REQUEST_DATA", customerData: { photoIdType: "FPAS" } });
        }
    } else if (photoIdType === "DRV") {
        invalidForCheckCashing = customer.previousCustomerData.photoIdCountry !== "USA";
        dispatch({ type: "FILL_UP_REQUEST_DATA", customerData: { photoIdType: invalidForCheckCashing ? "FDRV" : "USDRV" } });
    } else {
        const photoId = config.photoIdOptions.photoIdTypes.find((idType) => idType.value === photoIdType);
        invalidForCheckCashing = photoId.invalidForCheckCashing;
    }

    if (invalidForCheckCashing) {
        const description = (<Div >
            <Span>update.invalid.id.desc</Span>
            <ul className="valid-ids">
                <li><Span>photo.id.type.usdrv</Span></li>
                <li><Span>photo.id.type.uspas</Span></li>
                {/* <li><Span>photo.id.type.umc</Span></li>
                <li><Span>photo.id.type.mcc</Span></li>
                <li><Span>photo.id.type.grn</Span></li> */}
            </ul>
            <br />
            <Span args={config.defaultValues.helpHotline}>cannot.proceed.desc</Span>
        </Div>);
        dispatch({ type: "SHOW_CHECK_CASHING_ERROR",
            title: "update.invalid.id.title",
            description,
            onClose: () => onUpdateIdAction(dispatch),
            url: "/updateCustomer",
            noLink: false,
            button: "update.invalid.id.btn",
            additionalClass: "intervention-non-error invalid-id" });
    } else {
        dispatch(push("/checkCashing"));
    }
};

const onUpdateIdAction = (dispatch) => {
    window.setTimeout(() => {
        dispatch({ type: "SHOW_CHECK_CASHING_ERROR", hideIntervention: true, destinationPage: "/checkCashing" });
        dispatch({ type: INITIATE_UPDATE_CUSTOMER, hasLogin: true });
        dispatch({ type: CUSTOMER_NEXT_PAGE });
        dispatch({ type: "SHOW_KEYBOARD" });
        dispatch({ type: "EDIT_CUSTOMER_DETAILS_PAGE_NAVIGATE", pageTitle: "photoId" });
        dispatch({ type: "VALIDATION_FAILED", hasFormatErrors: true });
    }, 50);
};

const CCLogin = () => <Login onLoginAction={onLoginAction} />;
export default CCLogin;



