import { MESSAGES_SUCCESS, MESSAGES_FAILURE, MESSAGES_LANGUAGE_UPDATE } from "../actions/moneygramMessages";

const defaultState = {
    messages: {},
    language: "en",
    languages: [
      { name: "English", value: "en" },
      { name: "Español", value: "es" },
    ],
};

const moneygramMessagesReducer = (state, action) => {
    const messages = state.moneygramMessages || defaultState;
    switch (action.type) {
        case MESSAGES_SUCCESS:
            return Object.assign({}, messages, {
                language: action.selectedLanguage,
                messages: action.moneygramMessages,
            });

        case MESSAGES_LANGUAGE_UPDATE:
            return Object.assign({}, messages, {
                language: action.selectedLanguage,
            });

        case MESSAGES_FAILURE:
            return Object.assign({}, defaultState);

        default:
            return messages;
    }
};

export default moneygramMessagesReducer;



