import React from "react";
import { connect } from "react-redux";
import { Intl, Helpers, Validation } from "common-functions";

import { FIELD_FOCUS } from "../actions/keyboardAction";
import ViewComponent from "./ViewComponent";
import { Label, P, Span } from "./HTML";

const RESET_INPUTS_FORMAT_VALIDATION = "RESET_INPUTS_FORMAT_VALIDATION";

const removeSpecialCharacters = (value) => {
    if (value && value.length > 0) {
        return value.replace(/[^a-zA-Z0-9 ]/g, "");
    }
    return value;
};

const getFilteredItem = (items, maxDisplayedItem, rawValue, excludeSpecialCharactersInFilter) => {
    const excludeSpecialCharacterValue = excludeSpecialCharactersInFilter ? removeSpecialCharacters(rawValue) : null;
    let value = rawValue;
    const filteredItems = [];

    if (value != null && value.toString().length) {
        value = value.toString();
        for (let i = 0; i < items.length; i += 1) {
            const item = items[i];

            let name = item.name || item;
            name = name.toLowerCase();
            const searchWord = Helpers.escapeRegExp(value.toLowerCase());
            const searchRegEx = new RegExp(`(^${searchWord})|([(]${searchWord})`);

            if (searchRegEx.test(name)) {
                filteredItems.push(item);
            } else if (excludeSpecialCharacterValue) {
                const excludeSearchWord = Helpers.escapeRegExp(excludeSpecialCharacterValue.toLowerCase());
                const excludeSearchRegEx = new RegExp(`(^${excludeSearchWord})|([(]${excludeSearchWord})`);

                if (excludeSearchRegEx.test(name)) {
                    filteredItems.push(item);
                }
            }

            if (filteredItems.length === maxDisplayedItem) {
                break;
            }
        }
    }
    return filteredItems;
};

const hasSelected = (items, selectedValue) => items.find((item) => item.name === selectedValue);

class Dropdown extends ViewComponent {

    constructor(props) {
        super(props);

        const { value } = this.props;
        const state = Object.assign({}, this.getUpdatedSelectedValue(value));

        state.show = false;
        this.state = state;

        this.hasPlaceholderIssue = Helpers.hasPlaceholderIssue();
        this.handleInput = (event) => {
            this.setState({ valid: false });
            if (this.props.onChange) {
                this.props.onChange(event);
            }
            this.onSelectionByChange(event);
        };
    }

    getUpdatedSelectedValue(value) {
        const { name, items, translation } = this.props;

        const state = {};
        const translatedItems = items.map((item) => {
            return { ...item, name: Intl.translate(translation, item.name) };
        });

        if (value || value === 0) {
            const selectedItem = translatedItems.find((item) => item.value.toString() === value.toString());
            if (selectedItem) {
                state[name] = selectedItem.name;
                state.valid = true;
            }
        }
        return state;
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.sessionReplaying) {
            const { value } = nextProps;
            this.setState(this.getUpdatedSelectedValue(value));
        }
    }

    componentDidMount() {
        this.bindKeyboardEvent();

        if (this.props.shouldAutoFocus) {
            this.input.focus();
        }
    }

    componentDidUpdate() {
        this.bindKeyboardEvent();
    }

    onSelectionByChange(event) {
        const { onChange, onSelection, name, validateInputs, excludeSpecialCharactersInFilter } = this.props;
        const value = event.target.value;
        const items = this.props.items.map((item) => {
            return { ...item, value: item.value, name: Intl.translate(this.props.translation, item.name) };
        });
        const filteredItems = getFilteredItem(items, this.props.maxDisplayedItem || 2, value, excludeSpecialCharactersInFilter);
        const hasFilteredItems = filteredItems && filteredItems.length > 0;

        if (hasFilteredItems) {
            const selectedItem = filteredItems.find((item) => {
                const itemName = item.name || item;
                return value.toLowerCase() === itemName.toLowerCase();
            });

            if (selectedItem) {
                if (onSelection) {
                    onSelection(selectedItem);
                } else if (onChange) {
                    const target = { name, value: selectedItem.value };
                    onChange({ target });
                }
                this.onCompleteFillUp(selectedItem);
                const nextState = {};
                nextState[name] = selectedItem.name;
                nextState.valid = true;
                this.setState(nextState);
            }
        }
        validateInputs();
    }

    onSelection(filteredItems, selectedValue) {
        const { onChange, onSelection, name, validateInputs } = this.props;
        const selectedItem = filteredItems.find((item, index) => {
            const value = item.value || item.code || index;
            return value === selectedValue;
        });

        if (onSelection) {
            onSelection(selectedItem);
        } else if (onChange) {
            const target = { name, value: selectedItem.value };
            onChange({ target });
        }
        this.onCompleteFillUp(selectedItem);
        const nextState = {};
        nextState[name] = selectedItem.name;
        nextState.valid = true;
        this.setState(nextState);

        validateInputs();
    }

    onCompleteFillUp(selectedItem) {
        const focusOnCompleteFillUp = this.props.focusOnCompleteFillUp;
        if (focusOnCompleteFillUp && selectedItem) {
            const focusOnCompleteFillUpElement = document.getElementById(focusOnCompleteFillUp);
            if (focusOnCompleteFillUpElement) {
                focusOnCompleteFillUpElement.focus();
            }
        }
    }

    getInputProps() {
        const { value, validation, items, translation, onClick } = this.props;
        const id = this.props.id || this.props.name;

        let displayValue = value || "";
        if (this.state.valid && (value || value === 0)) {
            const selectedItem = items.find((item, index) => (item.value || item.code || index).toString() === value.toString());
            displayValue = selectedItem ? Intl.translate(translation, selectedItem.name) : displayValue;
        }

        const validationAttributes = Validation.getAsAttributes(validation);
        const inputProps = Object.assign({}, validationAttributes, this.props, {
            onChange: (event) => this.onChange(event),
            value: displayValue,
            id,
        });

        const currentClassName = inputProps.className ? `${inputProps.className} ` : "";
        const additionalClassName = inputProps.selected ? "selected" : "";
        inputProps.className = `${currentClassName}${additionalClassName}`;

        inputProps.onClick = (event) => {
            Helpers.setCaretToEnd(id);
            if (onClick) {
                onClick(event);
            }
        };

        delete inputProps.translation;
        delete inputProps.label;
        delete inputProps.validation;
        delete inputProps.errorMessage;
        delete inputProps.dispatch;
        delete inputProps.shouldAutoFocus;
        delete inputProps.items;
        delete inputProps.onSelection;
        delete inputProps.hideErrorMessage;
        delete inputProps.labelComponent;
        delete inputProps.maxDisplayedItem;
        delete inputProps.validateInputs;
        delete inputProps.parentClassName;
        delete inputProps.excludeSpecialCharactersInFilter;
        delete inputProps.allowNotOnList;
        delete inputProps.focusOnCompleteFillUp;
        delete inputProps.sessionReplaying;
        delete inputProps.defaultErrorMessage;

        return inputProps;
    }

    bindKeyboardEvent() {
        if (this.input) {
            this.input.removeEventListener("onkeyclick", this.handleInput);
            this.input.addEventListener("onkeyclick", this.handleInput, false);
        }
    }

    onChange(event) {
        super.handleChange(event);
        this.handleInput(event);
    }

    render() {
        const props = this.props;
        const id = props.id || props.name;

        const items = props.items.map((item) => {
            return { ...item, value: item.value, name: Intl.translate(props.translation, item.name) };
        });

        const label = props.label || props.placeholder;
        const translatedLabel = Intl.translate(props.translation, label);
        const labelComponent = props.label ? <Label htmlFor={id}>{props.label}</Label> : props.labelComponent;

        let inputContainerClassName = props.prefix ? "prefixed relative" : "relative";
        inputContainerClassName = props.disabled ? `${inputContainerClassName} disabled` : inputContainerClassName;

        const hasPrefix = props.prefix;
        const prefixComponent = hasPrefix ? <p className="prefix">{props.prefix}</p> : null;
        const inputProps = this.getInputProps();
        const propsClassName = inputProps.className || "";

        let placeholderComponent = null;
        const placeholderIntl = Intl.translate(props.translation, props.placeholder);
        if (this.hasPlaceholderIssue && !inputProps.value) {
            placeholderComponent = <P id={`${id}Placeholder`} className={`placeholder ${propsClassName}`} onClick={() => this.input.focus()}>{placeholderIntl}</P>;
            delete inputProps.placeholder;
        } else {
            inputProps.placeholder = placeholderIntl;
        }

        const decorComponent = (<div className="absolute">
            {prefixComponent}
            {placeholderComponent}
        </div>);

        const filteredItems = getFilteredItem(items, props.maxDisplayedItem || 2, inputProps.value, props.excludeSpecialCharactersInFilter);
        const hasFilteredItems = filteredItems && filteredItems.length > 0;
        const hasChild = !hasSelected(items, inputProps.value);

        const inputClassName = propsClassName +
            (hasFilteredItems && hasChild ? " has-child" : "") +
            (this.props.throwUp ? " dropdown-input--throw-up" : "");
        const isRightAlign = propsClassName.indexOf("right-align") >= 0 ? "right-align" : "";
        const itemsClassName = `dropdown-content ${this.props.throwUp ? " dropdown-content--throw-up" : ""} ${hasFilteredItems && !hasSelected(items, inputProps.value) ? "show" : ""} ${isRightAlign}`;

        const dropdownContentId = `${id}Content`;
        inputProps.className = inputClassName;

        let itemsComponent = [];
        if (hasFilteredItems) {
            itemsComponent = filteredItems.map((item, index) => {
                const itemName = item.name || item;
                const itemValue = item.value || item.code || index;
                return (<a className={propsClassName} key={itemValue}
                  onClick={() => this.onSelection(filteredItems, itemValue)}>
                    <Span className="non-select-btn">{itemName}</Span>
                    <Span className="select-btn">select.btn</Span>
                </a>)
            });
        }

        const dataLabel = !/^[\s]+$/.test(translatedLabel) ? translatedLabel : placeholderIntl;
        const parentClassName = `${props.errorMessage ? "has-error" : ""} pull-left ${props.parentClassName}`;

        return (<div className={parentClassName} style={{ display: props.hidden ? "none" : "" }}>
            {labelComponent}

            <div className={inputContainerClassName}>
                {hasPrefix ? decorComponent : placeholderComponent}

                <input {...inputProps}
                  data-label={dataLabel}
                  onFocus={(e) => props.onFocus(this.input || e.target)}
                  ref={(input) => { this.input = input; }}
                  data-valid={props.allowNotOnList || this.state.valid}
                  data-dropdown />

                <div id={dropdownContentId} className={itemsClassName}>
                    {itemsComponent}
                </div>
            </div>

            <div className="clearfix" />
            {!props.hideErrorMessage ? <P className={`${propsClassName} message error`} defaultValue={props.defaultErrorMessage}>
                {props.errorMessage}
            </P> : null}
        </div>);
    }
}


Dropdown.defaultProps = {
    items: [],
    translation: {},
    type: "text",
    disabled: false,
    shouldAutoFocus: false,
    onChange: null,
};

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.id || ownProps.name;
    const i18n = state.internationalization;
    const validations = state.validation || state.validations;
    const validation = validations ? validations.find((item) => item.fieldName === id) : null;

    let error = null;
    if (state.ui && !ownProps.hideErrorMessage) {
        error = state.ui.errors.find((item) => item.fieldName === id);
    }

    const { keyboard } = state;
    const selected = keyboard && keyboard.selectedField && keyboard.selectedField.id === id;
    const sessionReplaying = state.sessionRecording && state.sessionRecording.playing;

    return {
        validation,
        selected,
        errorMessage: error ? error.message : null,
        defaultErrorMessage: error ? error.defaultMessage: null,
        translation: i18n ? i18n.translation : null,
        sessionReplaying,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onFocus: (selectedField) => dispatch({ type: FIELD_FOCUS, selectedField }),
        validateInputs: () => dispatch({ type: RESET_INPUTS_FORMAT_VALIDATION }),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Dropdown);



