import React from "react";
import { connect } from "react-redux";
import { Switch, Route, Link } from "react-router-dom";
import { Keyboard } from "common-components";

import { HIDE_INTERVENTION } from "../actions";
import LoadingBox from "../components/LoadingBox";
import Intervention from "../components/Intervention";

import Terms from "./Terms";
import Welcome from "./checkCashing/Welcome";
import CCLogin from "./checkCashing/Login";
import CheckCashing from "./checkCashing/CheckCashing";
import CheckCashingOverview from "./checkCashing/Overview";
import MoneyOrderLoadingBox from "../components/MoneyOrderLoadingBox";
import UserCheck from "./UserCheck";
import RegisterCustomer from "./customer/RegisterCustomer";
import UpdateCustomer from "./customer/UpdateCustomer";
import Overview from "./Overview";
import Unimplemented from "./Unimplemented";
import ResetPassword from "./customer/ResetPassword";
import RestrictedAccess from "./checkCashing/RestrictedAccess";
import { CheckCashingIntervention } from "./checkCashing/Intervention";
import Faqs from "./help/Faqs";

import { Div, Button } from "../components/HTML";

class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = { showBuildInfo: false };
    }

    showBuildInfo() {
        this.setState({ showBuildInfo: true });

        const hideBuildInfo = () => this.setState({ showBuildInfo: false });
        setTimeout(hideBuildInfo, 5000);
    }

    render() {
        const { onMainMenu, lang } = this.props;
        const { showBuildInfo } = this.state;

        const buildDetails = (<div className="build-details">
            <p className="build-version">{__webpack_version__}</p>
            <p className="build-hash">{__webpack_hash__ || "-"}</p>
        </div>);

        return (<div className="header">
            <div className="header-navigation-bar">
                <div className="navigation-bar-brand" onDoubleClick={() => this.showBuildInfo()}>
                    <div className="temp-header" />
                    <img src="resources/images/check-cashing2.png" alt="Money Orders" className="check-cashing" />
                </div>

                {showBuildInfo ? buildDetails : null}

                <Link to="/exit" onClick={onMainMenu}>
                    <div className="button-container">
                        <Button className={`quit exit-app pull-right ${lang}-button`} defaultValue="Exit" data-analytics="Exit">header.exit</Button>
                    </div>
                </Link>

                <Link to="/" onClick={onMainMenu}>
                    <Div className="button-container">
                        <Button className={`home to-home pull-right ${lang}-button`} defaultValue="Main Menu" data-analytics="Main menu">header.main.menu</Button>
                    </Div>
                </Link>

                <div className="line pull-right" />
                <div className="clearfix" />
            </div>
        </div>);
    }

}

const mapStateToInterventionProps = (state) => {
    const { showIntervention, interventionTitle, interventionDescription,
        interventionChildren, interventionClassName } = state.ui;
    return {
        show: showIntervention,
        title: interventionTitle,
        description: interventionDescription,
        children: interventionChildren,
        className: interventionClassName,
        lang: state.internationalization.language || "en",
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        hideIntervention: () => dispatch({ type: HIDE_INTERVENTION }),
        onMainMenu: () => {
            dispatch({ type: "INITIATE_MONEY_ORDER" });
            dispatch({ type: "INITIATE_LOGIN" });
            dispatch({ type: "INITIATE_HALF_REGISTER_CUSTOMER" });
            dispatch({ type: "INITIATE_RESET_PASSWORD" });
            dispatch({ type: "RESET_STATE" });
        }
    };
};

const DefaultIntervention = connect(mapStateToInterventionProps, mapDispatchToProps)(Intervention);
const DefaultHeader = connect(mapStateToInterventionProps, mapDispatchToProps)(Header);

const App = () => (
  <div id="app">
    <DefaultHeader />

    <div className="container">
      <Switch>
        <Route exact path="/" component={Welcome} />
        <Route path="/login" component={CCLogin} />
        <Route path="/checkCashing" component={CheckCashing} />
        <Route path="/checkCashingOverview" component={CheckCashingOverview} />
        <Route path="/register" component={RegisterCustomer} />
        <Route path="/updateCustomer" component={UpdateCustomer} />
        <Route path="/userCheck" component={UserCheck} />
        <Route path="/overview" component={Overview} />
        <Route path="/resetPassword" component={ResetPassword} />
        <Route path="/soon" component={Unimplemented} />
        <Route path="/terms" component={Terms} />
        <Route path="/restricted" component={RestrictedAccess} />
        <Route path="/faq" component={Faqs} />
        <Route component={Unimplemented} />
      </Switch>
    </div>

    <Keyboard />
    <LoadingBox />
    <MoneyOrderLoadingBox />
    <DefaultIntervention />
    <CheckCashingIntervention />
    <RestrictedAccess />
  </div>

);

export default App;



