import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Button } from "common-components";

import { VALIDATE_INPUTS_FORMAT } from "../actions/validation";

class Navigation extends React.Component {

    componentDidMount() {
        this.validateInputs();
    }

    componentDidUpdate() {
        this.validateInputs();
    }

    validateInputs() {
        const { shouldRevalidate, validateInputs } = this.props;
        if (shouldRevalidate) {
            validateInputs();
        }
    }

    render() {
        const { containerClassName, prevUrl, nextOnEnter,
            disablePrevious, disableNext, onPrevious, onNext,
            hasInput, hasFormatErrors, previousLink, nextLink,
            hidePrevious, hideNext, cssStyle } = this.props;

        const defaultNextClassName = !hasInput || !hasFormatErrors ? "next highlight" : "next";
        const nextClassName = disableNext ? "disabled next" : defaultNextClassName;
        const prevClassName = disablePrevious ? "disabled prev" : "prev";
        const displayPrevious = { display: hidePrevious ? "none" : "" };
        const displayNext = { display: hideNext ? "none" : "" };

        return (<div id="navigation" className={containerClassName}>
            <Link to={previousLink}>
                <Button className={prevClassName}
                  onClick={!disablePrevious ? onPrevious : null}
                  to={!disablePrevious ? prevUrl : null}
                  style={displayPrevious}
                  data-analytics="Previous">
                  prev.btn
                </Button>
            </Link>

            <Link to={nextLink}>
                <Button className={nextClassName}
                  onClick={!disableNext ? onNext : null}
                  type={nextOnEnter ? "submit" : "button"}
                  style={displayNext}
                  data-analytics="Next">
                  next.btn
                </Button>
            </Link>
        </div>);
    }
}

const mapStateToProps = (state, ownProps) => {
    const { keyboard, ui } = state;

    const containerClassName = keyboard.show ? "navigation has-keyboard" : "navigation";
    const hasFormatErrors = typeof ownProps.hasFormatErrors !== "undefined" ? ownProps.hasFormatErrors : ui.hasFormatErrors;
    const hasInput = typeof ownProps.hasInput !== "undefined" ? ownProps.hasInput : ui.hasInput;
    const shouldRevalidate = ui.hasFormatErrors === undefined && ui.hasInput === undefined;
    const previousLink = ownProps.previousLink || "#";
    const nextLink = ownProps.nextLink || "#";
    const hasErrors = ui.errors && (ui.errors.length > 0);

    return {
        containerClassName,
        hasFormatErrors,
        hasInput,
        previousLink,
        nextLink,
        shouldRevalidate,
        hasErrors,
    };
};

const mapDispatchToProps = (dispatch) => {
    return { validateInputs: () => dispatch({ type: VALIDATE_INPUTS_FORMAT }) };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navigation);



