import { Helpers, RestHost } from "common-functions";

import CCGRest from "../common/CCGRest";

export const RESET_PASSWORD_REQUEST = "RESET_PASSWORD_REQUEST";
export const RESET_PASSWORD_SUCCESS = "RESET_PASSWORD_SUCCESS";
export const RESET_PASSWORD_FAILURE = "RESET_PASSWORD_FAILURE";
export const RESET_PASSWORD_NEXT_PAGE = "RESET_PASSWORD_NEXT_PAGE";
export const RESET_PASSWORD_PREV_PAGE = "RESET_PASSWORD_PREV_PAGE";
export const FILL_UP_RESET_PASSWORD_REQUEST_DATA = "FILL_UP_RESET_PASSWORD_REQUEST_DATA";
export const FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA = "FILL_UP_RESET_PASSWORD_QA_REQUEST_DATA";
export const FILL_UP_FORCE_CHANGE_PASSWORD = "FILL_UP_FORCE_CHANGE_PASSWORD";
export const GET_CUSTOMER_QA_REQUEST = "GET_CUSTOMER_QA_REQUEST";
export const GET_CUSTOMER_QA_SUCCESS = "GET_CUSTOMER_QA_SUCCESS";
export const GET_CUSTOMER_QA_FAILURE = "GET_CUSTOMER_QA_FAILURE";

export const onContinue = (dispatch, getState) => {
    const resetPassword = getState().resetPassword;
    const { pages, currentIndex } = resetPassword;
    const currentPage = pages[currentIndex];

    const additionalValidation = currentPage.validate;
    const onCustomOnContinue = currentPage.onContinue;

    const onSuccessfulValidation = () => {
        const action = { type: RESET_PASSWORD_NEXT_PAGE };
        dispatch(action);
    };

    if (additionalValidation) {
        additionalValidation(dispatch, getState, onCustomOnContinue || onSuccessfulValidation);
    } else if (onCustomOnContinue) {
        onCustomOnContinue(dispatch, getState);
    } else {
        onSuccessfulValidation(dispatch, getState);
    }
};

export const onPrevious = (dispatch, getState) => {
    const { pages, currentIndex } = getState().resetPassword;
    const currentPage = pages[currentIndex];
    const onCustomOnPrevious = currentPage.onPrevious;

    if (onCustomOnPrevious) {
        onCustomOnPrevious(dispatch, getState);
    } else {
        const action = { type: RESET_PASSWORD_PREV_PAGE };
        dispatch(action);
    }
};

const getRequestData = (state) => {
    const requestData = state.resetPassword.requestData;
    const { questionType, answerYear, answerMonth, answerDay } = requestData.customerQA;

    if (questionType === "DOB" && Helpers.isDateValid(answerYear, answerMonth, answerDay)) {
        const answer = `${answerYear}-${parseInt(answerMonth, 10) + 1}-${answerDay}`;
        requestData.customerQA = { questionType, answer };
    }

    requestData.agentDetails = Helpers.getAgentDetails();
    return { resetPasswordRequest: requestData };
};

export const resetPassword = (dispatch, getState) => {
    const state = getState();
    const resetPasswordRequest = getRequestData(state);

    dispatch({ type: RESET_PASSWORD_REQUEST, resetPasswordRequest });

    CCGRest.send(RestHost.POST, "customers/resetPassword", resetPasswordRequest, null,
        (response) => {
            dispatch({ type: RESET_PASSWORD_SUCCESS, response });
            dispatch({ type: RESET_PASSWORD_NEXT_PAGE });
        },
        (error) => {
            dispatch({ type: RESET_PASSWORD_FAILURE, error });
            dispatch({ type: "SHOW_POP_UP", showPopUp: true });
        },
        RestHost.JSON,
    );
};

export const getCustomerQA = (dispatch, getState) => {
    const state = getState();
    const requestData = state.resetPassword.requestData;
    const request = {
        getCustomerQARequest: {
            registrationRetrievalNumber: requestData.registrationRetrievalNumber,
        },
    };

    dispatch({ type: GET_CUSTOMER_QA_REQUEST, request });

    CCGRest.send(RestHost.POST, "customers/getCustomerQA", request, null,
        (response) => {
            dispatch({ type: GET_CUSTOMER_QA_SUCCESS, response });
            dispatch({ type: RESET_PASSWORD_NEXT_PAGE });
        },
        (error) => {
            dispatch({ type: GET_CUSTOMER_QA_FAILURE, error });
            dispatch({ type: RESET_PASSWORD_NEXT_PAGE });
        },
        RestHost.JSON,
    );
};



