export const nextPage = (requestData, dispatch) => {
    dispatch({ type: FILL_UP_REQUEST_DATA, requestData });
};

const login = (userName, password, onLoginSuccess, onLoginError, getState) => {
    const requestData = {
        getCustomerIdRequest: {
            customerCredentials: {
                registrationRetrievalNumber: userName,
                password,
            },
        },
    };

    restPostHandler(getState, "customers/getCustomerId", requestData,
        (response) => {
            let customerId = null;
            if (response && response.getCustomerIdResponse && response.getCustomerIdResponse.customerIdStr) {
                customerId = response.getCustomerIdResponse.customerIdStr.replace("L", "");
            }
            onLoginSuccess(customerId);
        },
        onLoginError);
};

export const loginRequest = (userName, password, successCallbackParam, processOnBackground) => (dispatch, getState) => {
    dispatch({ type: LOGIN_REQUEST, processOnBackground });

    const defaultSuccessCallback = (customerId, response) => {
        dispatch({ type: LOGIN_SUCCESS, customerId, response, userName, password, processOnBackground });
    };
    const successCallback = successCallbackParam || defaultSuccessCallback;

    const onLoginError = () => {
        dispatch({
            type: LOGIN_FAILURE,
            error: { fieldName: "userName", message: "invalid.customer.credentials" },
            processOnBackground,
        });
    };

    const onLoginSuccess = (customerId) => {
        const requestData = { getCustomerRequest: { customerId } };
        restSendHandler(getState, RestHost.POST, "customers/getCustomer", requestData, null,
          (response) => successCallback(customerId, response, userName, password), onLoginError,
          RestHost.JSON,
        );
    };

    login(userName, password, onLoginSuccess, onLoginError, getState);
};

const checkCustomerCredentialsStatus = (getState, registrationRetrievalNumber, successCallback, errorCallback) => {
    const checkCustomerCredentialsStatusRequest = {
        checkCustomerCredentialsStatusRequest: { registrationRetrievalNumber },
    };

    restSendHandler(getState, RestHost.POST, "customers/checkCustomerCredentialsStatus", checkCustomerCredentialsStatusRequest, null,
        successCallback, errorCallback, RestHost.JSON,
    );
};

const clearUnnecessaryFields = (requestData) => {
    const data = requestData;
    delete data.customerData.photoIdCardType;
    delete data.customerData.photoIdSecondaryCardType;

    delete data.customerData.photoIdReferenceNumber;
    delete data.customerData.secondaryPhotoIdReferenceNumber;

    delete data.customerCredentialsData.passwordConfirmation;
    delete data.customerCredentialsData.customerQA.answerYear;
    delete data.customerCredentialsData.customerQA.answerMonth;
    delete data.customerCredentialsData.customerQA.answerDay;

    delete data.customerData.birthYear;
    delete data.customerData.birthMonth;
    delete data.customerData.birthDay;

    delete data.customerData.photoIdExpiryYear;
    delete data.customerData.photoIdExpiryMonth;
    delete data.customerData.photoIdExpiryDay;

    delete data.customerData.photoIdIssueYear;
    delete data.customerData.photoIdIssueMonth;
    delete data.customerData.photoIdIssueDay;

    delete data.customerData.secondaryPhotoIdExpiryYear;
    delete data.customerData.secondaryPhotoIdExpiryMonth;
    delete data.customerData.secondaryPhotoIdExpiryDay;

    delete data.customerData.secondaryPhotoIdIssueYear;
    delete data.customerData.secondaryPhotoIdIssueMonth;
    delete data.customerData.secondaryPhotoIdIssueDay;

    delete data.customerData.previousSelectedCountry;

    return data;
};

const clearSecondaryPhotoIdFields = (requestData) => {
    const data = requestData;
    delete data.customerData.secondaryPhotoIdType;
    delete data.customerData.secondaryPhotoIdNumber;
    delete data.customerData.secondaryPhotoIdExpiryDate;
    delete data.customerData.secondaryPhotoIdIssueDate;
    return data;
};

const getDefaultPostcode = (customerData, config) => {
    const { countryValidations, defaultValues } = config;
    const countryValidation = countryValidations && countryValidations
        .find((validation) => validation.country === customerData.country);

    const defaultPostcode = countryValidation && countryValidation.disablePostcode
        && countryValidation.prefix ? countryValidation.prefix : defaultValues.postcode;
    return defaultPostcode;
};

const getRequestData = (state) => {
    const { config, codeTable, customer } = state;
    const { countryValidations, photoIdOptions } = config;
    const { countryTypeMappings, driversLicenseTypes } = photoIdOptions;
    let requestData = cloneDeep(customer.requestData);

    const getMappedCountry = (type) => {
        const countryTypeMapping = countryTypeMappings.find((item) => item.type === type);
        return countryTypeMapping ? countryTypeMapping.country : null;
    };

    let { photoIdCountry, secondaryPhotoIdCountry } = requestData.customerData;
    const { country, photoIdState, secondaryPhotoIdState,
        photoIdType, secondaryPhotoIdType, photoIdNumber, secondaryPhotoIdNumber,
        birthYear, birthMonth, birthDay, occupation, birthCountry, birthTown, address1, town,
        photoIdCardType, secondaryPhotoIdCardType, photoIdReferenceNumber, secondaryPhotoIdReferenceNumber,
        verifyAddress, verifyPhotoId, verifySecondaryPhotoId, promotionFlag,
        photoIdExpiryDay, photoIdExpiryMonth, photoIdExpiryYear,
        photoIdIssueDay, photoIdIssueMonth, photoIdIssueYear,
        secondaryPhotoIdExpiryDay, secondaryPhotoIdExpiryMonth, secondaryPhotoIdExpiryYear,
        secondaryPhotoIdIssueDay, secondaryPhotoIdIssueMonth, secondaryPhotoIdIssueYear, email } = requestData.customerData;
    const { questionType, answerDay, answerMonth, answerYear } = requestData.customerCredentialsData.customerQA;

    if (Helpers.isDateValid(birthYear, birthMonth, birthDay)) {
        requestData.customerData.birthDate = `${birthYear}-${parseInt(birthMonth, 10) + 1}-${birthDay}`;
    }

    if (!email) {
        requestData.customerData.email = config.defaultValues.email;
    }

    requestData.customerCredentialsData.registrationRetrievalNumber = requestData.customerData.phone;


    requestData.customerData.occupation = occupation || "NOT_DEFINED";
    requestData.customerData.birthCountry = birthCountry || "ZZZ";
    requestData.customerData.birthTown = birthTown || "NOT_DEFINED";
    requestData.customerData.country = country || "ZZZ";
    requestData.customerData.address1 = address1 || "NOT_DEFINED";
    requestData.customerData.town = town || "NOT_DEFINED";


    if (questionType === "DOB" && Helpers.isDateValid(answerYear, answerMonth, answerDay)) {
        requestData.customerCredentialsData.customerQA.answer = `${answerYear}-${parseInt(answerMonth, 10) + 1}-${answerDay}`;
    }

    requestData.agentDetails = Helpers.getAgentDetails();

    requestData.customerData.verifyAddress = verifyAddress || config.customerOptions.verifyAddress;
    requestData.customerData.verifyPhotoId = verifyPhotoId || config.customerOptions.verifyPhotoId;
    requestData.customerData.verifySecondaryPhotoId = verifySecondaryPhotoId || config.customerOptions.verifySecondaryPhotoId;
    requestData.customerData.promotionFlag = promotionFlag || config.customerOptions.promotionFlag;

    if (Helpers.isDateValid(photoIdExpiryYear, photoIdExpiryMonth, photoIdExpiryDay)) {
        requestData.customerData.photoIdExpiryDate = `${photoIdExpiryYear}-${parseInt(photoIdExpiryMonth, 10) + 1}-${photoIdExpiryDay}`;
    }

    if (Helpers.isDateValid(photoIdIssueYear, photoIdIssueMonth, photoIdIssueDay)) {
        requestData.customerData.photoIdIssueDate = `${photoIdIssueYear}-${parseInt(photoIdIssueMonth, 10) + 1}-${photoIdIssueDay}`;
    }

    if (photoIdReferenceNumber) {
        requestData.customerData.photoIdNumber = `${photoIdNumber}-${photoIdReferenceNumber}`;
    }

    const photoIdMappedCountry = getMappedCountry(photoIdType);
    requestData.customerData.photoIdType = driversLicenseTypes.includes(photoIdType) ? "DRV" : (photoIdCardType || photoIdType);
    requestData.customerData.photoIdCountry = photoIdMappedCountry || photoIdCountry || country;
    photoIdCountry = requestData.customerData.photoIdCountry;

    const countryValidation = countryValidations.find((item) => item.country === photoIdCountry);
    const defaultPhotoIdState = countryValidation ? (countryValidation.senderPhotoIdState || photoIdCountry) : photoIdCountry;

    const countryStates = photoIdCountry === "AUS" ? codeTable.ausStates : codeTable.states.filter((item) => item.countryCode === photoIdCountry);
    requestData.customerData.photoIdState = countryStates.length === 0 ? defaultPhotoIdState : (photoIdState || defaultPhotoIdState);

    if (!requestData.customerData.postcode) {
        requestData.customerData.postcode = getDefaultPostcode(requestData.customerData, config);
    }

    if (secondaryPhotoIdNumber) {
        if (secondaryPhotoIdReferenceNumber) {
            requestData.customerData.secondaryPhotoIdNumber = `${secondaryPhotoIdNumber}-${secondaryPhotoIdReferenceNumber}`;
        }
        const secondaryPhotoIdMappedCountry = getMappedCountry(secondaryPhotoIdType);
        requestData.customerData.secondaryPhotoIdType = driversLicenseTypes.includes(secondaryPhotoIdType) ? "DRV" : (secondaryPhotoIdCardType || secondaryPhotoIdType);
        requestData.customerData.secondaryPhotoIdCountry = secondaryPhotoIdMappedCountry || secondaryPhotoIdCountry || country;
        secondaryPhotoIdCountry = requestData.customerData.secondaryPhotoIdCountry;

        const secondaryCountryValidation = countryValidations.find((item) => item.country === secondaryPhotoIdCountry);
        const defaultSecondaryPhotoIdState = secondaryCountryValidation ? (secondaryCountryValidation.senderPhotoIdState || secondaryPhotoIdCountry) : secondaryPhotoIdCountry;

        const secondaryCountryStates = secondaryPhotoIdCountry === "AUS" ? codeTable.ausStates : codeTable.states.filter((item) => item.countryCode === secondaryPhotoIdCountry);
        requestData.customerData.secondaryPhotoIdState = secondaryCountryStates.length === 0 ? defaultSecondaryPhotoIdState : (secondaryPhotoIdState || defaultSecondaryPhotoIdState);

        if (Helpers.isDateValid(secondaryPhotoIdExpiryYear, secondaryPhotoIdExpiryMonth, secondaryPhotoIdExpiryDay)) {
            requestData.customerData.secondaryPhotoIdExpiryDate = `${secondaryPhotoIdExpiryYear}-${parseInt(secondaryPhotoIdExpiryMonth, 10) + 1}-${secondaryPhotoIdExpiryDay}`;
        }

        if (Helpers.isDateValid(secondaryPhotoIdIssueYear, secondaryPhotoIdIssueMonth, secondaryPhotoIdIssueDay)) {
            requestData.customerData.secondaryPhotoIdIssueDate = `${secondaryPhotoIdIssueYear}-${parseInt(secondaryPhotoIdIssueMonth, 10) + 1}-${secondaryPhotoIdIssueDay}`;
        }
        if (requestData.customerData.secondaryPhotoIdType === "SSN") {
            requestData.customerData.secondaryPhotoIdNumber = String(requestData.customerData.secondaryPhotoIdNumber).replace(/-/g, "");
        }
    } else {
        requestData = clearSecondaryPhotoIdFields(requestData);
    }
    return clearUnnecessaryFields(requestData);
};

const printRegistration = (customer, rrn, defaultValues) => {
    const { displayDateFormat } = defaultValues;
    let urlParam = "?print-registration=true";
    const date = new Date();
    const fullDate = `${Helpers.formatDate(date, displayDateFormat)} ${Helpers.getTime12HourFormat(date)}`;

    let fullName = customer.firstName;
    if (customer.middleInitial) {
        fullName = `${fullName} ${customer.middleInitial}`;
    }
    fullName = `${fullName} ${customer.lastName}`;

    urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("productName", "Money Order")}`;
    urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("fullName", fullName)}`;
    urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("rrn", rrn)}`;
    urlParam = `${urlParam}&${HttpUtils.urlEncodeParam("registrationDate", fullDate)}`;
    window.open(window.location.href + urlParam, "_self");
};

const handleCustomerError = (dispatch, getState, error) => {
    const state = getState();
    const { allowedContinueCustomerErrorCodes } = state.config;

    const mgExceptionErrorCode = error.moneygramException ? error.moneygramException.errorCode : null;
    const usedErrorCode = error.mappedKeyCode || mgExceptionErrorCode;

    if (usedErrorCode && allowedContinueCustomerErrorCodes && allowedContinueCustomerErrorCodes.includes(usedErrorCode.toString())) {
        const hideIntervention = () => dispatch({ type: HIDE_INTERVENTION });
        const children = (<Div>
            <P className="title">intervention.default.title</P>
            <P className="description">{error.processedMessage}</P>
            <Button className="small" onClick={hideIntervention} data-analytics="Continue">continue.btn</Button>
        </Div>);
        dispatch({ type: SHOW_INTERVENTION, children });
    } else {
        dispatch({
            type: "SHOW_CHECK_CASHING_ERROR",
            title: "cannot.proceed",
            description: {
                text: "cannot.proceed.desc",
                args: state.config.defaultValues.helpHotline,
            },
        });
    }
};

export const registerCustomer = (dispatch, getState) => {
    const state = getState();
    const { defaultValues } = state.config;

    if (!state.terms.onAgreement) {
        dispatch({ type: VALIDATION_FAILED, error: { fieldName: "terms", message: "money.order.agree.to.terms" } });
        return;
    }

    const requestData = getRequestData(state);
    const sourceOfRegistration = state.config.defaultValues.sourceOfRegistration || "ALON CHECK CASHING KIOSK";
    const sourceOfHalfRegistration = state.config.defaultValues.sourceOfHalfRegistration || "CHECK CASHING - HALF";
    requestData.customerData.attribute7 = state.customer.halfRegister ? sourceOfHalfRegistration : sourceOfRegistration;


    const registerCustomerRequest = { registerCustomerRequest: requestData };

    dispatch({ type: REGISTER_CUSTOMER_REQUEST, registerCustomerRequest });

    restSendHandler(getState, RestHost.POST, "customers/registerCustomer", registerCustomerRequest, null,
        (response) => {
            dispatch({ type: REGISTER_CUSTOMER_SUCCESS, response });
            const { customerData, customerCredentialsData } = requestData;
            const type = response.registerCustomerResponse.type;
            const username = type === "PHONE" ? customerData.phone : customerData.email;

            const successCallback = (customerId, loginResponse, userName, password) => {
                dispatch({
                    type: LOGIN_SUCCESS,
                    processOnBackground: false,
                    response: loginResponse,
                    customerId,
                    userName,
                    password,
                });

                dispatch({ type: CUSTOMER_NEXT_PAGE });
                dispatch({ type: SAVE_LOGIN_CUSTOMER_ID, customerId, customerData });
                printRegistration(customerData, username, defaultValues);
            };

            dispatch(loginRequest(username, customerCredentialsData.password, successCallback, false));
        },
        (error) => {
            dispatch({ type: REGISTER_CUSTOMER_FAILURE, error });
            handleCustomerError(dispatch, getState, error);
            Logger.error(`error preparing anonymous money order voucher.. ${JSON.stringify(error)}`);
        },
        RestHost.JSON,
        false,
        true,
    );
};

export const updateCustomerCredentials = (dispatch, getState, customerCredentialsData, updatedRRN, successCallback) => {
    const customerQA = customerCredentialsData.customerQA;
    const password = customerCredentialsData.password;

    const previousCustomerCredentialsData = getState().customer.previousCustomerCredentialsData || getState().moneyOrder.customerCredentialsData;
    const previousCustomerQA = previousCustomerCredentialsData.customerQA;
    const previousPassword = previousCustomerCredentialsData.password;

    const registrationRetrievalNumber = updatedRRN || previousCustomerCredentialsData.registrationRetrievalNumber;

    const originalCredentials = {
        registrationRetrievalNumber,
        password: previousPassword,
        customerQA: previousCustomerQA,
    };

    const updatedCredentials = {
        registrationRetrievalNumber,
        password: password || previousPassword,
        customerQA: customerQA || previousCustomerQA,
    };

    const updateCustomerCredentialsRequest = {
        updateCustomerCredentialsRequest: {
            originalCredentialsData: originalCredentials,
            updatedCredentialsData: updatedCredentials,
            agentDetails: Helpers.getAgentDetails(),
        },
    };

    dispatch({ type: UPDATE_CUSTOMER_CREDENTIALS_REQUEST, updateCustomerCredentialsRequest });

    restSendHandler(getState, RestHost.POST, "customers/updateCustomerCredentials", updateCustomerCredentialsRequest, null,
        (response) => {
            dispatch({ type: UPDATE_CUSTOMER_CREDENTIALS_SUCCESS, response });
            successCallback(dispatch, getState, customerCredentialsData);
        },
        (error) => {
            dispatch({ type: UPDATE_CUSTOMER_CREDENTIALS_FAILURE, error });
            handleCustomerError(dispatch, getState, error);
        },
        RestHost.JSON,
        false,
        true,
    );
};

export const forceChangePassword = (dispatch, getState, successCallback) => {
    const { password, passwordConfirmation } = getState().resetPassword.forceChangePasswordRequestData;

    if (password !== passwordConfirmation) {
        dispatch({ type: VALIDATION_FAILED, error: { fieldName: "password", message: "password.does.not.match" } });
    } else {
        const customerCredentialsData = { password };
        updateCustomerCredentials(dispatch, getState, customerCredentialsData, null, successCallback);
    }
};

export const updateCustomer = (dispatch, getState, successCallbackParam) => {
    const state = getState();
    const requestData = getRequestData(state);
    const customerCredentialsData = cloneDeep(requestData.customerCredentialsData);
    delete requestData.customerData.recipients;
    delete requestData.customerCredentialsData;

    const defaultSuccessCallback = () => dispatch({ type: CUSTOMER_NEXT_PAGE });
    const successCallback = successCallbackParam || defaultSuccessCallback;

    const updateCustomerRequest = { updateCustomerRequest: requestData };

    const customerQA = customerCredentialsData.customerQA;
    const { previousCustomerCredentialsData } = state.customer;
    const previousCustomerQA = previousCustomerCredentialsData.customerQA;

    const validateAndUpdateCustomerCredentials = (customerUpdated, updatedRRN) => {
        const passwordChanged = customerCredentialsData.password;
        const questionChanged = previousCustomerQA.questionType !== customerQA.questionType;
        const answerChanged = previousCustomerQA.answer !== customerQA.answer;

        if (passwordChanged || questionChanged || answerChanged) {
            updateCustomerCredentials(dispatch, getState, customerCredentialsData, updatedRRN, successCallback);
        } else {
            successCallback(dispatch, getState);
        }
    };

    const shouldUpdateCustomer = () => true;

    if (shouldUpdateCustomer()) {
        dispatch({ type: UPDATE_CUSTOMER_REQUEST, updateCustomerRequest });

        restSendHandler(getState, RestHost.POST, "customers/updateCustomer", updateCustomerRequest, null,
            (response) => {
                dispatch({ type: UPDATE_CUSTOMER_SUCCESS, response, customerData: requestData.customerData });

                const { phone, email } = requestData.customerData;
                const { type } = response.updateCustomerResponse;
                const updatedRRN = type === "EMAIL" ? email : phone;
                validateAndUpdateCustomerCredentials(true, updatedRRN);
            },
            (error) => {
                dispatch({ type: UPDATE_CUSTOMER_FAILURE, error });
                handleCustomerError(dispatch, getState, error);
            },
            RestHost.JSON,
            false,
            true,
        );
    } else {
        validateAndUpdateCustomerCredentials(false);
    }
};

const postValidation = (dispatch, getState, errors, onSuccessfulValidation) => {
    if (errors.length > 0) {
        dispatch({ type: VALIDATION_FAILED, errors });
    } else {
        onSuccessfulValidation(dispatch, getState);
    }
};

export const validateEmail = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const email = customer.requestData.customerData.email;
    const { errors } = ui;

    const emailChange = customer.previousCustomerData && "email" in customer.previousCustomerData &&
            email !== customer.previousCustomerData.email;

    if (!email) {
        postValidation(dispatch, getState, errors, onSuccessfulValidation);
    } else if (!customer.newUser && !emailChange) {
        dispatch({ type: CHECK_CUSTOMER_CREDENTIALS_STATUS_SUCCESS });
        postValidation(dispatch, getState, errors, onSuccessfulValidation);
    } else if ((customer.newUser || emailChange) && (!errors || errors.length === 0)) {
        dispatch({ type: CHECK_CUSTOMER_CREDENTIALS_STATUS_REQUEST, email });

        checkCustomerCredentialsStatus(getState, email,
          (response) => {
              if (response.checkCustomerCredentialsStatusResponse.status === "AVAILABLE") {
                  dispatch({ type: CHECK_CUSTOMER_CREDENTIALS_STATUS_SUCCESS });
                  postValidation(dispatch, getState, errors, onSuccessfulValidation);
              } else {
                  dispatch({ type: CHECK_CUSTOMER_CREDENTIALS_STATUS_FAILURE,
                      error: { fieldName: "email", message: "email.is.unavailable" } });
              }
          },
          (error) => {
              const message = error || "cannot.process.at.this.time";
              dispatch({ type: CHECK_CUSTOMER_CREDENTIALS_STATUS_FAILURE,
                  error: { fieldName: "email", message } });
          },
      );
    } else {
        postValidation(dispatch, getState, errors, onSuccessfulValidation);
    }
};

export const validateBirthdate = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const { birthDay, birthMonth, birthYear } = customer.requestData.customerData;

    const fieldNameMapping = new Map();
    fieldNameMapping.set("day", "birthDay");
    fieldNameMapping.set("month", "birthMonth");
    fieldNameMapping.set("year", "birthYear");

    let { errors } = ui;

    if (errors.length === 0) {
        if (!Helpers.isDateValid(birthYear, birthMonth, birthDay)) {
            errors = mergeErrors(errors, { fieldName: "birthDay", message: " " });
            errors = mergeErrors(errors, { fieldName: "birthMonth", message: "invalid.birth.date" });
            errors = mergeErrors(errors, { fieldName: "birthYear", message: " " });
        } else if (Helpers.getAge(birthYear, birthMonth, birthDay) < 18) {
            errors = mergeErrors(errors, { fieldName: "birthDay", message: " " });
            errors = mergeErrors(errors, { fieldName: "birthMonth", message: "invalid.age" });
            errors = mergeErrors(errors, { fieldName: "birthYear", message: " " });
        }
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const validateAnswerDate = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const { answerDay, answerMonth, answerYear, questionType } = customer.requestData.customerCredentialsData.customerQA;

    const fieldNameMapping = new Map();
    fieldNameMapping.set("day", "birthDay");
    fieldNameMapping.set("month", "birthMonth");
    fieldNameMapping.set("year", "birthYear");

    let { errors } = ui;

    if (errors.length === 0 && questionType === "DOB" && !Helpers.isDateValid(answerYear, answerMonth, answerDay)) {
        errors = mergeErrors(errors, { fieldName: "answerDay", message: " " });
        errors = mergeErrors(errors, { fieldName: "answerMonth", message: "invalid.birth.date" });
        errors = mergeErrors(errors, { fieldName: "answerYear", message: " " });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};


export const validatePassword = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const { password, passwordConfirmation } = customer.requestData.customerCredentialsData;
    let { errors } = ui;

    if (password !== passwordConfirmation) {
        errors = mergeErrors(errors, { fieldName: "password", message: "password.does.not.match" });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const isPhotoIdNumberValid = (value) => {
    const photoIdNumber = String(value).replace(/\s/g, "");
    const specialChar = "-";
    let previousNumber = null;

    let isSequenceAsc = true;
    let isSequenceDesc = true;
    let isAllCharSame = true;

    let hasLetter = false;
    let hasNumber = false;
    let hasSpecial = false;

    for (let i = 0; i < photoIdNumber.length; i++) {
        const char = photoIdNumber[i];

        if (Helpers.isLetter(char)) {
            hasLetter = true;
        }

        if (Helpers.isNumber(char)) {
            hasNumber = true;

            if (previousNumber) {
                if (char !== previousNumber) {
                    isAllCharSame = false;
                }
                const diff = parseInt(char, 10) - parseInt(previousNumber, 10);
                if (diff !== 1) {
                    isSequenceAsc = false;
                }
                if (diff !== -1) {
                    isSequenceDesc = false;
                }
            }
            previousNumber = char;
        }

        if (specialChar && char === specialChar) {
            hasSpecial = true;
        }
    }
    const specialCharOnly = !hasLetter && !hasNumber && hasSpecial;
    const invalidPattern = isSequenceAsc || isSequenceDesc || isAllCharSame;
    return !specialCharOnly && !invalidPattern;
};

const doesPhotoIdContainProhibitedWords = (prohibitedWords, photoIdNumber) => {
    const photoIdNumberStr = `${photoIdNumber}`.toUpperCase();
    const matchCondition = (word) => photoIdNumberStr.indexOf(`${word}`.toUpperCase()) >= 0;
    return prohibitedWords.find(matchCondition);
};

export const validatePhotoId = (dispatch, getState, onSuccessfulValidation) => {
    let { errors } = getState().ui;

    const { customer, config } = getState();
    const { prohibitedWords } = config.photoIdOptions;
    /*
    const { photoIdNumber, photoIdExpiryDay, photoIdExpiryMonth, photoIdExpiryYear } = customer.requestData.customerData;
    */
    const { photoIdNumber, photoIdType, photoIdExpiryDay, photoIdExpiryMonth, photoIdExpiryYear } = customer.requestData.customerData;

    const photoId = config.photoIdOptions.photoIdTypes.find((idType) => idType.value === photoIdType);

    if (photoId.invalidForCheckCashing) {
        errors = mergeErrors(errors, { fieldName: "photoIdType", message: "invalid.photo.id.type" });
    }

    if (photoIdNumber
            && (!isPhotoIdNumberValid(photoIdNumber) || doesPhotoIdContainProhibitedWords(prohibitedWords, photoIdNumber))) {
        errors = mergeErrors(errors, { fieldName: "photoIdNumber", message: "invalid.photo.id.number" });
    }

    const photoIdExpiryFields = ["photoIdExpiryDay", "photoIdExpiryMonth", "photoIdExpiryYear"];
    const incompleteExpiryDate = errors.find((error) => photoIdExpiryFields.includes(error.fieldName));
    if (!incompleteExpiryDate &&
          (!Helpers.isDateValid(photoIdExpiryYear, photoIdExpiryMonth, photoIdExpiryDay)
              || Helpers.isPastDate(photoIdExpiryYear, photoIdExpiryMonth, photoIdExpiryDay))) {
        errors = mergeErrors(errors, { fieldName: "photoIdExpiryDay", message: " " });
        errors = mergeErrors(errors, { fieldName: "photoIdExpiryMonth", message: "invalid.expiry.date" });
        errors = mergeErrors(errors, { fieldName: "photoIdExpiryYear", message: " " });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const validateSecondaryPhotoId = (dispatch, getState, onSuccessfulValidation) => {
    let { errors } = getState().ui;

    const { customer } = getState();
    const { secondaryPhotoIdNumber } = customer.requestData.customerData;

    const ssnRegEx = /^[0-9]{3}-[0-9]{2}-[0-9]{4}$/;
    if (secondaryPhotoIdNumber &&
            (!isPhotoIdNumberValid(secondaryPhotoIdNumber) || !ssnRegEx.test(secondaryPhotoIdNumber))) {
        errors = mergeErrors(errors, { fieldName: "secondaryPhotoIdNumber", message: "invalid.secondary.photo.id.number" });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const validateAddress = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, config, ui } = getState();
    const { address1, country, postcode } = customer.requestData.customerData;

    let { errors } = ui;
    const residenceError = validateResidence(address1);
    const postCodeError = validatePostcode(country, postcode, config.countryValidations);

    if (residenceError) {
        errors = mergeErrors(errors, residenceError);
    }
    if (postCodeError) {
        errors = mergeErrors(errors, postCodeError);
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const validateOccupution = (dispatch, getState, onSuccessfulValidation) => {
    let { errors } = getState().ui;

    const { customer, config } = getState();
    const { prohibitedWords } = config.occupationOptions;
    const { occupation } = customer.requestData.customerData;

    if (doesPhotoIdContainProhibitedWords(prohibitedWords, occupation)) {
        errors = mergeErrors(errors, { fieldName: "occupation", message: "invalid.occupation" });
    }

    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

const getCurrentPage = (getState) => {
    const { pages, currentIndex } = getState().customer;
    return pages[currentIndex];
};

export const onContinue = (dispatch, getState) => {
    dispatch({ type: VALIDATE });
    const { errors } = getState().ui;

    const currentPage = getCurrentPage(getState);
    const atReviewPage = currentPage.title === "review";

    const additionalValidation = currentPage.validate;
    const onCustomOnContinue = currentPage.onContinue;

    const onSuccessfulValidation = () => {
        const customer = getState().customer;
        const { editing } = customer;

        const reviewPageNavigateAction = { type: EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE, persist: true };
        const nextPageAction = { type: CUSTOMER_NEXT_PAGE };
        const action = ((editing && !atReviewPage) && currentPage.title !== "country") ? reviewPageNavigateAction : nextPageAction;
        dispatch(action);
    };

    if (additionalValidation) {
        if (Helpers.isArray(additionalValidation)) {
            const firstValidation = additionalValidation[0];
            const secondValidation = additionalValidation[1];
            firstValidation(dispatch, getState, () => secondValidation(dispatch, getState, onCustomOnContinue || onSuccessfulValidation));
        } else {
            additionalValidation(dispatch, getState, onCustomOnContinue || onSuccessfulValidation);
        }
    } else if (onCustomOnContinue) {
        onCustomOnContinue(dispatch, getState, onSuccessfulValidation);
    } else if (errors.length === 0) {
        onSuccessfulValidation(dispatch, getState);
    }
};

export const onCustomPhotoIdContinue = (dispatch, getState) => {
    const state = getState();
    const { isForceUpdatePhotoIdDetails, destinationPage } = state.checkCashing;

    if (isForceUpdatePhotoIdDetails) {
        updateCustomer(dispatch, getState, () => {
            const { customer } = getState();
            const checkCashingAction = { type: INITIATE_CHECK_CASHING, customerData: customer.previousCustomerData, proceedToTransaction: true };
            const reviewPageNavigateAction = { type: EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE, persist: true };

            if (destinationPage === "/checkCashing") {
                dispatch(checkCashingAction);
                dispatch(push(destinationPage));
            } else {
                dispatch(reviewPageNavigateAction);
            }
        });
    } else {
        const customer = state.customer;
        const { editing } = customer;

        const currentPage = getCurrentPage(getState);
        const atReviewPage = currentPage.title === "review";

        const nextPageAction = { type: CUSTOMER_NEXT_PAGE };
        const reviewPageNavigateAction = { type: EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE, persist: true };
        const action = ((editing && !atReviewPage) && currentPage.title !== "country") ? reviewPageNavigateAction : nextPageAction;
        dispatch(action);
    }
};

export const onPrevious = (disableEditing) => (dispatch, getState) => {
    const { editing, requestData } = getState().customer;
    const currentPage = getCurrentPage(getState);
    const onCustomOnPrevious = currentPage.onPrevious;
    const atReviewPage = currentPage.title === "review";

    if (onCustomOnPrevious) {
        onCustomOnPrevious(dispatch, getState);
    } else if (editing && !atReviewPage) {
        dispatch({ type: EDIT_CUSTOMER_DETAILS_REVIEW_PAGE_NAVIGATE });
    } else {
        dispatch({ type: CUSTOMER_PREVIOUS_PAGE, disableEditing });
        const atSecondaryPhotoIDPage = getCurrentPage(getState).title === "secondaryPhotoId";
        if (atSecondaryPhotoIDPage && !requestData.customerData.secondaryPhotoIdNumber) {
            dispatch({ type: CUSTOMER_PREVIOUS_PAGE });
        }
    }
};

export const updateCustomerAdditionalId = (state, successCallback, errorCallback) => {
    const { countryValidations, photoIdOptions } = state.config;

    const getMappedCountry = (type) => {
        const countryTypeMapping = photoIdOptions.countryTypeMappings.find((item) => item.type === type);
        return countryTypeMapping ? countryTypeMapping.country : null;
    };

    const requestData = getRequestData(state);
    const customerData = requestData.customerData;

    customerData.secondaryPhotoIdType = "SSN";
    customerData.secondaryPhotoIdNumber = String(state.checkCashing.updateCustomerData.additionalIdNumber).replace(/-/g, "");

    customerData.secondaryPhotoIdCountry = getMappedCountry(customerData.secondaryPhotoIdType);
    const countryValidation = countryValidations.find((item) => item.country === customerData.secondaryPhotoIdCountry);
    customerData.secondaryPhotoIdState = countryValidation && countryValidation.senderPhotoIdState ? countryValidation.senderPhotoIdState : customerData.secondaryPhotoIdCountry;

    // customerData.secondaryPhotoIdExpiryDate = customerData.photoIdExpiryDate;
    customerData.customerId = state.checkCashing.customerId;

    delete requestData.customerCredentialsData;
    delete requestData.customerData.recipients;
    const updateCustomerRequest = {
        updateCustomerRequest: requestData,
    };
    restSendHandler(null, RestHost.POST, "customers/updateCustomer", updateCustomerRequest, null,
        () => {
            successCallback(customerData.customerId, customerData);
        }, errorCallback,
        RestHost.JSON,
    );
};

export const validateHalfRegisterCustomer = (dispatch, getState, onSuccessfulValidation) => {
    const state = getState();
    const requestData = getRequestData(state);

    if (Helpers.isHalfRegistered(requestData.customerData) && Helpers.incompleteRegistration(requestData.customerData)) {
        const ok = () => dispatch({ type: HIDE_INTERVENTION });
        const children = (<Div>
            <P className="title">half.register.intervention.title</P>
            <P className="description">half.register.intervention.incomplete.update</P>
            <Button className="small" onClick={ok} data-analytics="Ok">ok.btn</Button>
        </Div>);
        dispatch({ type: SHOW_INTERVENTION, children });
    } else {
        if (Helpers.isHalfRegistered(requestData.customerData)) {
            let attribute7 = requestData.customerData.attribute7;
            attribute7 = attribute7.replace(" - HALF", "");
            dispatch({ type: FILL_UP_REQUEST_DATA, customerData: { attribute7 } });
        }
        onSuccessfulValidation(dispatch, getState);
    }
};

export const validateName = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const { firstName, middleInitial, lastName, secondLastName } = customer.requestData.customerData;
    let { errors } = ui;

    if (firstName && firstName !== "" && doesNameStartsWithSpecialCharacter(firstName)) {
        errors = mergeErrors(errors, { fieldName: "firstName", message: "name.should.not.starts.with.character" });
    }
    if (middleInitial && middleInitial !== "" && doesNameStartsWithSpecialCharacter(middleInitial)) {
        errors = mergeErrors(errors, { fieldName: "middleInitial", message: "name.should.not.starts.with.character" });
    }
    if (lastName && lastName !== "" && doesNameStartsWithSpecialCharacter(lastName)) {
        errors = mergeErrors(errors, { fieldName: "lastName", message: "name.should.not.starts.with.character" });
    }
    if (secondLastName && secondLastName !== "" && doesNameStartsWithSpecialCharacter(secondLastName)) {
        errors = mergeErrors(errors, { fieldName: "secondLastName", message: "name.should.not.starts.with.character" });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};

export const validatePhoneNumber = (dispatch, getState, onSuccessfulValidation) => {
    const { customer, ui } = getState();
    const { phone } = customer.requestData.customerData;
    const pattern = getPattern(phone);
    let { errors } = ui;
    if (pattern.counting) {
        errors = mergeErrors(errors, { fieldName: "phone", message: "invalid.mobile.number" });
    }
    postValidation(dispatch, getState, errors, onSuccessfulValidation);
};



