import React from "react";
import { connect } from "react-redux";
import { Dropdown } from "common-components";

import { onTownRequestCheck } from "../actions/codeTable";

class TownDropdown extends React.Component {

    componentDidMount() {
        if (this.props.enableTownAutoComplete) {
            this.props.onLoad();
        }
    }

    render() {
        const items = this.props.items || this.state.items;
        const props = { ...this.props, items, allowNotOnList: true };
        delete props.country;
        delete props.enableTownAutoComplete;
        return <Dropdown {...props} />;
    }
}

const mapStateToProps = (state) => {
    const { enableTownAutoComplete } = state.config;
    return { enableTownAutoComplete };
};

const mapDispatchToProps = (dispatch, ownProps) => {
    return { onLoad: () => dispatch(onTownRequestCheck(ownProps.country)) };
};

export default connect(mapStateToProps, mapDispatchToProps)(TownDropdown);



