import React from "react";
import { connect } from "react-redux";
import { Intl } from "common-functions";
import { Div, Label, P } from "./HTML";
import Panel from "./Panel";

class SelectV2 extends React.Component {

    constructor(props) {
        super(props);
        this.state = { showItems: false };
    }

    componentDidMount() {
        const { value, name, items, onChange } = this.props;
        if (!value && onChange && items && items.length > 0) {
            onChange({ target: { name, value: items[0].value } });
        }
    }

    onMouseDown(event, item) {
        this.setState({ preSelected: item, scrolled: false, yPosition: event.clientY });
    }

    onMouseUp() {
        const { scrolled, preSelected } = this.state;

        if (!scrolled) {
            const { name, onChange } = this.props;
            onChange({ target: { name, value: preSelected.value } });

            this.setState({ selected: preSelected, showItems: false, yPosition: undefined });
        }
    }

    onMouseMove(event) {
        const scrollAllowance = 10;
        const { yPosition } = this.state;

        if (yPosition !== undefined) {
            const movedUp = yPosition - scrollAllowance > event.clientY;
            const movedDown = yPosition + scrollAllowance < event.clientY;
            if (movedUp || movedDown) {
                this.setState({ scrolled: true });
            }
        }
    }

    hideItems() {
        this.setState({ showItems: false });
    }

    toggle() {
        this.setState({ showItems: !this.state.showItems });
        this.update();
    }

    update() {
        this.panel.update();
    }

    render() {
        const { id, name, items, label, translation, className, value, hideDisabled } = this.props;
        const { showItems } = this.state;

        const selected = items.find((item) => item.value === value);

        const properties = Object.assign({}, this.props);
        properties.className = showItems ? "select-v2 selected" : "select-v2";

        delete properties.items;
        delete properties.translation;
        delete properties.dispatch;
        delete properties.labelComponent;
        delete properties.hideDisabled;
        delete properties.errorMessage;

        const translatedItems = items.map((item) => {
            return { ...item, name: Intl.translate(translation, item.name) };
        });

        const itemsComponent = translatedItems.map((item) => {
            if (item.disabled && !hideDisabled) {
                return (<Div key={item.value}
                  id={item.value}
                  className={`item option-disabled ${hideDisabled ? "option-hidden" : ""}`}>
                    {item.name}
                </Div>);
            }
                return (<Div key={item.value}
                  id={item.value}
                  onMouseDown={(event) => this.onMouseDown(event, item)}
                  onMouseMove={(event) => { this.onMouseMove(event, item); this.onMouseDown(event, item); }}
                  onMouseUp={(event) => this.onMouseUp(event, item)}
                  className="item with-sound">
                    {item.name}
                </Div>);

        });

        const selectedItem = selected || translatedItems[0];
        const labelComponent = label ? <Label htmlFor={id || name}>{label}</Label> : this.props.labelComponent;
        const panelClassName = className ? `${className} select-v2-panel` : "select-v2-panel";

        const inputProps = {
            onClick: () => this.toggle(),
            className: className ? `${className} input with-sound` : "input with-sound",
        };
        inputProps["data-valid"] = !selectedItem.disabled;
        inputProps["data-value"] = selectedItem.name;
        inputProps["data-select"] = true;

        const parentClassName = `${this.props.errorMessage ? "has-error" : ""} pull-left select-wrapper ${this.props.parentClassName}`;
        const additionalErrorClasName = this.props.errorClassName ? ` ${this.props.errorClassName}` : "";
        return (<Div className={parentClassName} onBlur={() => this.hideItems()} tabIndex={0}>
            {labelComponent}
            <Div {...properties}>
                <Div {...inputProps}>
                    <P>{selectedItem.name}</P>
                    <img src="resources/images/arrow-select.png" alt="Arrow" />
                </Div>

                <Panel ref={(panel) => (this.panel = panel)}
                  style={{ display: showItems ? "block" : "none" }}
                  className={panelClassName}
                  noPanelStyling
                  updateOnMount>
                    <Div className="items">{itemsComponent}</Div>
                </Panel>

                {!this.props.hideErrorMessage ? <P className={`message error${additionalErrorClasName}`} defaultValue={this.props.defaultErrorMessage}>
                    {this.props.errorMessage}
                </P> : null}
            </Div>
        </Div>);
    }

}

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.id || ownProps.name;
    let error = null;
    if (state.ui && !ownProps.hideErrorMessage) {
        error = state.ui.errors.find((item) => item.fieldName === id);
    }

    return {
        errorMessage: error ? error.message : null,
        defaultErrorMessage: error ? error.defaultMessage: null,
        translation: state.internationalization.translation,
    };
};

export default connect(mapStateToProps)(SelectV2);



