import React from "react";
import { connect } from "react-redux";
import { Panel } from "common-components";
import { Helpers } from "common-functions";

import { PREV_PAGE, FILL_UP_AMOUNT, FIELD_FOCUS, ENABLE_EDIT_MODE, TERMS_AGREEMENT,
  SHOW_MO_TERMS_AND_CONDITIONS, HIDE_KEYBOARD, SUBMIT_CHECK_CASHING } from "../../actions/constants";

import { getMaxAmount, getPaymentFee, isCardDisabled } from "../../reducers/checkCashing";
import { Div, P, Button, Label, Input, Span } from "../../components/HTML";
import Card from "../../components/Card";
import Checkbox from "../../components/Checkbox";

import Navigation from "../../components/Navigation";
import { relative } from "path";

class Review extends React.Component {
    constructor(){
        super();
        this.state = {
            isLowerThanFee: false
        }
    }
    componentDidUpdate() {
        this.panel.update();
    }

    render() {
        const { voucher, onChange, showAmountError, reviewEditMode, enableEditMode, disableEditMode,
              onClick, onFocus, onTermsAndConditionToggle, termsAgreement, showTermsError,
              showTermsAndConditions, showTerms, onPrevious, hideKeyboard, language } = this.props;
        const { isLowerThanFee } = this.state;
        const MAX_AMOUNT = Helpers.formatMoney(getMaxAmount());
        const FEE_AMOUNT = Helpers.formatMoney(getPaymentFee());
        const NEXT = language === "en" ? "Save" : "guardar";
        const amountErrorClass = showAmountError !== "check.cashing.amount.error.message" ? "amount-error-review2" : "amount-error-review";
        const lowerThanFeeError = showAmountError === "check.cashing.amount.error.message";
        const styleAmountError = { display: showAmountError && showAmountError !== "check.cashing.amount.error.message" && reviewEditMode ? "" : "none" };
        const styleLowerThanFee = { display: lowerThanFeeError && reviewEditMode ? "" : "none" }

        return (<Div>
            <Div className="review-page customer-review-page">

                <P className="mo-review-subtitle">please.review.your.check.cashing</P>
                <Div className="items items-mo-review">
                    <Div className="pull-left" style={{position: "relative"}}>
                        <Label>check.cashing.payment.amount</Label>
                        <Input name="checkAmount" value={reviewEditMode ? voucher.checkAmount : Helpers.formatMoney(voucher.checkAmount)}
                          className={reviewEditMode ? "input-review-edit selected input-review-edit-small" : "input-review-edit input-review-edit-small"}
                          placeholder="money.order.amount.placeholder" shouldAutoFocus prefix="$"
                          onChange={onChange} readOnly={!reviewEditMode} data-type="NUMERIC"
                          data-allowed-special-characters="."
                          onFocus={reviewEditMode ? onFocus : () => {}} maxLength="10" id="checkAmountInput" />

                        <Button onClick={() => {
                            if(lowerThanFeeError){
                                enableEditMode();
                                this.setState({isLowerThanFee: true})
                            } else {
                                 disableEditMode();
                                 this.setState({isLowerThanFee: false})
                            }
                            if(isLowerThanFee && lowerThanFeeError){
                                disableEditMode();
                                this.setState({isLowerThanFee: false})
                            }
                        }} style={{ display: reviewEditMode ? "" : "none" }} type="submit" className="review-edit-btn" data-analytics="Save">save</Button>

                    <Div className={amountErrorClass} args={[MAX_AMOUNT, FEE_AMOUNT, NEXT]} style={isLowerThanFee && lowerThanFeeError ? styleLowerThanFee : styleAmountError }>{showAmountError}</Div>
                 
                    </Div>
                    <Div className="clearfix" />
                    <Div className="pull-left">
                        <Label>check.cashing.payment.fee</Label>
                        <Input name="paymentFee" value={Helpers.formatMoney(voucher.fee)} className="input-review input-review-edit-small" label="&nbsp;"
                          placeholder="money.order.payment.fee.placeholder" prefix="$" readOnly prefixClassName="review" />

                    </Div>
                    <Div className="pull-left">
                        <Label>check.cashing.receive.amount</Label>
                        <Input name="receiveAmount" value={Helpers.formatMoney(voucher.receiveAmount)} className="input-review input-review-edit-small" label="&nbsp;"
                          placeholder="money.order.cash.amount.placeholder" prefix="$" readOnly prefixClassName="review" />
                    </Div>
                    <Div className="pull-left" style={{ display: isCardDisabled() ? "none" : "" }}>
                        <Label>check.cashing.card.amount</Label>
                        <Input name="cardAmount" value={Helpers.formatMoney(voucher.cardAmount)} className="input-review input-review-edit-small" label="&nbsp;"
                          placeholder="money.order.card.amount.placeholder" prefix="$" readOnly prefixClassName="review" />
                    </Div>
                </Div>
                <Div className="terms-and-condition-mo" data-analytics="Terms agreement">
                    <Checkbox label={<Span><Span>terms.agreement.1</Span> <Span onClick={() => showTermsAndConditions(true)} className="bold-link">terms.agreement.2</Span></Span>}
                      checked={termsAgreement}
                      onChange={onTermsAndConditionToggle}
                      name="termsAgreement" />
                    <Div className="amount-error-review2" style={{ display: showTermsError ? "" : "none" }}>money.order.agree.to.terms</Div>
                </Div>

                <Div className="actions fixed-bottom" style={{ display: reviewEditMode ? "none" : "" }}>
                    <Button onClick={enableEditMode} className="small secondary" data-analytics="Edit details">money.order.edit.details</Button>
                    <Button onClick={onClick} className="small" data-analytics="Submit">money.order.submit</Button>
                </Div>

                <Navigation onNext={onClick}/* previousLink="/" */ onPrevious={onPrevious} hideNext={reviewEditMode} hidePrevious={reviewEditMode}
                  nextOnEnter={reviewEditMode} hasFormatErrors={!termsAgreement} hasInput={!termsAgreement} />
            </Div>

            <Div className="mo-popup mo-popup-2" style={{ display: showTerms ? "" : "none" }}>
                <Div className="terms-page mo-terms-page">
                    <P className="title">terms.title</P>

                    <Panel ref={(panel) => (this.panel = panel)} className="mo-terms-page-description" updateOnMount>
                        <P className="description">
                            <Span>check.cashing.terms.and.conditions.line.01</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.02</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.03</Span><br />
                            <Span args={
                                <Span style={{ textDecoration: "underline", color: "#0000EE" }}>check.cashing.terms.and.conditions.line.url</Span>
                              }>check.cashing.terms.and.conditions.line.04</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.05</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.06</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.07</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.08</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.09</Span><br />
                            <Span>check.cashing.terms.and.conditions.line.10</Span><br />
                            <ul>
                                <li><Span>check.cashing.terms.and.conditions.line.11</Span></li>
                                <li><Span args={
                                  <Span style={{ "text-decoration": "underline", color: "#0000EE" }}>check.cashing.terms.and.conditions.payment.url</Span>
                                  }>check.cashing.terms.and.conditions.line.12</Span></li>
                                <li><Span>check.cashing.terms.and.conditions.line.13</Span></li>
                                <li><Span>check.cashing.terms.and.conditions.line.14</Span></li>
                                <li><Span>check.cashing.terms.and.conditions.line.15</Span></li>
                                <li><Span>check.cashing.terms.and.conditions.line.16</Span></li>
                                <li><Span>check.cashing.terms.and.conditions.line.17</Span></li>
                            </ul>
                            <Span>check.cashing.terms.and.conditions.line.18</Span><br /><br />
                        </P>
                    </Panel>

                    <Div className="actions fixed-bottom">
                        <Button onClick={() => showTermsAndConditions(false)} className="small" data-analytics="Got it">got.it.btn</Button>
                    </Div>
                </Div>
            </Div>

            <img alt="dummy" src="resources/images/check.png" style={{ display: "none" }} onLoad={hideKeyboard} />
        </Div>);
    }

}

const onChange = (event, dispatch) => {
    dispatch({ type: FILL_UP_AMOUNT, target: event.target });
};

const onKeyclick = (element, dispatch) => {
    element.removeEventListener("onkeyclick", (event) => { onChange(event, dispatch); });
    element.addEventListener("onkeyclick", (event) => { onChange(event, dispatch); }, false);
};

const onLoad = (dispatch) => {
    const checkAmountInput = document.getElementById("checkAmountInput");
    onKeyclick(checkAmountInput, dispatch);
    dispatch({ type: FIELD_FOCUS, selectedField: checkAmountInput });
};

const mapStateToProps = (state) => {
    return {
        selectedLanguage: state.internationalization.selectedLanguage,
        languages: state.internationalization.languages,
        voucher: state.checkCashing.voucher,
        showAmountError: state.checkCashing.showAmountError,
        showTermsError: state.checkCashing.showTermsError,
        reviewEditMode: state.checkCashing.reviewEditMode,
        termsAgreement: state.checkCashing.termsAgreement,
        showTerms: state.checkCashing.showTerms,
        language: state.internationalization.language,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onChange: (event) => {
            dispatch({ type: FILL_UP_AMOUNT, target: event.target });
        },
        onClick: (event) => {
            dispatch({ type: SUBMIT_CHECK_CASHING, event, dispatch });
        },
        enableEditMode: (event) => {
            const result = { success: null };
            dispatch({ type: ENABLE_EDIT_MODE, reviewEditMode: true, event, result });
            onLoad(dispatch);
        },
        disableEditMode: (event) => {
            const result = { success: null };
            dispatch({ type: ENABLE_EDIT_MODE, reviewEditMode: false, event, result });
            if (result.success) {
                dispatch({ type: HIDE_KEYBOARD });
            }
        },
        onFocus: (event) => {
            onKeyclick(event.target, dispatch);
            dispatch({ type: FIELD_FOCUS, selectedField: event.target });
        },
        onTermsAndConditionToggle: (event) => {
            dispatch({ type: TERMS_AGREEMENT, termsAgreement: event.target.checked });
        },
        onPrevious: () => {
            dispatch({ type: PREV_PAGE });
        },
        showTermsAndConditions: (showTermsAndConditions) => {
            dispatch({ type: SHOW_MO_TERMS_AND_CONDITIONS, showTermsAndConditions });
            dispatch({ type: TERMS_AGREEMENT, termsAgreement: true });
        },
        hideKeyboard: () => {
            dispatch({ type: HIDE_KEYBOARD });
        },
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Review);



