import React from "react";
import { connect } from "react-redux";

// import { INITIATE_LOGIN } from "../../reducers/login";
import { INITIATE_HALF_REGISTER_CUSTOMER, INITIATE_RESET_PASSWORD } from "../../actions";
import { Div, P, Button } from "../../components/HTML";

const RestrictedAccess = ({ onClick, display }) => {

    return (<Div style={{ display }} className="restricted-access page-background-3">

        <Div className="restricted-access-page">
            <P className="title">restricted.customer.title</P>
            <P className="subtitle">restricted.customer.subtitle</P>

            <Div className="buttons">
                <Button className="close-btn" to="/" onClick={onClick} data-analytics="Close">close.btn</Button>
            </Div>
        </Div>
    </Div>);
};

const mapStateToProps = (state) => {
    return {
        display: state.checkCashing.restricted ? "" : "none",
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onClick: () => {
          //  dispatch({ type: INITIATE_LOGIN });
            dispatch({ type: INITIATE_HALF_REGISTER_CUSTOMER });
            dispatch({ type: INITIATE_RESET_PASSWORD });
        },
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(RestrictedAccess);



