import React from "react";
import { connect } from "react-redux";

const LoadingBox = (props) => {
    const loadingBox = (<div id="loadingBox" className="loading-box money-order-loading-box">
      <div className="loading-box-image">
        <img src="resources/images/spinner.gif" alt="Loading spinner" />
      </div>
    </div>);
    return (props.show ? loadingBox : null);
};

const mapStateToProps = (state) => {
    return { show: state.checkCashing.showLoadingBox };
};

export default connect(mapStateToProps)(LoadingBox);



