import React from "react";

let count = 0;
const getPanelId = () => {
    count += 1;
    return `panel${count}`;
};

class Panel extends React.Component {

    constructor(props) {
        super(props);
        this.state = { panelId: getPanelId() };
    }

    componentDidMount() {
        if (this.props.updateOnMount) {
            this.update();
        }
    }

    recalculate() {
        const scrollable = document.querySelector(`#${this.state.panelId}`);
        SimpleScrollbar.initEl(scrollable);
        dragscroll.reset();
    }

    update() {
        const { postUpdate, remountOnUpdate } = this.props;

        const postSetState = () => {
            this.recalculate();

            if (postUpdate) {
                postUpdate();
            }
        };

        if (remountOnUpdate) {
            const previousContent = document.querySelector(`#${this.state.panelId} .ss-content`);
            const scrollTop = previousContent ? previousContent.scrollTop : 0;

            const newPanelId = getPanelId();
            /* Replace key to trigger remount for simple scrollbar to work again */
            this.setState({ panelId: newPanelId }, () => {
                postSetState();

                /* Re-scroll cause remount resets scroll to top */
                const content = document.querySelector(`#${newPanelId} .ss-content`);
                content.scrollTop = scrollTop;
            });
        } else {
            postSetState();
        }
    }

    render() {
        const { panelId } = this.state;
        const { className, noPanelStyling, children } = this.props;

        const defaultClassName = !noPanelStyling ? "panel" : "";
        const propsCopy = Object.assign({}, this.props, {
            className: className ? `${className} ${defaultClassName}` : defaultClassName,
        });
        delete propsCopy.updateOnMount;
        delete propsCopy.noPanelStyling;
        delete propsCopy.postUpdate;
        delete propsCopy.remountOnUpdate;

        return (<div id={panelId} key={panelId} {...propsCopy}>
            {noPanelStyling ? children : <div className="panel-content">{children}</div>}
        </div>);
    }

}

Panel.defaultProps = { remountOnUpdate: true };

export default Panel;



